import { encodeIdBase64 } from '../utils'
import { captureException } from '../components/Sentry/utils'

const useKlaviyoIdentify = async email => {
  const data = {
    token: process.env.GATSBY_KLAVIYO_COMPANY_ID,
    properties: {
      $email: email,
    },
  }

  const url = `https://a.klaviyo.com/api/identify?data=${encodeIdBase64(
    JSON.stringify(data),
  )}`

  const options = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
    },
  }

  try {
    const response = fetch(url, options)
    return { response, error: null, loading: false }
  } catch (error) {
    captureException(
      new Error(`Error in identifying the user via E-mail. E-mail: ${email}`),
    )
    return { response: null, error, loading: false }
  }
}

export default useKlaviyoIdentify
