import { gql } from '@apollo/client'

const mutation = gql`
  mutation checkoutDiscountCodeApplyV2(
    $discountCode: String!
    $checkoutId: ID!
  ) {
    checkoutDiscountCodeApplyV2(
      discountCode: $discountCode
      checkoutId: $checkoutId
    ) {
      checkout {
        id
      }
      checkoutUserErrors {
        code
        field
        message
      }
    }
  }
`

const checkoutDiscountCodeApply = (client, variables) =>
  client.mutate({ mutation, variables })

export default checkoutDiscountCodeApply
