import React from 'react'

import { desktopVw } from '../styles/utils'

// Styles
import styled from 'styled-components'
import theme from '../styles/theme'

const Wrapper = styled.div`
  background: ${theme.colors.red};
  height: 100vh;
  overflow: hidden;
  margin-top: -${theme.header.mobile.height}px;

  @media (min-width: ${theme.breakpoints.desktopSmall}px) {
    margin-top: -${desktopVw(theme.header.desktop.height)};
  }
`

const Mainbg = styled.h1`
  position: relative;
  color: ${theme.colors.salmon};
  font-family: ${theme.fonts.secondary};
  font-size: ${desktopVw(950)};
  height: inherit;
`

const Word = styled.span`
  position: absolute;

  &:nth-child(1) {
    top: 30px;
    left: 0;
  }

  &:nth-child(2) {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &:nth-child(3) {
    bottom: 20px;
    right: 30px;
  }

  @media (min-width: ${theme.breakpoints.desktopSmall}px) {
    &:nth-child(1) {
      top: -${desktopVw(30)};
      left: -${desktopVw(40)};
    }

    &:nth-child(2) {
      top: ${desktopVw(290)};
      left: ${desktopVw(770)};
    }

    &:nth-child(3) {
      top: ${desktopVw(90)};
      right: ${desktopVw(30)};
    }
  }
`

const TextWrapper = styled.div`
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: ${theme.colors.secondary};
  font-family: ${theme.fonts.heading};
  font-weight: 500;
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  line-height: 30px;

  @media (min-width: ${theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(70)};
    line-height: ${desktopVw(65)};
  }
`

const bgText = ['4', '0', '4']

const PageNotFound = () => {
  return (
    <Wrapper>
      <Mainbg>
        {bgText.map(w => (
          <Word>{w}</Word>
        ))}
      </Mainbg>
      <TextWrapper>
        sorry <br />
        the page is not
        <br /> found
      </TextWrapper>
    </Wrapper>
  )
}

export default PageNotFound
