import React from 'react'
import styled, { css } from 'styled-components'

import { desktopVw } from '../styles/utils'

import SpinningLoader from './SpinningLoader'
import TrackableButton from './TrackableButton'
import TrackableLink from './TrackableLink'
import TrackableAnchor from './TrackableAnchor'

const buttonStyle = css`
  position: relative;
  display: inline-block;
  font-size: 18px;
  box-sizing: border-box;
  border: 1px solid ${props => props.theme.colors.primary};
  color: ${props =>
    props.black ? props.theme.colors.secondary : props.theme.colors.primary};
  background: ${props =>
    props.black ? props.theme.colors.primary : 'transparent'};
  font-family: ${props => props.theme.fonts.primary};
  padding: 22px 40px;
  text-align: center;
  text-decoration: none;
  transition: background 350ms ease-out, border-color 350ms ease-out,
    color 350ms ease-out;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(22)} ${desktopVw(96)};
    font-size: ${desktopVw(18)};

    &:hover {
      background: ${props => props.theme.colors.primary};
      border-color: ${props => props.theme.colors.primary};
      color: ${props => props.theme.colors.secondary};
    }
  }
`

const TextWrapper = styled.span`
  opacity: ${props => (props.opacity ? '0' : '1')};
`

const StyledButton = styled(TrackableButton)`
  ${buttonStyle}
`

const StyledLink = styled(TrackableLink)`
  ${buttonStyle}
`

const Anchor = styled(TrackableAnchor)`
  ${buttonStyle}
`

const Button = React.forwardRef(
  (
    {
      className,
      children,
      disabled,
      onClick,
      to,
      form,
      href,
      loading,
      black = false,
      state,
      target,
      trackParams,
      style,
    },
    ref,
  ) => {
    if (to) {
      return (
        <StyledLink
          to={to}
          className={className}
          style={style}
          black={black ? 1 : 0}
          state={state}
          trackParams={trackParams}
          onClick={e => onClick && onClick(e)}
        >
          {loading && <SpinningLoader />}
          <TextWrapper opacity={loading ? 1 : 0}>{children}</TextWrapper>
        </StyledLink>
      )
    }

    if (onClick) {
      return (
        <StyledButton
          ref={ref}
          type='button'
          className={className}
          style={style}
          disabled={disabled}
          onClick={() => onClick()}
          black={black ? 1 : 0}
          trackParams={trackParams}
        >
          {loading && <SpinningLoader />}
          <TextWrapper opacity={loading ? 1 : 0}>{children}</TextWrapper>
        </StyledButton>
      )
    }

    if (href) {
      return (
        <Anchor
          href={href}
          trackParams={trackParams}
          className={className}
          style={style}
          target={target}
          black={black ? 1 : 0}
        >
          {loading && <SpinningLoader />}
          <TextWrapper opacity={loading ? 1 : 0}>{children}</TextWrapper>
        </Anchor>
      )
    }

    return (
      <StyledButton
        className={className}
        style={style}
        disabled={disabled}
        black={black ? 1 : 0}
        trackParams={trackParams}
        form={form}
      >
        {loading && <SpinningLoader />}
        <TextWrapper opacity={loading ? 1 : 0}>{children}</TextWrapper>
      </StyledButton>
    )
  },
)

export default Button
