import { captureException } from '../components/Sentry/utils'

const useKlaviyoSignup = () => {
  const signup = async params => {
    const klaviyoSignupEndpoint = process.env.GATSBY_LASTBRAND_LIST_ENDPOINT

    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(params),
    }

    try {
      const response = await fetch(klaviyoSignupEndpoint, options)
      return { response, error: null, loading: false }
    } catch (error) {
      captureException(
        new Error(
          `Error in subscribing the user via E-mail. E-mail: ${params.email}`,
        ),
      )
      return { response: null, error, loading: false }
    }
  }

  return { signup }
}

export default useKlaviyoSignup
