import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import inputStyle from './inputStyle'
import { ErrorMessage } from '.'

const StyledTextArea = styled.textarea`
  ${inputStyle};
  min-height: ${({ height }) => (height ? `${height}px` : '166px')};
  &::placeholder {
    font-size: 12px;
    color: ${({ theme }) => theme.colors.silver};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    min-height: ${({ height }) =>
      height ? desktopVw(height) : desktopVw(166)};
    &::placeholder {
      font-size: ${desktopVw(12)};
      text-transform: unset;
    }
  }
`

export const TextArea = ({
  className,
  name,
  defaultValue,
  onChange,
  placeholder,
  required = true,
  register,
  errors,
  value,
  height,
}) => {
  return (
    <>
      <StyledTextArea
        className={className}
        height={height}
        onChange={e => onChange && onChange(e)}
        ref={
          register &&
          register({
            required: {
              value: required,
              message: `Please fill in ${placeholder}`,
            },
          })
        }
        name={name}
        placeholder={placeholder}
        defaultValue={defaultValue}
        value={value}
      />
      {errors && errors[name] && (
        <ErrorMessage>{errors[name].message}</ErrorMessage>
      )}
    </>
  )
}
