import * as types from '../actions/types'
import { loadState, saveState } from '../utils'

export const storeSubdomain = (state = '', { type, storeSubdomain }) =>
  type === types.SELECT_STORE ? storeSubdomain : state

export const checkouts = (state = {}, { type, checkout }) =>
  type === types.REPLACE_CHECKOUT
    ? {
        ...state,
        [checkout.storeSubdomain]: checkout,
      }
    : state

const initialUpsellVariantState = loadState('upsellVariant')
const saveUpsellVariant = saveState('upsellVariant')

export const upsellVariant = (
  state = initialUpsellVariantState || { loading: false, data: null },
  action,
) => {
  switch (action.type) {
    case types.REPLACE_UPSELL_VARIANT: {
      const { loading, data } = action.payload
      const newState = { loading, data }
      saveUpsellVariant(newState)
      return newState
    }

    default:
      return state
  }
}
