import { gql } from '@apollo/client'

const query = gql`
  query ShopifyVariant(
    $handle: String!
    $selectedOptions: [SelectedOptionInput!]!
  ) {
    product: productByHandle(handle: $handle) {
      id
      title
      options {
        name
        values
      }
      variantBySelectedOptions(selectedOptions: $selectedOptions) {
        id
        title
        availableForSale
        quantityAvailable
        sku
        priceV2 {
          amount
        }
        presentmentPrices(first: 250) {
          edges {
            node {
              price {
                amount
                currencyCode
              }
              compareAtPrice {
                amount
                currencyCode
              }
            }
          }
        }
      }
    }
  }
`

/**
 * https://help.shopify.com/en/api/storefront-api/reference/queryroot
 *
 * @param {{handle: string}} variables
 */
const queryVariantBySelectedOptions = (client, variables) =>
  client.query({ query, variables })

export default queryVariantBySelectedOptions
