import { captureException } from '../components/Sentry/utils'

const useNewsLetterSignUp = () => {
  const newsLetterSignUp = async (email, bucket, isOldUser) => {
    const url = `${process.env.GATSBY_DISCOUNTS_API_ENDPOINT}/newsletter/signup?email=${email}&bucket=${bucket}&isOldUser=${isOldUser}`

    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    }

    try {
      const response = await fetch(url, options)
      return { response: await response.json(), error: null, loading: false }
    } catch (error) {
      captureException(new Error(`Error in Fetching discount code: ${email}`))
      return { response: null, error, loading: false }
    }
  }

  return { newsLetterSignUp }
}

export default useNewsLetterSignUp
