import React, { useRef } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import Button from '../Button'

const StyledCopyField = styled.div`
  display: flex;
  flex-direction: row;
  border: 1px solid #979797;
  margin: 0 0 18px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(20)};
  }
`

const InputArea = styled.textarea`
  padding: 22px 20px 10px 13.25px;
  width: 75%;
  border: 0;
  border-right: 1px solid #979797;
  resize: none;
  font-size: 12px;
  line-height: 1;
  font-family: ${props => props.theme.fonts.primary};
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(22)} ${desktopVw(20)} ${desktopVw(10)}
      ${desktopVw(13.25)};
    font-size: ${desktopVw(12)};
  }
`

const CopyValueButton = styled(Button)`
  min-width: 25%;
  font-size: 12px;
  line-height: 1;
  text-transform: uppercase;
  padding: 0;
  border: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    font-size: ${desktopVw(12)};
  }
`

export const CopyField = ({ value }) => {
  const ref = useRef()

  const copyTextAreaValue = () => {
    ref.current.select()
    document.execCommand('copy')
  }

  return (
    <StyledCopyField>
      <InputArea value={value ? value : ''} ref={ref} />
      <CopyValueButton
        onClick={() => copyTextAreaValue()}
        trackParams={{
          trackInternal: true,
          trackGA: true,
          category: 'referral',
          action: 'copy_referral_link',
        }}
      >
        Copy
      </CopyValueButton>
    </StyledCopyField>
  )
}
