import React, { useState, useRef } from 'react'
import styled from 'styled-components'
import { useDispatch } from 'react-redux'
import { setMobileMenuOpen } from '../../actions'

// Styles
import { desktopVw } from '../../styles/utils'

import { getLinkPath, capitalizeWord, isLoggedIn, logout } from '../../utils'

// Components
import PlusButton from './PlusButton'
import TrackableLink from '../TrackableLink'
import TrackableButton from '../TrackableButton'
import MyAccount from '../../assets/my_account.svg'
import { useSignoutUser } from '../../hooks/useSignoutUser'
import TextButton from '../TextButton'

const Submenu = styled.div`
  overflow: hidden;
  padding-left: 11px;
`

const StyledMenuItem = styled.li`
  margin-bottom: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
    padding: 0 ${desktopVw(23)} 0 0;
  }
`

const TopLevelButton = styled(TrackableButton)`
  display: flex;
  outline: none;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;
  font-size: 18px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: inline;
    text-transform: uppercase;
    font-size: ${desktopVw(11)};
    color: ${props => props.theme.colors.secondary};
  }
`

const StyledMyAccountSvg = styled(MyAccount)`
  margin-right: 10px;
  path {
    fill: currentColor !important;
  }
  width: 13px;
  height: 13px;
`

const StyledLink = styled(TrackableLink)`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;
  font-size: 18px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-transform: uppercase;
    font-size: ${desktopVw(11)};
  }
`

const SubmenuColumns = styled.div`
  margin: 22px 0 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    margin: ${desktopVw(35)} 0 0;
  }
`

const SubmenuTitle = styled.div`
  margin-bottom: 10px;
  font-size: 15px;
  line-height: 18px;
  font-family: 'GT America Expanded', sans-serif;
  font-weight: 500;
`

const SubmenuList = styled.ul`
  margin-top: 22px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: 0;
    margin-right: ${desktopVw(80)};
  }
`

const SubmenuItem = styled.li`
  line-height: 28px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    line-height: ${desktopVw(20)};
  }
`

const SubmenuLink = styled(TrackableLink)`
  text-transform: ${props => (props.isFooterMenu ? `uppercase` : `none`)};
  font-size: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const SubmenuButton = styled(TextButton)`
  text-transform: ${props => (props.isFooterMenu ? `uppercase` : `none`)};
  font-size: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`

const SubMenu = ({ submenuColumn, submenuTitle, isFooterMenu }) => {
  const dispatch = useDispatch()

  const { signOutUser } = useSignoutUser(() => {
    dispatch(setMobileMenuOpen(false))
  })

  return (
    <SubmenuList>
      {submenuTitle && <SubmenuTitle>{submenuTitle}</SubmenuTitle>}
      {submenuColumn.map(link => (
        <SubmenuItem key={link.id}>
          {link?.url ? (
            <SubmenuButton href={link?.url}>
              {!isFooterMenu ? capitalizeWord(link.text) : link.text}
            </SubmenuButton>
          ) : (
            <SubmenuLink
              to={getLinkPath(link)}
              onClick={
                (link.text || '').toLowerCase() === 'logout'
                  ? signOutUser
                  : () => dispatch(setMobileMenuOpen(false))
              }
              trackParams={{
                trackGA: true,
                trackInternal: true,
                category: 'navigation',
                action: 'click_menu_link',
                label: link.text,
                value: getLinkPath(link),
              }}
            >
              {!isFooterMenu ? capitalizeWord(link.text) : link.text}
            </SubmenuLink>
          )}
        </SubmenuItem>
      ))}
    </SubmenuList>
  )
}

const MenuItem = ({
  className,
  link,
  submenuColumnOne,
  submenuColumnTwo,
  submenuColumnThree,
  submenuColumnFour,
  submenuColumnFive,
  submenuColumnSix,
  isFooterMenu,
}) => {
  const dispatch = useDispatch()

  const [open, setOpen] = useState(false)
  const [height, setHeight] = useState(null)
  const ref = useRef(null)
  const handleClick = () => {
    if (submenu) {
      setHeight(ref.current.scrollHeight)
      setOpen(!open)
    }
  }

  const submenu = submenuColumnOne || submenuColumnTwo || submenuColumnThree

  return (
    <StyledMenuItem className={className}>
      {!submenu || (!isLoggedIn() && getLinkPath(link) === '/log-in') ? (
        <StyledLink
          to={
            isLoggedIn() && link.entry.slug === 'log-in'
              ? '/account/my-orders-returns'
              : getLinkPath(link)
          }
          className='toplevel-link'
          onClick={() => dispatch(setMobileMenuOpen(false))}
          trackParams={{
            trackGA: true,
            trackInternal: true,
            category: 'navigation',
            action: isFooterMenu ? 'click_footer_link' : 'click_menu_link',
            label: link.text,
            value:
              isLoggedIn() && link.entry.slug === 'log-in'
                ? '/account/my-orders-returns'
                : getLinkPath(link),
            context: { overrideText: link.overrideText },
          }}
        >
          {!isFooterMenu
            ? capitalizeWord(link.overrideText || link.text)
            : link.overrideText || link.text
          // Override text is only in case of Picked for You collection.
          // Because we still want to log label as 'Picked for You', hence adding overrider text on UI.
          }
        </StyledLink>
      ) : (
        <>
          <TopLevelButton
            className='toplevel-btn'
            onClick={() => handleClick()}
          >
            <div>
              {getLinkPath(link) === '/log-in' && <StyledMyAccountSvg />}
              {link.overrideText || link.text}
            </div>
            <PlusButton active={open ? 1 : null} />
          </TopLevelButton>
          <Submenu
            className='submenu'
            active={open ? 1 : null}
            ref={ref}
            style={{
              height: open ? `${height}px` : `0px`,
            }}
          >
            <SubmenuColumns>
              {submenuColumnOne && (
                <>
                  {submenuColumnOne.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
              {submenuColumnTwo && (
                <>
                  {submenuColumnTwo.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
              {submenuColumnThree && (
                <>
                  {submenuColumnThree.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
              {submenuColumnFour && (
                <>
                  {submenuColumnFour.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
              {submenuColumnFive && (
                <>
                  {submenuColumnFive.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
              {submenuColumnSix && (
                <>
                  {submenuColumnSix.map(it => (
                    <SubMenu
                      submenuColumn={it.links}
                      submenuTitle={it?.title?.title}
                      isFooterMenu={isFooterMenu}
                    />
                  ))}
                </>
              )}
            </SubmenuColumns>
          </Submenu>
        </>
      )}
    </StyledMenuItem>
  )
}

export default MenuItem
