/* eslint-disable immutable/no-mutation */
import * as types from '../actions/types'

const filters = (state = {}, { type, payload }) => {
  switch (type) {
    case types.INITIALIZE_FILTERS: {
      return { ...payload.filters }
    }
    case types.TOGGLE_FILTER: {
      const newState = { ...state }
      if (newState[payload.filterType]) {
        const filterIndex = newState[payload.filterType].findIndex(
          it => it === payload.filterValue,
        )
        if (filterIndex === -1) {
          newState[payload.filterType] = [
            ...(newState[payload.filterType] ?? []),
            payload.filterValue,
          ]
        } else {
          newState[payload.filterType] = (
            newState[payload.filterType] ?? []
          ).filter(it => it !== payload.filterValue)
          if (newState[payload.filterType].length === 0) {
            delete newState[payload.filterType]
          }
        }
      } else {
        newState[payload.filterType] = [payload.filterValue]
      }

      return newState
    }

    case types.REMOVE_FILTER: {
      const newState = { ...state }
      newState[payload.filterType] = (
        newState[payload.filterType] ?? []
      ).filter(it => it !== payload.filterValue)
      if (newState[payload.filterType].length === 0) {
        delete newState[payload.filterType]
      }
      return newState
    }

    case types.CLEAR_ALL_FILTER: {
      return {}
    }

    default:
      return state
  }
}

export default filters
