import { captureException } from '../components/Sentry/utils'
import { getUserSession } from '../utils'

const getShopifyLoginURL = async redirectUri => {
  try {
    const userSession = await getUserSession()

    const headers = new Headers()
    headers.append('Content-Type', 'application/json')
    headers.append('Authorization', userSession.idToken.jwtToken)
    const multipassFetch = await fetch(
      `${process.env.GATSBY_COGNITO_SHOPIFY_MULTIPASS_TOKEN_ENDPOINT}?return_to=${redirectUri}`,
      {
        method: 'GET',
        headers,
      },
    )
    const multipassToken = await multipassFetch.json()

    return `${process.env.GATSBY_SHOPIFY_MULTIPASS_LOGIN_ENDPOINT}/${multipassToken}`
  } catch (e) {
    if (e !== 'No current user') {
      captureException(new Error(`Error while fetching shopify login url`, e))
      console.error('Error while fetching shopify login url', e)
    }

    return null
  }
}

export default getShopifyLoginURL
