import { useDispatch, useSelector } from 'react-redux'

import { addToWishlist, isFetching, toggleFromWishlist } from '../actions'

import { loadState, saveState, decodeIdFromGraphQLResponse } from '../utils'
import { extractProductJsonFromAPIProduct, zipObject } from '../utils/build'

import { captureException } from '../components/Sentry/utils'

import EventType from '../trackers/eventType'
import eventTracker from '../trackers/EventTracker'

const getWishlistFromBrowserStorage = () => loadState('wishlist')
const saveWishlistToBrowserStorage = saveState('wishlist')

const getWishlistQueryParam = (
  wishlist,
  wishlistProduct,
  isAlreadyInWishlist,
) => {
  const contentfulProductId =
    wishlistProduct.product_id ||
    decodeIdFromGraphQLResponse(wishlistProduct.productId)
  const contentfulProductWishlistQp = `${contentfulProductId}_${wishlistProduct.color}`

  const wishlistQp = wishlist.reduce((prev, current) => {
    const productId =
      current.product_id || decodeIdFromGraphQLResponse(current.productId)
    const productWishlistQp = `${productId}_${current.color}`

    if (
      isAlreadyInWishlist &&
      productWishlistQp === contentfulProductWishlistQp
    ) {
      return prev
    }

    return prev ? `${prev},${productWishlistQp}` : `${productWishlistQp}`
  }, '')

  return isAlreadyInWishlist
    ? wishlistQp
    : `${wishlistQp}${wishlistQp ? ',' : ''}${contentfulProductWishlistQp}`
}

const updateWishlistPromise = (userId, wishlistQp) => {
  const endpoint = `${process.env.GATSBY_QUINCE_STORE_FRONT_API}/update-user-wishlist/?user_id=${userId}&productIdColors=${wishlistQp}`

  // eslint-disable-next-line no-undef
  return fetch(endpoint)
    .then(response => {
      const responseJson = response.json()

      eventTracker.track(EventType.API_RESPONSE, {
        apiName: 'update-user-wishlist',
        inputRequest: { userId, wishlistQp },
        response: responseJson,
      })

      return responseJson
    })
    .catch(e => {
      captureException(
        e,
        { userId, wishlistQp },
        { type: 'API_FAILURE', API: 'UPDATE_USER_WISHLIST' },
      )
    })
}

const updateWishlist = async (userId, wishlistProduct, wishlist, dispatch) => {
  const wishlistId = `${wishlistProduct.shopifyProduct.handle}/${wishlistProduct.color}`
  const isActive = wishlist.some(obj => obj.wishlistId === wishlistId)

  const wishlistQp = getWishlistQueryParam(wishlist, wishlistProduct, isActive)

  await updateWishlistPromise(userId, wishlistQp)

  dispatch(toggleFromWishlist(wishlistProduct))
}

const getCombinedWishlist = (
  wishlistFromApi,
  currentWishlistInBrowserStorage,
) => {
  const formattedWishlistFromApi = wishlistFromApi.map(wlp => {
    if (wlp.wishlistId) return wlp

    const wishlistId = wlp.contentful_product
      ? `${wlp.contentful_product.shopifyProduct.handle}/${wlp.color}`
      : ''
    const mwlp = { ...wlp, wishlistId, ...wlp.contentful_product }

    if (mwlp.productImages) {
      return extractProductJsonFromAPIProduct(mwlp, 1400)
    }

    return mwlp
  })

  // Make a map of wishlist and wishlistId
  const wishlistFromApiWithHandleMap = zipObject(
    formattedWishlistFromApi.map(wlp => wlp.wishlistId),
    formattedWishlistFromApi,
  )

  const wishlistProductsNotPresentInApiData = currentWishlistInBrowserStorage.filter(
    wlp => !wishlistFromApiWithHandleMap[wlp.wishlistId],
  )

  return [...formattedWishlistFromApi, ...wishlistProductsNotPresentInApiData]
}

const fetchUserWishlistPromise = userId => {
  const endpoint = `${process.env.GATSBY_QUINCE_STORE_FRONT_API}/get-user-wishlist/?user_id=${userId}`

  // eslint-disable-next-line no-undef
  return fetch(endpoint)
    .then(response => {
      const responseJson = response.json()

      eventTracker.track(EventType.API_RESPONSE, {
        apiName: 'get-user-wishlist',
        inputRequest: { userId },
        response: responseJson,
      })

      return responseJson
    })
    .catch(e => {
      captureException(
        e,
        { userId },
        { type: 'API_FAILURE', API: 'GET_USER_WISHLIST' },
      )
    })
}

// isLoggedInNow - refers to the state where user transitioned from logged out state
// to login state. We need this state here to update any wishlist in local storage
// to the database
const fetchUserWishlist = async (userId, isLoggedInNow, dispatch) => {
  if (isLoggedInNow) {
    const currentWishlistInBrowserStorage =
      getWishlistFromBrowserStorage() || []

    saveWishlistToBrowserStorage('')

    dispatch(isFetching(true))
    const wishlist = await fetchUserWishlistPromise(userId)

    const combinedWishlist = getCombinedWishlist(
      wishlist,
      currentWishlistInBrowserStorage,
    )

    // eslint-disable-next-line immutable/no-let
    let combinedWishlistQueryParam = ''
    combinedWishlist.forEach((product, idx) => {
      const productId =
        product.product_id || decodeIdFromGraphQLResponse(product.productId)
      const productWishlistQp = `${productId}_${product.color}`

      combinedWishlistQueryParam += `${
        idx === 0 ? '' : ','
      }${productWishlistQp}`
    })

    await updateWishlistPromise(userId, combinedWishlistQueryParam)
    combinedWishlist.forEach(product => {
      dispatch(addToWishlist(product))
    })
    dispatch(isFetching(false))
  } else {
    dispatch(isFetching(true))
    const wishlist = await fetchUserWishlistPromise(userId)
    const combinedWishlist = getCombinedWishlist(wishlist, [])
    combinedWishlist.forEach(product => {
      dispatch(addToWishlist(product))
    })
    dispatch(isFetching(false))
  }
}

const useWishlist = () => {
  const dispatch = useDispatch()
  const { data: wishlist } = useSelector(state => state.wishlist)

  return {
    fetchUserWishlist: (userId, isLoggedInNow = false) =>
      fetchUserWishlist(userId, isLoggedInNow, dispatch),
    updateWishlist: (userId, wishlistProduct) =>
      updateWishlist(userId, wishlistProduct, wishlist, dispatch),
  }
}

export default useWishlist
