import EventType from '../eventType'

export default class PinterestTag {
  [EventType.ADD_TO_CART] = properties => {
    if (window.pintrk)
      window.pintrk('track', 'addtocart', {
        currency: 'USD',
        line_items: [
          {
            product_id: properties.productId,
            product_variant_id: properties.variantId,
            product_name:
              properties.productTitle + ' - ' + properties.variantTitle,
            product_price: properties.price,
            product_quantity: properties.quantity || 1,
          },
        ],
      })
  };

  [EventType.PRODUCT_VIEW] = properties => {
    if (window.pintrk)
      window.pintrk('track', 'pagevisit', {
        currency: 'USD',
        line_items: [
          {
            product_id: properties.productId,
            product_variant_id: properties.variantId,
            product_name:
              properties.productTitle + ' - ' + properties.variantTitle,
            product_price: properties.price,
            product_quantity: properties.quantity || 1,
          },
        ],
      })
  };

  [EventType.SEARCH] = properties => {
    if (window.pintrk)
      window.pintrk('track', 'search', {
        search_query: properties.searchQuery,
      })
  };

  [EventType.COLLECTION_VIEW] = properties => {
    if (window.pintrk) {
      let productIds = [
        ...new Set(properties.variantList.map(x => x.product_id)),
      ]
      window.pintrk('track', 'viewcategory', {
        line_items: [
          {
            product_category: properties.collectionName,
            product_variant_id: productIds,
          },
        ],
      })
    }
  }
}
