import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'
import Button from '../Button'

const Checkout = styled(Button)`
  background: ${props => props.theme.colors.primary} !important;
  color: ${props => props.theme.colors.secondary} !important;
  width: 100%;
  margin-bottom: 7px;
  font-size: 20px;
  padding: 15px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(7)};
    padding: ${desktopVw(14)};
    font-size: ${desktopVw(20)};
  }
`

export function CheckoutContainer({
  checkoutUrl,
  loading,
  onCheckoutClick,
  className,
}) {
  return (
    <div className={className}>
      <Checkout
        onClick={onCheckoutClick}
        href={checkoutUrl}
        trackParams={{
          trackGA: true,
          category: 'checkout',
          action: 'start_checkout',
        }}
        loading={loading}
      >
        Checkout
      </Checkout>
    </div>
  )
}
