import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import inputStyle from './inputStyle'
import DropdownSvg from '../../assets/dropdown.svg'
import { ErrorMessage } from '.'

const SelectWrapper = styled.div`
  position: relative;
  box-sizing: border-box;
`

const StyledSelect = styled.select`
  ${inputStyle}
  -webkit-appearance: none;
  font-size: 16px;
  font-family: ${props => props.theme.fonts.primary};
  border: 1px solid ${props => props.theme.colors.greyLight};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    text-transform: uppercase;
  }
`

const Option = styled.option``

const DropdownIcon = styled(DropdownSvg)`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  pointer-events: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    right: ${desktopVw(16)};
  }
`

export const Select = ({
  className,
  name,
  label,
  defaultValue,
  value,
  options,
  required = true,
  onChange,
  register,
  errors,
  renderOption,
  showLabel = true,
  errorClassName,
}) => {
  return (
    <>
      <SelectWrapper className={className}>
        <StyledSelect
          name={name}
          id={label}
          value={value}
          onChange={e => onChange && onChange(e)}
          aria-label={label || name}
          ref={
            register &&
            register({
              required: {
                value: required,
                message: `Select an option`,
              },
            })
          }
          className='Select__styled_select'
        >
          {showLabel && label && <Option value=''>{label}</Option>}
          {options.map(option => (
            <Option key={option} value={option}>
              {renderOption ? renderOption(option) : option}
            </Option>
          ))}
        </StyledSelect>
        <DropdownIcon />
      </SelectWrapper>
      {errors && errors[name] && (
        <ErrorMessage className={errorClassName}>
          {errors[name].message}
        </ErrorMessage>
      )}
    </>
  )
}
