import React from 'react'
import { withErrorBoundary } from '../ErrorBoundary/ErrorBoundary'
import UpsellCard from '../AddProductCard'
import styled from 'styled-components'

const StyledUpsell = styled(UpsellCard)`
  padding: 24px 10px;
  background-color: #fcfcfc;
  > div {
    padding: 0;
  }
`

const Upsell = ({ className, upsellVariant, product }) => {
  return (
    <>
      {upsellVariant && (
        <StyledUpsell
          shopifyProduct={product}
          product={upsellVariant?.product}
          startingColor={upsellVariant?.color}
          startingSize={upsellVariant?.size}
          className={className}
          tracker='upsell'
          addToCartTrackerSection={'cart_upsell_section'}
        />
      )}
    </>
  )
}

export default withErrorBoundary(Upsell, { fallbackRender: () => <div /> })
