import React, { useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import TextButton from '../TextButton'
import ForgotPasswordModal from '../ForgotPasswordModal'
import { ErrorMessage } from './'

const PasswordWrapper = styled.div`
  position: relative;
  display: flex;
  border: 1px solid ${props => props.theme.colors.greyLight};
  overflow: hidden;
  border-radius: 0;
  align-items: center;
`

const StyledInput = styled.input`
  display: block;
  width: 100%;
  font-size: 16px;
  border: none;
  font-family: ${props => props.theme.fonts.primary};
  padding: 22px 10px;
  line-height: 24px;
  text-align: left;
  color: ${props => props.theme.colors.primary};
  -webkit-appearance: none;

  &::placeholder {
    color: ${props => props.theme.colors.primary};
    font-size: 16px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(18)} ${desktopVw(22)} ${desktopVw(18)} ${desktopVw(15)};
    line-height: ${desktopVw(24)};

    &::placeholder {
      font-size: ${desktopVw(12)};
      text-transform: uppercase;
    }
  }
`

const StyledTextButton = styled(TextButton)`
  text-transform: uppercase;
  color: ${props => props.theme.colors.darkGrey};
  white-space: nowrap;
  font-size: 10px;
  margin-right: 10px;

  &:hover {
    background-image: ${props =>
      `linear-gradient(${props.theme.colors.darkGrey}, ${props.theme.colors.darkGrey});`};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(20)};
    font-size: ${desktopVw(10)};
  }
`

export const Password = ({
  className,
  placeholder,
  name = 'password',
  required = true,
  onChange,
  showForgotPassword,
  register,
  errors,
  username,
  backToLogin,
}) => {
  const [modalOpen, setModalOpen] = useState(false)
  return (
    <>
      <PasswordWrapper>
        <StyledInput
          className={className}
          placeholder={placeholder}
          name={name}
          type='password'
          onChange={e => onChange && onChange(e)}
          ref={
            register &&
            register({
              required: {
                value: required,
                message: `Please fill in ${placeholder}`,
              },
            })
          }
        />
        {showForgotPassword && (
          <>
            <StyledTextButton
              onClick={() => {
                backToLogin()
                setModalOpen(true)
              }}
            >
              Forgot my password
            </StyledTextButton>
            <ForgotPasswordModal
              open={modalOpen}
              onClose={() => setModalOpen(false)}
              username={username}
            />
          </>
        )}
      </PasswordWrapper>
      {errors && errors[name] && (
        <ErrorMessage>{errors[name].message}</ErrorMessage>
      )}
    </>
  )
}
