import React from 'react'
import * as Sentry from '@sentry/browser'
import Provider from './src/components/Provider'
import { stores } from './fill-config'
import useCheckoutHydration from './src/hooks/useCheckoutHydration'
import SentryWrapper from './src/components/Sentry'

import { SiteContext } from './src/contexts'

import Layout from './src/components/Layout'
import EventType from './src/trackers/eventType'
import initializeAnalytics from './src/trackers/initializeAnalytics'
import eventTracker from './src/trackers/EventTracker'
import experimentManager from './src/trackers/ExperimentManager'
import klaviyoInitializer from './src/trackers/KlaviyoInitializer'
import { initializeSnapchatPixel } from './src/trackers/SnapchatInitializer'
import { initializePepperjamPixel } from './src/trackers/PepperjamPixel'

import './src/shared/css/inter-ui.css'
import { executeSafely, getCookie, setCookie } from './src/utils'
import { initializePinterestTag } from './src/trackers/PinterestInitializer'

const CheckoutHydration = ({ children }) => {
  useCheckoutHydration()
  return children
}

export const wrapRootElement = ({ element }) => (
  <Provider stores={stores}>
    <CheckoutHydration>{element}</CheckoutHydration>
  </Provider>
)

export const wrapPageElement = ({
  element,
  props: {
    pageContext: {
      site,
      theme,
      slug,
      subscribeBannerCardColor,
      disableSEOHeaderNode,
      showBgColor,
    },
    data,
  },
}) => {
  if (slug === '/local-storage') return element
  return (
    <SentryWrapper value={site}>
      <SiteContext.Provider value={site}>
        <Layout
          theme={theme}
          slug={slug}
          subscribeBannerCardColor={subscribeBannerCardColor}
          data={data}
          disableSEOHeaderNode={disableSEOHeaderNode}
          showBgColor={showBgColor}
        >
          {element}
        </Layout>
      </SiteContext.Provider>
    </SentryWrapper>
  )
}

export const onRouteUpdate = ({ location, prevLocation }) => {
  if (!prevLocation || location.pathname !== prevLocation.pathname) {
    eventTracker.track(EventType.PAGE_VIEW, {})
  }
  if (prevLocation && location.pathname !== prevLocation.pathname) {
    window.__client_side_navigation__ = true
  }
}

function setCrazyEggVisitor() {
  if (window.CE2) {
    CE2.identify(getCookie('_lb_user_id'))
  } else {
    // if not loaded yet, set the recording identifier once the script loads
    window.CE_READY = function() {
      CE2.identify(getCookie('_lb_user_id'))
    }
  }
}

function clearOldUpsellMemory() {
  if (!getCookie('OLD_UPSELL_CLEARED')) {
    setCookie('OLD_UPSELL_CLEARED', true)
    localStorage.removeItem('upsell')
  }
}

export const onClientEntry = () => {
  Sentry.init({ dsn: process.env.SENTRY_DSN })
  // TODO: remove after 30 days
  executeSafely(() => clearOldUpsellMemory())
  // Initialize analytics
  executeSafely(() => initializeAnalytics())
  executeSafely(() => setCrazyEggVisitor())
  executeSafely(() => klaviyoInitializer.initialize())
  // Initialize experiment manager
  executeSafely(() => experimentManager.initialize())
  // Initialize pepperjam
  executeSafely(() => initializePepperjamPixel())
  // Initialize snapchat pixel
  executeSafely(() => initializeSnapchatPixel())
  // Initialize pinterest tag
  executeSafely(() => initializePinterestTag())
}
