import * as types from '../actions/types'

// UI reducers
const initialState = {
  mobileMenuOpen: false,
  activeMegaMenuId: '',
  cartOpen: false,
  asideFilterOpen: false,
  asideSortOpen: false,
}

const ui = (state = initialState, { type, payload }) => {
  switch (type) {
    case types.TOGGLE_MOBILE_MENU:
      return { ...state, mobileMenuOpen: !state.mobileMenuOpen }
    case types.SET_MOBILE_MENU_OPEN:
      return { ...state, mobileMenuOpen: payload }
    case types.SET_ACTIVE_MEGAMENU_ID:
      return { ...state, megaMenuId: payload }
    case types.SET_CART_OPEN:
      return { ...state, cartOpen: payload }
    case types.SET_ASIDE_FILTER_OPEN:
      return { ...state, asideFilterOpen: payload }
    case types.SET_ASIDE_SORT_OPEN:
      return { ...state, asideSortOpen: payload }
    default:
      return state
  }
}

export default ui
