import { useContext } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { StoreContext } from '../contexts'
import { replaceCheckout, setCartOpen } from '../actions'
import replaceCheckoutLineItems from '../shopify/replaceCheckoutLineItems'
import {
  getNewLineItemsWithTransformedSrc,
  handleShopifyDiscount,
  pick,
} from '../utils'
import useDiscount from './useDiscount'
import useReplaceUpsells from './useReplaceUpsells'
import { captureException } from '../components/Sentry/utils'

function handleUpdateLineItemQuantity(lineItems, input) {
  return lineItems.reduce(
    (lineItemsAcc, { id, variantId, quantity, customAttributes }) => {
      if (id === input.lineItemId) {
        if (input.quantity === 0) return lineItemsAcc
        return [
          ...lineItemsAcc,
          {
            variantId: input.variantId,
            quantity: input.quantity,
            customAttributes: input.customAttributes,
          },
        ]
      }
      return [...lineItemsAcc, { variantId, quantity, customAttributes }]
    },
    [],
  )
}

function handleAddVariantToCart(lineItems, input) {
  return [
    ...lineItems,
    {
      variantId: input.variantId,
      quantity: input.quantity,
      customAttributes: input.customAttributes,
    },
  ]
}

function handleRemoveLineItem(lineItems, input) {
  return lineItems.filter(lineItem => lineItem.id !== input.lineItemId)
}

const useCheckoutLineItem = () => {
  const stores = useContext(StoreContext)
  const activeStore = useSelector(({ storeSubdomain }) => storeSubdomain)
  const { addDiscountToCheckout } = useDiscount()
  const { replaceUpsell } = useReplaceUpsells()

  const { storeSubdomain: activeStoreSubdomain } = useSelector(
    ({ storeSubdomain, checkouts }) => ({
      storeSubdomain,
      checkouts,
    }),
  )
  const dispatch = useDispatch()

  const lineItemHelper = async (
    input,
    lineItemsHandler = () => {},
    callUpsell = false,
  ) => {
    const storeSubdomain = activeStoreSubdomain

    if (input) {
      if (input.customAttributes) {
        input.customAttributes.forEach(
          customAttribute => delete customAttribute.__typename,
        )
      }

      const { client } = stores[storeSubdomain]
      // Override with what we have in localStorage - to maintain synchronization across tabs
      const checkout = JSON.parse(
        localStorage.getItem('onequince::checkout::object'),
      )
      const lineItemWithoutTypeNames = (checkout?.lineItems?.edges || []).map(
        ({
          node: {
            id,
            variant: { id: variantId },
            quantity,
            customAttributes,
          },
        }) => {
          if (customAttributes)
            customAttributes.forEach(
              customAttribute => delete customAttribute.__typename,
            )
          return { id, variantId, quantity, customAttributes }
        },
      )
      await handleShopifyDiscount(activeStore, addDiscountToCheckout, checkout)
      try {
        const updatedLineItems = lineItemsHandler(
          lineItemWithoutTypeNames,
          input,
        )

        const {
          data: {
            checkoutLineItemsReplace: {
              checkout: {
                id,
                subtotalPriceV2,
                lineItemsSubtotalPrice,
                webUrl,
                lineItems,
                discountApplications,
              },
            },
          },
        } = await replaceCheckoutLineItems(client, {
          checkoutId: checkout.id,
          lineItems: updatedLineItems.map(lineItem =>
            pick(lineItem, ['customAttributes', 'quantity', 'variantId']),
          ),
        })

        if (callUpsell) {
          try {
            replaceUpsell({ lineItems: updatedLineItems })
          } catch (e) {
            captureException(e)
          }
        }

        const newLineItems = getNewLineItemsWithTransformedSrc(lineItems)

        const newLineItemsWithEdges = { edges: newLineItems }

        dispatch(
          replaceCheckout({
            ...checkout,
            discountApplications,
            id,
            webUrl,
            subtotalPriceV2,
            lineItemsSubtotalPrice,
            lineItems: newLineItemsWithEdges,
          }),
        )
        dispatch(setCartOpen(true))
        return newLineItemsWithEdges
      } catch (error) {
        console.error(error)
      }
    }
  }

  const updateLineItemQuantity = ({
    lineItemId,
    quantity,
    variantId,
    customAttributes,
  }) =>
    lineItemHelper(
      { lineItemId, quantity, variantId, customAttributes },
      handleUpdateLineItemQuantity,
    )

  const addVariantToCart = ({ quantity = 1, variantId, customAttributes }) =>
    lineItemHelper(
      { quantity, variantId, customAttributes },
      handleAddVariantToCart,
      true,
    )

  const removeLineItem = ({ lineItemId }) =>
    lineItemHelper({ lineItemId }, handleRemoveLineItem)

  return { updateLineItemQuantity, addVariantToCart, removeLineItem }
}

export default useCheckoutLineItem
