import { getCurrentUser } from '../utils'

export function initializePinterestTag() {
  const user = getCurrentUser()
  window.pintrk(
    'load',
    `${process.env.GATSBY_PINTEREST_TAG_ID}`,
    user && user.email
      ? {
          em: user.email,
        }
      : undefined,
  )
  window.pintrk('page')
}
