import { useContext } from 'react'
import { StoreContext } from '../contexts'
import checkoutDiscountCodeApply from '../shopify/checkoutDiscountCodeApply'

const useDiscount = () => {
  const stores = useContext(StoreContext)

  const addDiscountToCheckout = async (discountCode, checkoutId) => {
    try {
      const discountResponse = await checkoutDiscountCodeApply(
        stores['lastbrand'].client,
        {
          discountCode: discountCode,
          checkoutId: checkoutId,
        },
      )
      return { response: discountResponse, error: null, loading: false }
    } catch (error) {
      return { response: null, error: error, loading: false }
    }
  }
  return { addDiscountToCheckout }
}

export default useDiscount
