// UI actions
import * as types from './types'

export const toggleMobileMenu = () => ({ type: types.TOGGLE_MOBILE_MENU })

export const setMobileMenuOpen = payload => ({
  type: types.SET_MOBILE_MENU_OPEN,
  payload,
})

export const setActiveMegaMenuId = payload => ({
  type: types.SET_ACTIVE_MEGAMENU_ID,
  payload,
})

export const setCartOpen = payload => ({ type: types.SET_CART_OPEN, payload })

export const setAsideFilterOpen = payload => ({
  type: types.SET_ASIDE_FILTER_OPEN,
  payload,
})

export const setAsideSortOpen = payload => ({
  type: types.SET_ASIDE_SORT_OPEN,
  payload,
})
