import CookieConsent from 'react-cookie-consent'
import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'
import { desktopVw } from '../styles/utils'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const StyledConsentCookies = styled.div`
  .cookie_consent_container {
    justify-content: center !important;
    text-align: center !important;
    background: ${({ theme }) => theme.colors.green} !important;
    font-size: 8px !important;
    line-height: 12.34px !important;
    padding: 17px 20px 20px !important;
    width: unset !important;
  }
  .cookie_consent_buttons {
    font-size: 8px !important;
    background: ${({ theme }) => theme.colors.green} !important;
    color: ${({ theme }) => theme.colors.secondary} !important;
    border: ${({ theme }) => `1px solid ${theme.colors.secondary}`} !important;
    margin: 0 !important;
    padding: 8px 10px !important;
    width: 100% !important;
  }
  .cookie_consent_content {
    margin: 0 !important;
    margin-bottom: 29px !important;
  }
  .cookie_consent_buttons_wrapper {
    width: 100% !important;
  }
  a {
    font-weight: 500;
    text-decoration: underline;
  }
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .cookie_consent_container {
      align-items: center !important;
      justify-content: space-between !important;
      font-size: 0.7vw !important;
      line-height: ${desktopVw(12.58)} !important;
      text-align: left !important;
      padding: ${desktopVw(16)} ${desktopVw(41)} !important;
      width: unset !important;
      right: 0 !important;
    }
    .cookie_consent_content {
      flex: 0 0 ${desktopVw(525)} !important;
      margin: 0 !important;
    }
    .cookie_consent_buttons_wrapper {
      width: unset !important;
    }
    .cookie_consent_buttons {
      font-size: 0.7vw !important;
      padding: ${desktopVw(8)} ${desktopVw(18)} !important;
      width: unset !important;
      text-transform: uppercase !important;
      &:hover {
        text-decoration: underline !important;
      }
    }
  }
`

export function CookiesConsent() {
  return (
    <StyledConsentCookies>
      <CookieConsent
        location='bottom'
        buttonText='Accept All Cookies'
        containerClasses='cookie_consent_container'
        contentClasses='cookie_consent_content'
        buttonClasses='cookie_consent_buttons'
        buttonWrapperClasses='cookie_consent_buttons_wrapper'
        expires={150}
        onAccept={() => {
          eventTracker.track(EventType.BUTTON_CLICK, {
            action: 'accept_cookies_consent',
          })
        }}
      >
        We use cookies to improve our site and your shopping experience. By
        continuing to browse our site you accept our cookie policy. Please{' '}
        <Link to='/privacy-policy'>click here</Link> for more information
      </CookieConsent>
    </StyledConsentCookies>
  )
}
