import React from 'react'
import PageNotFound from '../../templates/404'
import theme from '../../styles/theme'
import Layout from '../Layout'
import { SiteContext } from '../../contexts'
import { getCookie, setCookie } from '../../utils'
import eventTracker from '../../trackers/EventTracker'
import EventType from '../../trackers/eventType'
import { captureFatalException } from './utils'

const GATSBY_STATIC_QUERY_ERROR = 'The result of this StaticQuery could not be fetched'.toLowerCase()
const IS_PAGE_RELOADED_ON_ERROR = '__page_reloaded_on_error'
const PAGE_RELOADED_EXPIRY_MINUTES = 10

function handleGatsbyStaticQueryError() {
  setCookie(IS_PAGE_RELOADED_ON_ERROR, true, PAGE_RELOADED_EXPIRY_MINUTES)
  eventTracker.track(EventType.SITE_ERROR, {
    action: 'gatsby_static_query_error',
    context: { errorClass: 'critical' },
  })
  window.location.reload(true)
}

function trackError(error) {
  eventTracker.track(EventType.SITE_ERROR, {
    action: '404',
    context: { errorClass: 'critical', error },
  })
}

export default class SentryWrapper extends React.Component {
  constructor(props) {
    super(props)
    this.state = { error: null }
  }

  componentDidCatch(error, errorInfo) {
    const errorMessage = error?.message || error
    if (
      typeof errorMessage === 'string' &&
      errorMessage.toLowerCase().includes(GATSBY_STATIC_QUERY_ERROR) &&
      !getCookie(IS_PAGE_RELOADED_ON_ERROR)
    ) {
      handleGatsbyStaticQueryError()
    } else if (!getCookie(IS_PAGE_RELOADED_ON_ERROR)) {
      setCookie(IS_PAGE_RELOADED_ON_ERROR, true, PAGE_RELOADED_EXPIRY_MINUTES)
      trackError(error)
      captureFatalException(error, errorInfo)
      window.location.reload(true)
    } else {
      trackError(error)
      this.setState({ error })
      captureFatalException(error, errorInfo)
    }
  }

  render() {
    if (this.state.error) {
      return (
        <SiteContext.Provider value={this.props.value}>
          <Layout theme={theme} slug='/404'>
            <PageNotFound />
          </Layout>
        </SiteContext.Provider>
      )
    }
    // when there's not an error, render children untouched
    return this.props.children
  }
}
