import * as types from './types'

/**
 * The product handle
 * @param {String} payload
 */
export const addToWishlist = product => ({
  type: types.ADD_TO_WISHLIST,
  wishlistId: `${product.handle}/${product.color}`,
  ...product,
})

/**s
 * The product handle
 * @param {String} payload
 */
export const removeFromWishList = product => ({
  type: types.REMOVE_FROM_WISHLIST,
  wishlistId: `${product.handle}/${product.color}`,
  ...product,
})

/**s
 * The product handle
 * @param {String} payload
 */
export const toggleFromWishlist = product => ({
  type: types.TOGGLE_FROM_WISHLIST,
  wishlistId: `${product.handle}/${product.color}`,
  ...product,
})

export const isFetching = fetchingState => ({
  type: types.IS_FETCHING_WISHLIST,
  isFetching: fetchingState,
})
