// Note: We will now no longer have cart details in any event as we dont get this info from checkout graphql api
const logSessionStreamEvent = (
  event_type,
  entity_type,
  entity_id,
  context,
  cart_details,
) => {
  cart_details = cart_details || {}
  context = context || {}
  let email =
    getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
    getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL)
  if (email) {
    email = email.toLowerCase()
  }
  let event_data = {
    id: generateUniqueEventId(),
    event_type: event_type,
    entity_type: entity_type,
    entity_id: entity_id,
    context: context,
    session_id: getCookie(ANALYTIC_COOKIES.SESSION_ID),
    lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
    email: email,
    device: getUserDevice(),
    device_category: getUserDeviceCategory(),
    url: window.location.href,
    campaign: {
      source: getCookie(ANALYTIC_COOKIES.UTM_SOURCE),
      medium: getCookie(ANALYTIC_COOKIES.UTM_MEDIUM),
      campaign: getCookie(ANALYTIC_COOKIES.UTM_CAMPAIGN),
      content: getCookie(ANALYTIC_COOKIES.UTM_CONTENT),
      term: getCookie(ANALYTIC_COOKIES.UTM_TERM),
    },
    event_time: new Date(),
    cart_token: cart_details.token,
    active_experiments: getActiveExperiments(),
    referrer: document.referrer,
    qclid: getCookie(ANALYTIC_COOKIES.QCLID),
  }
  if (process.env.GATSBY_DEBUG_EVENT_LOGGING) {
    console.log('Internal Analytics', event_data)
  }
  fetch(process.env.GATSBY_INTERNAL_ANALYTICS_URL, {
    method: 'POST',
    mode: 'cors',
    credentials: 'same-origin',
    headers: {
      'Content-type': 'application/json',
    },
    redirect: 'follow',
    referrer: 'no-referrer',
    body: JSON.stringify(event_data),
  }).catch(err => captureException(err))
}

const captureTentativeEmail = email => {
  let emailCookie = getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL)
  if (!emailCookie && email && validateEmail(email)) {
    setCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL, email)
  }
}

// Mimicking what is done in the render code of collection page.
const getVariantLocationInCollectionOnDesktop = index => {
  if (index < 3) {
    return { row: 0, col: index }
  }
  if (index < 7) {
    return { row: 1, col: index - 3 }
  }
  if (index === 7) {
    return { row: 3, col: 0 }
  }
  if (index < 12) {
    return { row: 4, col: index - 8 }
  }
  if (index < 14) {
    return { row: 5, col: index - 12 }
  }
  if (index < 17) {
    return { row: 6, col: index - 14 }
  }
  let loadMoreIndex = index - 17
  let block = parseInt(loadMoreIndex / 7)
  let blockIndex = loadMoreIndex % 7
  let row = 6 + block * 2 + (blockIndex < 4 ? 1 : 2)
  let col = blockIndex < 4 ? blockIndex : blockIndex - 4
  return {
    row,
    col,
  }
}

// Mimicking what is done in the render code of collection page.
const getVariantLocationInCollectionOnMobile = index => {
  let block = parseInt(index / 3)
  let blockIndex = index % 3
  let row = block * 2 + (blockIndex < 2 ? 0 : 1)
  let col = blockIndex < 2 ? blockIndex : blockIndex - 2
  return {
    row,
    col,
  }
}

const S4 = () => (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1)

//TODO: Think of a better algorithm to generate this.
const generateUniqueEventId = () => {
  return (
    S4() +
    S4() +
    '-' +
    S4() +
    '-4' +
    S4().substr(0, 3) +
    '-' +
    S4() +
    '-' +
    S4() +
    S4() +
    S4()
  ).toLowerCase()
}

const getActiveExperiments = () => {
  let activeExperiments = []
  let experimentBuckets = experimentManager.getUserBucketsForEventLogging()
  for (let experimentBucket of experimentBuckets) {
    activeExperiments.push(
      experimentBucket.experimentName + '_' + experimentBucket.bucketName,
    )
  }
  return activeExperiments
}
