import React from 'react'
import styled from 'styled-components'
import { CheckoutContainer } from './CheckoutContainer'
import { desktopVw } from '../../styles/utils'

export function CartFooter({
  checkoutUrl,
  discountApplicable,
  lineItemsSubtotalPrice,
  loading,
  onCheckoutClick,
  subtotalPriceV2,
  pdExpDiscount,
}) {
  return (
    <Footer>
      <Subtotal>
        Subtotal
        <PriceContainer>
          {lineItemsSubtotalPrice && discountApplicable && (
            <ActualPrice>
              {`$${parseFloat(
                lineItemsSubtotalPrice.amount - pdExpDiscount,
              ).toFixed(2)}`}
            </ActualPrice>
          )}
          <DiscountedPrice>
            {subtotalPriceV2 &&
              `$${parseFloat(subtotalPriceV2.amount + pdExpDiscount).toFixed(
                2,
              )}`}
          </DiscountedPrice>
          {lineItemsSubtotalPrice && discountApplicable && (
            <DiscountAmount>
              &nbsp;(-$
              {parseFloat(
                parseFloat(lineItemsSubtotalPrice.amount) -
                  parseFloat(subtotalPriceV2.amount) -
                  pdExpDiscount,
              ).toFixed(2)}
              )
            </DiscountAmount>
          )}
        </PriceContainer>
      </Subtotal>
      <StandardShipping>
        Standard Shipping
        <StandardShippingText>FREE</StandardShippingText>
      </StandardShipping>
      <CheckoutContainer
        onCheckoutClick={onCheckoutClick}
        checkoutUrl={checkoutUrl}
        loading={loading}
      />
    </Footer>
  )
}

const Footer = styled.footer`
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 24px 10px;
  box-shadow: 0px -3px 4px rgba(0, 0, 0, 0.25);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(25)} ${desktopVw(29)} ${desktopVw(36)};
  }
`
const Subtotal = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
  padding-top: 10px;
  font-size: 12px;
  letter-spacing: 2px;
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin-bottom: ${desktopVw(12)};
  }
`
const StandardShipping = styled.div`
  display: flex;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 12px;
  justify-content: space-between;
  margin-bottom: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin-bottom: ${desktopVw(12)};
  }
`
const StandardShippingText = styled.span`
  color: ${props => props.theme.colors.lightGreen};
  margin-left: ${desktopVw(155)};
`
const PriceContainer = styled.span`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(77)};
  }
`
const DiscountedPrice = styled.span``
const ActualPrice = styled.span`
  text-decoration: line-through;
  margin-right: 5px;
  color: ${props => props.theme.colors.ashGrey};
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(5)};
  }
`
const DiscountAmount = styled.span`
  color: ${props => props.theme.colors.lightGreen};
`
