import EventType from '../eventType'

export default class FbPixel {
  // Note: Not handling page view event here explicitly, this is because fb pixel script takes care
  // of triggering Page View event in case of browser history state change.
  //  https://developers.facebook.com/ads/blog/post/v2/2017/05/29/tagging-a-single-page-application-facebook-pixel/

  [EventType.ADD_TO_CART] = properties => {
    logFbPixelEvent({
      eventType: 'AddToCart',
      contentIds: [properties.productId],
      contentType: 'product_group',
      contentName: properties.productTitle + ' - ' + properties.variantTitle,
      contentCategory: properties.productType,
      currency: 'USD',
      value: formatRevenue(properties.price),
      numItems: properties.quantity || 1,
    })
  };

  [EventType.PRODUCT_VIEW](properties) {
    logFbPixelEvent({
      eventType: 'ViewContent',
      contentIds: [properties.productId],
      contentType: 'product_group',
      contentName: properties.productTitle + ' - ' + properties.variantTitle,
      contentCategory: properties.productType,
      currency: 'USD',
      value: formatRevenue(properties.price),
    })
  }

  // [EventType.HOME_PAGE_VIEW] = properties => {
  //   logFbPixelEvent({ eventType: 'ViewContent', contentType: 'landing_page' })
  // };

  // [EventType.COLLECTION_VIEW] = properties => {
  //   logFbPixelEvent({
  //     eventType: 'ViewContent',
  //     contentIds: [properties.collectionId], // TODO: Verify correctness of this.
  //     contentType: 'product_collection',
  //     contentName: properties.subCollectionName || properties.collectionName,
  //   })
  // }
}

const logFbPixelEvent = ({
  eventType,
  contentIds,
  contentType,
  contentName,
  contentCategory,
  currency,
  value,
  numItems,
}) => {
  window.fbq('track', eventType, {
    content_ids: contentIds,
    content_type: contentType,
    content_name: contentName,
    content_category: contentCategory,
    currency: currency,
    value: value,
    num_items: numItems,
  })
}

const formatRevenue = price => {
  if (price != null) {
    return price
  }
  return 0
}
