import React, { useEffect } from 'react'
import styled from 'styled-components'

import useBodyScrollLock from '../../../hooks/useBodyScrollLock'

import MenuItem from '../../MenuItem'

const StyledNav = styled.nav`
  padding: 35px 0 23px;
  height: calc(100% - ${props => props.theme.header.mobile.height}px);
  box-sizing: border-box;
  border-top: 1px solid ${props => props.theme.colors.primary};
`

const MenusContainer = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0 31px;
  border-bottom: 1px solid ${props => props.theme.colors.primary};
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }
`

const MenuPrimary = styled.ul`
  margin-bottom: 64px;
`

const MenuSecondary = styled.ul``

const Nav = ({ menuPrimary, menuSecondary }) => {
  useBodyScrollLock(true)

  return (
    <StyledNav>
      <MenusContainer>
        <MenuPrimary>
          {menuPrimary.map(({ id, link, ...rest }) => {
            return <MenuItem key={link.id} id={id} link={link} {...rest} />
          })}
        </MenuPrimary>
        <MenuSecondary>
          {menuSecondary.map(({ id, link, ...rest }) => {
            return <MenuItem key={link.id} id={id} link={link} {...rest} />
          })}
        </MenuSecondary>
      </MenusContainer>
    </StyledNav>
  )
}

export default Nav
