import React from 'react'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const TrackableAnchor = props => {
  let clonedProps = { ...props }
  delete clonedProps.trackParams
  return (
    <a {...clonedProps} onClick={handleOnClick(props)}>
      {props.children}
    </a>
  )
}

const handleOnClick = props => () => {
  if (props.onClick) {
    props.onClick()
  }
  if (props.trackParams) {
    eventTracker.trackClick(EventType.LINK_CLICK, props.trackParams)
  }
}

export default TrackableAnchor
