import * as types from '../actions/types'
import { loadState, saveState } from '../utils/localStorage'

const initialStateWishlistState = loadState('wishlist')
const saveWishlist = saveState('wishlist')

const wishlist = (
  state = { data: initialStateWishlistState || [], isFetching: false },
  { type, wishlistId, isFetching, ...payload },
) => {
  switch (type) {
    case types.ADD_TO_WISHLIST: {
      const newWishlist = state.data.filter(
        obj => obj.wishlistId !== wishlistId,
      )

      newWishlist.push({ wishlistId, ...payload })

      saveWishlist(newWishlist)
      return { ...state, data: newWishlist }
    }

    case types.REMOVE_FROM_WISHLIST: {
      const newWishlist = state.data.filter(
        obj => obj.wishlistId !== wishlistId,
      )

      saveWishlist(newWishlist)
      return { ...state, data: newWishlist }
    }

    case types.TOGGLE_FROM_WISHLIST: {
      // eslint-disable-next-line immutable/no-let
      let newWishlist = [...state.data]

      const item = newWishlist.find(obj => obj.wishlistId === wishlistId)

      if (!item) {
        newWishlist = [...state.data, { wishlistId, ...payload }]
      } else {
        newWishlist = newWishlist.filter(obj => obj.wishlistId !== wishlistId)
      }

      saveWishlist(newWishlist)

      return { ...state, data: newWishlist }
    }

    case types.IS_FETCHING_WISHLIST: {
      return { ...state, isFetching }
    }

    default:
      return state
  }
}

export default wishlist
