import gql from 'graphql-tag'

export const variant = gql`
  fragment Variant on ProductVariant {
    availableForSale
    id
    title
    image {
      transformedSrc(maxWidth: 190)
    }
    presentmentPrices(first: 250) {
      edges {
        node {
          price {
            amount
            currencyCode
          }
          compareAtPrice {
            amount
            currencyCode
          }
        }
      }
    }
    selectedOptions {
      name
      value
    }
    priceV2 {
      amount
    }
    product {
      id
    }
    sku
    weight
    weightUnit
  }
`

export const discount = gql`
  fragment Discount on DiscountAllocation {
    allocatedAmount {
      amount
    }
    discountApplication {
      allocationMethod
      targetSelection
      targetType
      __typename
      ... on ScriptDiscountApplication {
        title
      }
      ... on AutomaticDiscountApplication {
        title
      }
      ... on ManualDiscountApplication {
        title
      }
      ... on DiscountCodeApplication {
        title: code
      }
    }
  }
`

export const checkout = gql`
  fragment ShopifyCheckout on Checkout {
    id
    subtotalPriceV2 {
      amount
    }
    lineItemsSubtotalPrice {
      amount
    }
    discountApplications(first: 10) {
      edges {
        node {
          ... on DiscountCodeApplication {
            code
          }
        }
      }
    }
    lineItems(first: 250) {
      edges {
        node {
          id
          title
          customAttributes {
            key
            value
          }
          quantity
          variant {
            ...Variant
          }
          discountAllocations {
            ...Discount
          }
        }
      }
    }
    orderStatusUrl
    webUrl
  }
  ${variant}
  ${discount}
`
