import React from 'react'
import styled, { css } from 'styled-components'
import Image from 'gatsby-image'

import { desktopVw } from '../styles/utils'
import theme from '../styles/theme'
import { getLinkPath, imageSources } from '../utils'

import Card from './Card'
import Button from './Button'
import Klaviyo from './Klaviyo'

const StyledBanner = styled.div`
  position: relative;
  min-height: ${props => (props.height ? '460px' : '910px')};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: ${props => (props.flex ? 'flex' : null)};
    align-items: center;
    min-height: ${props =>
      props.height ? `${desktopVw(519)}` : `${desktopVw(752)}`};
  }
`

const cardStyles = css`
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0 ${props => props.theme.spacings.mobile}px 15px;

  .card-title {
    white-space: pre-wrap;
  }

  .card-textwrapper {
    p {
      white-space: pre-wrap;
      max-width: 100%;
      font-size: 15px;
      line-height: 22px;

      &:last-child {
        margin: 0;
      }
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(70)} ${desktopVw(80)};

    width: ${desktopVw(359)};
    right: ${props => (props.alignRight ? '0' : null)};

    .card-title {
      font-size: ${desktopVw(30)};
      line-height: ${desktopVw(30)};
    }

    .card-subtitle {
      margin-top: ${desktopVw(6)};
      font-size: ${desktopVw(19)};
    }
  }
`

const BannerCard = styled(Card)`
  ${cardStyles}
  padding: 41px 20px 20px;

  .card-header {
    margin-bottom: 45px;
  }

  .card-title,
  .card-subtitle {
    font-size: ${props => (props.fontSize ? props.fontSize : '30')}px;
    line-height: 1;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(50)} ${desktopVw(40)} ${desktopVw(40)};

    .card-header {
      margin-bottom: ${desktopVw(136)};
    }

    .card-title,
    .card-subtitle {
      font-size: ${props =>
        props.fontSize ? desktopVw(props.fontSizeDesktop) : desktopVw(30)};
    }

    .card-textwrapper {
      p {
        font-size: ${desktopVw(15)};
        line-height: ${desktopVw(22)};
      }
    }
  }
`

const SubscribeBannerCard = styled(Card)`
  ${cardStyles}
  padding: 38px 20px 20px;

  .card-header {
    margin-bottom: 16px;
  }

  .card-title {
    font-size: 20px;
    line-height: 25px;
    text-transform: uppercase;
  }

  .card-textwrapper {
    display: none;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: static;
    margin: 0 ${desktopVw(80)} 0 auto;
    padding: ${desktopVw(42)} ${desktopVw(40)} ${desktopVw(66)};

    .card-header {
      margin-bottom: ${desktopVw(56)};
    }

    .card-title {
      font-size: ${desktopVw(30)};
      line-height: 1;
    }

    .card-subtitle {
      margin-top: ${desktopVw(10)};
    }

    .card-textwrapper {
      display: block;

      p {
        max-width: auto;
        font-size: ${desktopVw(12)};
        text-transform: uppercase;
        line-height: ${desktopVw(20)};
      }
    }
  }
`

const StyledButton = styled(Button)`
  display: block;
  font-size: 11px;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;
  margin-top: 21px;
  border: solid 1px
    ${props =>
      props.color ? props.theme.colors.secondary : props.theme.colors.primary};
  color: ${props =>
    props.color ? props.theme.colors.secondary : props.theme.colors.primary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(28)} 0;
    text-transform: uppercase;
    font-size: ${desktopVw(11)};
    margin-top: ${desktopVw(18)};

    &:hover {
      background: ${props =>
        props.color
          ? props.theme.colors.secondary
          : props.theme.colors.primary};
      border-color: ${props =>
        props.color
          ? props.theme.colors.secondary
          : props.theme.colors.primary};
      color: ${props =>
        props.color
          ? props.theme.colors.primary
          : props.theme.colors.secondary};
    }
  }
`

const Banner = ({
  className,
  image,
  imageDesktop,
  card,
  cardColor = theme.colors.primary,
}) => {
  return (
    <StyledBanner
      height={card && card.subscribeForm ? 1 : null}
      flex={card && card.subscribeForm ? 1 : null}
      className={className}
    >
      <Image
        fluid={imageSources(image, imageDesktop)}
        style={{
          position: 'absolute',
          left: '0',
          right: '0',
          bottom: '0',
          top: '0',
          zIndex: '-1',
        }}
        alt={image.description || image.title}
      />
      {card && !card.subscribeForm && (
        <BannerCard
          title={card.title.title}
          fontSize={card.titleFontSize}
          fontSizeDesktop={card.titleFontSizeDesktop}
          subtitle={card.subtitle}
          json={card.text.json}
          backgroundColor={card.backgroundColor.hexcode}
        >
          {card.button && (
            <StyledButton to={getLinkPath(card.button)} color>
              {card.button.text}
            </StyledButton>
          )}
        </BannerCard>
      )}
      {card && card.subscribeForm && (
        <SubscribeBannerCard
          backgroundColor={cardColor || theme.colors.primary}
          title={card.title.title}
          subtitle={card.subtitle}
          json={card.text.json}
        >
          {card.button && (
            <StyledButton to='/'>{card.button.text}</StyledButton>
          )}
          <Klaviyo
            buttonTextColor={card.backgroundColor}
            klaviyoListId={card.subscribeForm.klaviyoListId}
            successMessage={card.subscribeForm.successMessage.json}
          />
        </SubscribeBannerCard>
      )}
    </StyledBanner>
  )
}

export default Banner
