import React, { useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import StarIcon from '../../assets/star.svg'
import { ErrorMessage } from './'

const StyledRatingInput = styled.div`
  display: flex;
  align-items: flex-start;
`

const Rating = styled.div`
  display: flex;
  flex-direction: column;
`

const RatingInputWrapper = styled.div`
  display: flex;
  align-items: center;
  font-size: 16px;

  span {
    margin-right: 10px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};

    span {
      margin-right: ${desktopVw(10)};
    }
  }
`

const Rate = styled.div`
  position: relative;
  margin-right: 2px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(4.5)};
  }
`

const Star = styled(StarIcon)`
  width: 20px;

  .star {
    &__stroke {
      fill: ${props => props.theme.colors.yellow};
    }
    &__inner {
      fill: ${props => props.fill && props.theme.colors.yellow};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(20)};
  }
`

const Input = styled.input`
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;

  &:checked {
    + svg {
      .star {
        &__inner {
          fill: ${props => props.fill && props.theme.colors.yellow};
        }
      }
    }
  }
`

export const RatingInput = ({
  name,
  range,
  required = true,
  register,
  errors,
}) => {
  const [value, setValue] = useState()
  return (
    <Rating>
      <RatingInputWrapper>
        <span>Rating</span>
        <StyledRatingInput>
          {[...Array(range)].map((_, i) => (
            <Rate key={i}>
              <Input
                type='radio'
                value={i + 1}
                name={name}
                defaultChecked={value === `${i + 1}`}
                onChange={() => setValue(i + 1)}
                aria-label={`${i} star`}
                ref={register({
                  required: { value: required, message: `Please add a rating` },
                })}
              />
              <Star fill={i < value ? 1 : 0} />
            </Rate>
          ))}
        </StyledRatingInput>
      </RatingInputWrapper>
      {errors && errors[name] && (
        <ErrorMessage>{errors[name].message}</ErrorMessage>
      )}
    </Rating>
  )
}
