import EventType from '../eventType'

export default class SnapchatPixel {
  [EventType.PAGE_VIEW] = properties => {
    if (window.snaptr) window.snaptr('track', 'PAGE_VIEW')
  };

  [EventType.ADD_TO_CART] = properties => {
    if (window.snaptr)
      window.snaptr('track', 'ADD_CART', {
        item_category: properties.productId,
        item_ids: [properties.variantId],
        item_type: 'product_group',
        price: formatRevenue(properties.price),
        currency: 'USD',
        number_items: properties.quantity || 1,
        description: properties.productTitle + ' - ' + properties.variantTitle,
      })
  };

  [EventType.PRODUCT_VIEW] = properties => {
    if (window.snaptr)
      window.snaptr('track', 'VIEW_CONTENT', {
        item_category: properties.productId,
        item_ids: [properties.variantId],
        item_type: 'product_group',
        price: formatRevenue(properties.price),
        currency: 'USD',
        description: properties.productTitle + ' - ' + properties.variantTitle,
      })
  };

  [EventType.SEARCH] = properties => {
    if (window.snaptr)
      window.snaptr('track', 'SEARCH', {
        search_string: properties.searchQuery,
      })
  }
}

const formatRevenue = price => {
  if (price != null) {
    return price
  }
  return 0
}
