import React from 'react'
import styled from 'styled-components'
import { useStaticQuery, graphql } from 'gatsby'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

// Styles
import { desktopVw } from '../styles/utils'

import { getLinkPath } from '../utils'

// Components
import RichText from './RichText'
import MenuItem from './MenuItem'
import LogoIcon from '../assets/logo.svg'
import TextButton from './TextButton'
import Banner from './Banner'
import TrackableAnchor from './TrackableAnchor'

const StyledFooter = styled.footer`
  margin-top: 31px;
  padding: 0 21px 60px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    margin-top: ${desktopVw(32)};
    padding: 0 ${desktopVw(40)} ${desktopVw(125)};
    overflow: hidden;
  }
`

const Main = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 0 ${desktopVw(40)};
    display: flex;
    border-bottom: 1px solid ${props => props.theme.colors.greyLighter};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
  }
`
const FooterTextMobile = styled.section`
  white-space: pre-wrap;
  padding-bottom: 29px;
  border-bottom: 1px solid ${props => props.theme.colors.greyLighter};

  h1,
  h2,
  h3,
  h4 {
    font-family: ${props => props.theme.fonts.primary};
    font-size: 19px;
    line-height: 25px;
    font-weight: 400;
    margin-bottom: 24px;
  }

  p {
    font-size: 10px;
    line-height: 15px;
    text-transform: uppercase;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const FooterText = styled.section`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    width: 50%;
    padding: 0 ${desktopVw(40)};
    box-sizing: border-box;
    white-space: pre-wrap;

    h1,
    h2,
    h3,
    h4 {
      font-family: ${props => props.theme.fonts.primary};
      font-weight: 400;
      font-size: ${desktopVw(19)};
      line-height: ${desktopVw(25)};
      margin-bottom: ${desktopVw(14)};
    }

    p {
      font-size: ${desktopVw(10)};
      line-height: ${desktopVw(20)};
      text-transform: uppercase;
    }
  }
`

const FooterMenu = styled.nav`
  margin: 27px 0 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 50%;
    margin: ${desktopVw(22)} 0 0;
  }
`

const MenuMobile = styled.ul`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const MenuDesktop = styled.div`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
  }
`

const MenuColumnDesktop = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(126)};

    &:last-child {
      margin: 0;
    }
  }
`

const MenuTitleDesktop = styled.h4`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(25)};
    font-size: ${desktopVw(12)};
    font-family: ${props => props.theme.fonts.primary};
    text-transform: uppercase;
  }
`

const MenuItemDesktop = styled.li`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
    align-items: center;
    margin-bottom: ${desktopVw(10)};

    &:last-child {
      margin: 0;
    }
  }
`

const MenuLinkDesktop = styled(TextButton)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: 10px;
    text-transform: uppercase;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktop}px) {
    font-size: ${desktopVw(10)};
  }
`

const StyledMenuItem = styled(MenuItem)`
  button,
  a {
    text-transform: uppercase;
  }

  .toplevel-btn,
  .toplevel-link {
    font-size: 12px;
  }

  .submenu {
    padding: 0;
  }

  a {
    font-size: 11px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    button,
    a {
      color: ${props => props.theme.colors.primary};
    }
  }
`

const Social = styled.div`
  display: flex;
  align-items: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: absolute;
    right: ${desktopVw(80)};
    bottom: ${desktopVw(206)};
  }
`

const FollowUs = styled.p`
  margin-right: 25px;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const Logo = styled(LogoIcon)`
  width: 109px;
  fill: ${props => props.theme.colors.primary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const SocialLink = styled(TrackableAnchor)`
  margin-right: 17px;

  &:last-child {
    margin-right: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(10)};
  }
`

const SocialIcon = styled.img`
  height: 20px;
  width: auto;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(15)};
  }
`

const LogoWrapper = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
`

const Legal = styled.ul`
  text-align: right;
  max-width: 190px;
  margin: 0 0 0 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const LegalDesktop = styled.ul`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    position: absolute;
    left: ${desktopVw(80)};
    bottom: ${desktopVw(85)};
  }
`

const Copyright = styled.span`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    display: inline;
    text-transform: uppercase;
    font-size: ${desktopVw(10)};
    margin-right: ${desktopVw(18)};
    color: ${props => props.theme.colors.greyLight};
  }
`

const LegalItem = styled.li`
  display: inline-flex;
  align-items: center;
  margin: 0;

  &::after {
    content: '|';
    font-size: 10px;
    margin: 0 6px;
    display: inline;
    color: ${props => props.theme.colors.greyLight};
  }

  &:last-child {
    &::after {
      content: '';
      display: none;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;

    &::after {
      font-size: ${desktopVw(10)};
      margin: 0 ${desktopVw(6)};
    }
  }
`

const LegalLink = styled(TextButton)`
  text-transform: uppercase;
  font-size: 10px;
  color: ${props => props.theme.colors.greyLight};

  background-image: ${props =>
    `linear-gradient(${props.theme.colors.greyLight}, ${props.theme.colors.greyLight});`};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const Footer = ({ subscribeBannerCardColor }) => {
  const { contentfulSite } = useStaticQuery(graphql`
    query FooterQuery {
      contentfulSite {
        footerSubscribeBanner {
          __typename
          id
          image {
            description
            title
            fluid(maxWidth: 1024) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          imageDesktop {
            description
            title
            fluid(maxWidth: 3840) {
              ...GatsbyContentfulFluid_withWebp
            }
          }
          card {
            title {
              title
            }
            subtitle
            text {
              json
            }
            button {
              text
            }
            subscribeForm {
              klaviyoListId
              successMessage {
                json
              }
            }
          }
        }
        footerTextMobile {
          json
        }
        footerText {
          json
        }
        footerMenu {
          id
          link {
            ...Link
          }
          submenuColumnOne {
            ...Card
          }
        }
        footerFollowUs
        footerSocial {
          id
          images {
            id
            title
            description
            file {
              url
            }
          }
          link {
            ...Link
          }
        }
        copyright
        legal {
          ...Link
        }
      }
    }
  `)
  return (
    <>
      <Banner
        image={contentfulSite.footerSubscribeBanner.image}
        imageDesktop={contentfulSite.footerSubscribeBanner.imageDesktop}
        card={contentfulSite.footerSubscribeBanner.card}
        cardColor={subscribeBannerCardColor && subscribeBannerCardColor.hexcode}
      />
      <StyledFooter>
        <Main>
          <FooterTextMobile>
            <RichText json={contentfulSite.footerTextMobile.json} />
          </FooterTextMobile>
          <FooterText>
            <RichText json={contentfulSite.footerText.json} />
          </FooterText>
          <FooterMenu>
            <MenuMobile>
              {contentfulSite.footerMenu.map(
                ({ id, link, submenuColumnOne }) => (
                  <StyledMenuItem
                    key={link.id}
                    id={id}
                    link={link}
                    submenuColumnOne={submenuColumnOne}
                    isFooterMenu
                  />
                ),
              )}
            </MenuMobile>
            <MenuDesktop>
              {contentfulSite.footerMenu.map(
                ({ id, link, submenuColumnOne }) => (
                  <MenuColumnDesktop key={id}>
                    <MenuTitleDesktop>{link.text}</MenuTitleDesktop>
                    {submenuColumnOne.map(it => (
                      <ul>
                        {(it.links ?? []).map(subLink => (
                          <MenuItemDesktop key={subLink.id}>
                            <MenuLinkDesktop
                              to={
                                subLink?.url ? undefined : getLinkPath(subLink)
                              }
                              trackParams={{
                                trackGA: true,
                                trackInternal: true,
                                category: 'navigation',
                                action: 'click_footer_link',
                                label: subLink.text,
                                value: getLinkPath(subLink),
                              }}
                              href={subLink?.url}
                            >
                              {subLink.text}
                            </MenuLinkDesktop>
                          </MenuItemDesktop>
                        ))}
                      </ul>
                    ))}
                  </MenuColumnDesktop>
                ),
              )}
            </MenuDesktop>
            <LegalDesktop>
              <Copyright>{contentfulSite.copyright}</Copyright>
              {contentfulSite.legal.map(link => (
                <LegalItem key={link.id}>
                  <LegalLink to={getLinkPath(link)}>{link.text}</LegalLink>
                </LegalItem>
              ))}
            </LegalDesktop>
            <Social>
              <FollowUs>{contentfulSite.footerFollowUs}</FollowUs>
              {contentfulSite.footerSocial.map(({ id, images, link }) => (
                <SocialLink
                  key={id}
                  href={link.url}
                  target='_blank'
                  aria-label={link.text}
                  trackParams={{
                    trackGA: true,
                    trackInternal: true,
                    category: 'social',
                    action: 'click_social_link',
                    label: link.text,
                    value: link.url,
                  }}
                >
                  <SocialIcon
                    src={images[0].file.url}
                    alt={images[0].description || images[0].title}
                  />
                </SocialLink>
              ))}
            </Social>
          </FooterMenu>
        </Main>
        <LogoWrapper>
          <Logo />
          <Legal>
            {contentfulSite.legal.slice(0, 2).map(link => (
              <LegalItem key={link.id}>
                <LegalLink to={getLinkPath(link)}>{link.text}</LegalLink>
              </LegalItem>
            ))}
          </Legal>
        </LogoWrapper>
      </StyledFooter>
    </>
  )
}

export default Footer
