import { useState, useContext, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { StoreContext } from '../contexts'
import queryProductByHandle from '../shopify/queryProductByHandle'
import { captureException } from '../components/Sentry/utils'

/**
 *
 * @param {string} handle
 * @param {string} [store]
 *
 * @return {{product: Object, loading: boolean, error: Error}}
 */
const useShopifyProduct = (
  handle,
  storeSubdomain,
  notFoundInDataCache = true,
) => {
  const [state, setState] = useState({
    product: null,
    loading: false,
    error: handle ? null : new Error('"handle" is missing.'),
  })

  const stores = useContext(StoreContext)
  const activeStoreSubdomain = useSelector(
    ({ storeSubdomain }) => storeSubdomain,
  )
  const { client } = stores[storeSubdomain || activeStoreSubdomain]

  useEffect(() => {
    if (handle && notFoundInDataCache) {
      const queryShopifyProduct = async () => {
        setState({ product: null, loading: true, error: null })

        try {
          const {
            data: { product },
          } = await queryProductByHandle(client, {
            handle,
          })
          setState({ product, loading: false, error: null })
        } catch (error) {
          if ((error || '').toLowerCase() !== 'cancelled') {
            captureException(
              `query ShopifyProduct for ${handle} failed with the following error: ${error}`,
              { handle },
            )
          }
          setState({ product: null, loading: false, error })
        }
      }

      queryShopifyProduct()
    }
  }, [handle, client, notFoundInDataCache])

  return state
}

export default useShopifyProduct
