import * as Sentry from '@sentry/browser'
import { getCookie } from '../../utils'
import { ANALYTIC_COOKIES } from '../../utils/constants'

export function configureScope(errorInfo, additionalTags) {
  Sentry.configureScope(scope => {
    Object.keys(errorInfo || {}).forEach(key => {
      scope.setExtra(key, errorInfo[key])
    })

    Object.keys(additionalTags || {}).forEach(tagKey => {
      scope.setTag(tagKey, additionalTags[tagKey])
    })

    scope.setUser({
      email:
        getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
        getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL),
    })
    scope.setTag('lb_user_id', getCookie(ANALYTIC_COOKIES.LB_USER_ID))
  })
}

export function captureFatalException(error, errorInfo) {
  configureScope(errorInfo)
  Sentry.captureException(error, { level: Sentry.Severity.Fatal })
}

export function captureException(error, errorInfo, tags) {
  configureScope(errorInfo, tags)
  Sentry.captureException(error)
}

export function captureWarning(warning, info, tags) {
  configureScope(info, tags)
  Sentry.captureMessage(warning)
}

export function captureEvent(event, info) {
  configureScope(info)
  Sentry.captureEvent(event)
}
