import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'
import { useDispatch } from 'react-redux'
import { setMobileMenuOpen } from '../../actions'

import { desktopVw } from '../../styles/utils'
import { getPathName } from '../../utils'

import SearchIcon from '../../assets/search.svg'

const StyledSearchIcon = styled(SearchIcon)`
  width: 17px;
  fill: ${props => props.theme.colors.primary} !important;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(16)};
  }
`

const SearchLink = () => {
  const dispatch = useDispatch()
  return (
    <Link
      to={getPathName('search')}
      aria-label='Search'
      onClick={() => dispatch(setMobileMenuOpen(false))}
    >
      <StyledSearchIcon />
    </Link>
  )
}

export default SearchLink
