import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import { Radio } from './Radio'
import { ErrorMessage } from './'

const Fieldset = styled.div`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex-direction: row;
    align-items: center;
  }
`

const Title = styled.label`
  font-size: 16px;
  line-height: 20px;
  margin: 0 0 20px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
    line-height: ${desktopVw(20)};
    margin: 0 ${desktopVw(18)} 0 0;
  }
`

const Label = styled.label`
  display: flex;
  align-items: center;
  padding-right: 35px;
  font-size: 11px;
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-right: ${desktopVw(19)};
    font-size: ${desktopVw(11)};
  }
`

const RadioContainer = styled.div`
  display: flex;
`

const StyledRadio = styled(Radio)`
  margin-right: 4px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(9)};
  }
`

export const RadioGroup = ({
  name,
  register,
  errors,
  label,
  options,
  required = true,
}) => {
  return (
    <>
      <Fieldset>
        <Title>{label}</Title>
        <RadioContainer>
          {options.map((option, i) => (
            <Label key={i}>
              <StyledRadio
                value={option.value}
                name={name}
                register={register}
                error={errors[name]}
                required={required}
              />
              {option.name}
            </Label>
          ))}
        </RadioContainer>
      </Fieldset>
      {errors && errors[name] && (
        <ErrorMessage>{errors[name].message}</ErrorMessage>
      )}
    </>
  )
}
