import React from 'react'
import styled from 'styled-components'
import Image from 'gatsby-image'
// Styles
import { desktopVw } from '../styles/utils'
import { colors } from '../styles/theme'

const StyledColorOption = styled.div`
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: var(--background);
  border: 1px solid var(--borderColor);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(10)};
    height: ${desktopVw(10)};
  }
`

const TextureOption = styled.div`
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: white;
  border: 1px solid var(--borderColor);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(10)};
    height: ${desktopVw(10)};
  }
`

const StyledImage = styled(Image)`
  position: var(--position) !important;
`

const ColorOption = ({
  className,
  style,
  name,
  color,
  texture,
  addBorder,
  isPositionInitial = false,
}) => {
  return (
    <>
      {texture ? (
        <TextureOption
          className={className}
          style={{
            ...(style || {}),
            '--borderColor': addBorder
              ? colors.greyLighter
              : color || colors.greyLighter,
          }}
          name={name}
        >
          <StyledImage
            fluid={texture.fluid}
            alt={texture.description}
            imgStyle={{ objectFit: 'cover' }}
            style={{ '--position': isPositionInitial ? 'initial' : 'relative' }}
          />
        </TextureOption>
      ) : (
        <StyledColorOption
          className={className}
          style={{
            ...(style || {}),
            '--background': color,
            '--borderColor': addBorder
              ? colors.greyLighter
              : color || colors.greyLighter,
          }}
          name={name}
        />
      )}
    </>
  )
}

export default ColorOption
