import React, { useState, useEffect } from 'react'
import styled from 'styled-components'
import { useDispatch, useSelector } from 'react-redux'
import { setCartOpen } from '../../actions'

import { desktopVw } from '../../styles/utils'

import CartIcon from '../../assets/cart.svg'
import TrackableButton from '../TrackableButton'

const Button = styled(TrackableButton)`
  display: flex;
  align-items: center;
  border-radius: 13.5px;
  padding: 6px 10px;
  background-color: ${props => props.theme.colors.green};
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(6)} ${desktopVw(14)};
    border-radius: ${desktopVw(13.5)};
    fill: ${props => props.theme.colors.secondary};
  }
`

const StyledCartIcon = styled(CartIcon)`
  width: 12px;
  fill: ${props => props.theme.colors.secondary} !important;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(12)};
  }
`

const CartNumber = styled.span`
  color: ${props => props.theme.colors.secondary};
  font-size: 10px;
  margin-left: 4px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-left: ${desktopVw(4)};
  }
`

const CartButton = () => {
  const [amount, setAmount] = useState(0)
  const dispatch = useDispatch()
  let checkout = {}
  const windowGlobal = typeof window !== 'undefined' && window
  if (windowGlobal) {
    checkout = JSON.parse(
      windowGlobal.localStorage.getItem('onequince::checkout::object'),
    )
  }

  const { lineItems: { edges: cartLineItems = [] } = [] } = { ...checkout }

  useEffect(() => {
    if (cartLineItems) {
      const cartAmount = cartLineItems.reduce(
        (acc, { node }) => acc + node.quantity,
        0,
      )
      setAmount(cartAmount)
    }
  }, [cartLineItems])

  return (
    <Button
      type='button'
      onClick={() => dispatch(setCartOpen(true))}
      aria-label='Cart'
      trackParams={{
        trackGA: true,
        category: 'checkout',
        action: 'open_cart',
        trackInternal: true,
      }}
    >
      <StyledCartIcon />
      {amount ? <CartNumber>{amount > 0 && amount}</CartNumber> : ''}
    </Button>
  )
}
export default CartButton
