import React from 'react'

import LineItem from './LineItem'

const LineItems = React.forwardRef(({ lineItems }, lastLineItemRef) => {
  return (
    <ul>
      {lineItems.map(({ node }, i) => (
        <LineItem
          key={`${node.variant.id}${i}`}
          lineItem={node}
          ref={i === lineItems.length - 1 ? lastLineItemRef : undefined}
        />
      ))}
    </ul>
  )
})

export default LineItems
