import { captureException } from '../components/Sentry/utils'

const customerEndpoint = process.env.GATSBY_LASTBRAND_CUSTOMER_ENDPOINT

const useCustomerService = () => {
  const checkOldUser = async email => {
    const options = {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
    }
    const urlParams = `isOldUser?email=${email}`
    try {
      const response = await fetch(customerEndpoint + urlParams, options)
      return { response: await response.text(), error: null, loading: false }
    } catch (error) {
      captureException(new Error(`Error in Fetching customer state: ${email}`))
      return { response: null, error, loading: false }
    }
  }

  return { checkOldUser }
}

export default useCustomerService
