const mobile = 375
const tablet = 768
const desktop = 1440

const mobileH = 812
const desktopH = 900

export const mobileVw = px => `${((px / mobile) * 100).toFixed(3)}vw`
export const tabletVw = px => `${((px / tablet) * 100).toFixed(3)}vw`
export const desktopVw = px => `${((px / desktop) * 100).toFixed(3)}vw`

export const mobileVh = (px, shouldCalculateUsingInnerHeight = false) =>
  shouldCalculateUsingInnerHeight && typeof window !== 'undefined'
    ? // eslint-disable-next-line no-undef
      `${((px / mobileH) * window.innerHeight).toFixed(3)}px`
    : `${((px / mobileH) * 100).toFixed(3)}vh`

export const desktopVh = (px, shouldCalculateUsingInnerHeight = false) =>
  shouldCalculateUsingInnerHeight && typeof window !== 'undefined'
    ? // eslint-disable-next-line no-undef
      `${((px / desktopH) * window.innerHeight).toFixed(3)}px`
    : `${((px / desktopH) * 100).toFixed(3)}vh`

export const mobileVwInt = px => ((px / mobile) * 100).toFixed(3)
export const desktopVwInt = px => ((px / desktop) * 100).toFixed(3)

