import React from 'react'
import styled from 'styled-components'
import { useSpring, animated } from 'react-spring'
import { useSelector } from 'react-redux'

import MenuBar from '../MenuBar'
import Nav from './Nav'

const StyledAsideMenu = styled(animated.div)`
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: ${props =>
    props.backgroundColor
      ? props.backgroundColor
      : props.theme.colors.secondary};
  will-change: transform;
  -ms-overflow-style: none;
  transform: translate3d(0, 0, 0);

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const StyledMenuBar = styled(MenuBar)`
  svg {
    fill: ${props => props.theme.colors.primary};
  }

  a,
  button {
    color: ${props => props.theme.colors.primary};
  }

  .hamburger-bar {
      background: ${props => props.theme.colors.primary};
    }
  }
`

const AsideMenu = ({ menuPrimary, menuSecondary, backgroundColor }) => {
  return (
    <StyledAsideMenu backgroundColor={backgroundColor}>
      <StyledMenuBar />
      <Nav menuPrimary={menuPrimary} menuSecondary={menuSecondary} />
    </StyledAsideMenu>
  )
}

export default AsideMenu
