import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

const StyledCheckboxOption = styled.div`
  position: relative;
  display: flex;
`

const CheckboxContainer = styled.div`
  position: relative;
  flex-shrink: 0;
  margin: 4px 7px 0 0;
  width: 13px;
  height: 13px;
  background: ${({ theme, disabled }) =>
    disabled ? theme.colors.silver : theme.colors.secondary};
  border: 1px solid ${props => props.theme.colors.greyLight};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(3)} ${desktopVw(7)} 0 0;
    width: ${desktopVw(13)};
    height: ${desktopVw(13)};
  }
`

const Checkmark = styled.span`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  &::after {
    content: '';
    position: absolute;
    display: none;
    left: 4px;
    top: 0px;
    width: 3px;
    height: 7px;
    border: solid white;
    border-width: 0 3px 3px 0;
    transform: rotate(45deg);
  }

  background-color: ${props =>
    !props.showCheckmark && props.checkedColor ? props.checkedColor : 'unset'};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    &::after {
      left: ${desktopVw(4)};
      top: ${desktopVw(0)};
      width: ${desktopVw(3)};
      height: ${desktopVw(7)};
      border-width: 0 ${desktopVw(3)} ${desktopVw(3)} 0;
    }
  }
`

const Checkbox = styled.input`
  position: absolute;
  opacity: 0;
  z-index: 100;
  cursor: ${({ disabled }) => (disabled ? 'not-allowed' : 'pointer')};
  width: 100%;
  height: 100%;
  margin: 0;

  &:checked {
    + ${Checkmark} {
      background-color: ${props =>
        props.checkedColor || props.theme.colors.greyLight};

      &:after {
        display: ${props => (props.showCheckmark ? 'block' : 'none')};
      }
    }
    border: ${props =>
      props.showBordersWhenChecked
        ? `1px solid ${props.checkedColor}`
        : 'unset'};
  }
`

const Label = styled.label`
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 20px;
  color: ${props => props.theme.colors.greyLight};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    line-height: ${desktopVw(20)};
  }
`

export const CheckboxOption = ({
  label,
  name,
  id,
  value,
  checked,
  onChange,
  onClick,
  className,
  checkedColor,
  style,
  disabled = false,
  showCheckmark = true,
  showBordersWhenChecked = false,
}) => {
  return (
    <StyledCheckboxOption className={className} style={style}>
      <CheckboxContainer
        className='checkbox_option__container'
        disabled={disabled}
      >
        <Checkbox
          value={value}
          name={name}
          id={id}
          type='checkbox'
          onChange={onChange}
          checked={checked}
          checkedColor={checkedColor}
          onClick={() => onClick && onClick()}
          disabled={disabled}
          showCheckmark={showCheckmark}
          showBordersWhenChecked={showBordersWhenChecked}
        />
        <Checkmark checkedColor={checkedColor} showCheckmark={showCheckmark} />
      </CheckboxContainer>
      <Label htmlFor={id}>{label}</Label>
    </StyledCheckboxOption>
  )
}
