import React, { useState, useEffect } from 'react'

import SubscribeForm from './SubscribeForm'
import useFetch from '../hooks/useFetch'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const Klaviyo = ({ klaviyoListId, successMessage }) => {
  const url = process.env.GATSBY_LASTBRAND_LIST_ENDPOINT

  const { fetchData, response, error, loading } = useFetch()

  const [errorMsg, setErrorMsg] = useState('')

  const [showSuccessMsg, setShowSuccessMsg] = useState(false)
  const [email, setEmail] = useState('')

  const body = {
    klaviyoListId,
    email,
  }

  const options = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  }

  const handleSubmit = e => {
    e.preventDefault()
    setErrorMsg('')
    setShowSuccessMsg(false)
    fetchData(url, options)
  }

  useEffect(() => {
    if (error) {
      console.log(error)
      setErrorMsg('Oops something went wrong try again later')

      return
    }

    if (!response) return

    if (response.detail) {
      setErrorMsg(response.detail)

      return
    }

    setShowSuccessMsg(true)
    eventTracker.track(EventType.EMAIL_NEWSLETTER_SUBSCRIBE, {
      email,
    })
  }, [response, error])

  const onFocus = () => {
    setErrorMsg('')
  }

  return (
    <SubscribeForm
      email={email}
      handleChange={setEmail}
      onFocus={onFocus}
      handleSubmit={handleSubmit}
      success={showSuccessMsg}
      successMessage={successMessage}
      errorMsg={errorMsg}
      submitText='Join Us'
    />
  )
}

export default Klaviyo
