import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

const StyledTagSelection = styled.div``

const Title = styled.h4`
  margin: 0 0 10px;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 400;
  font-size: 12px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin: 0 0 ${desktopVw(11)};
  }
`

const CheckBoxGroup = styled.div`
  display: flex;
  flex-flow: wrap;
`

const Tag = styled.div`
  position: relative;
  margin: 0 0 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(10)} ${desktopVw(10)} 0;
  }
`

const Label = styled.label`
  display: block;
  border: 1px solid ${props => props.theme.colors.greyLight};
  border-radius: 40px;
  cursor: pointer;
  padding: 7px 12px;
  font-size: 12px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    border-radius: ${desktopVw(40)};
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(7)} ${desktopVw(12)};
  }
`

const CheckBox = styled.input`
  position: absolute;
  opacity: 0;

  &:checked + label {
    background: ${props => props.theme.colors.primary};
    color: ${props => props.theme.colors.secondary};
    border-color: ${props => props.theme.colors.primary};
  }
`

export const TagSelection = ({
  title,
  name,
  tags,
  onChange,
  active = [],
  required,
}) => {
  return (
    <StyledTagSelection>
      <Title>{title}</Title>
      <CheckBoxGroup>
        {tags.map((tag, i) => (
          <Tag key={`${tag}${i}`}>
            <CheckBox
              type='checkbox'
              id={`${tag}-${i}`}
              value={tag}
              name={name}
              defaultChecked={active.includes(tag)}
              onChange={onChange}
              {...(i === 0 ? { required } : {})}
            />
            <Label htmlFor={`${tag}-${i}`}>{tag}</Label>
          </Tag>
        ))}
      </CheckBoxGroup>
    </StyledTagSelection>
  )
}
