import {
  extractQueryParamFromPageLocation,
  getCookie,
  setCookie,
} from '../utils'

const clickIdUrlParameter = 'clickId'
const clickIdCookieName = 'pjn-click'
const lookBackPeriodInDays = 60

const maxCookieAge = lookBackPeriodInDays * 24 * 60

// Note: This is an alternate of pepperjam container tag, and doesn't really require any event logging.
export function initializePepperjamPixel() {
  // If click id is present in the url, then setting it in cookie.
  if (hasClickId()) {
    handleClickId(getClickIdValue())
  }
}

/**
 *Note: Below code copied from pepperjam container tag. https://container.pepperjam.com/1718103902.js
 *
 * Do not update it, only changes done are for cookie retrieval and set part.
 *
 * Note: Updated the logic a bit to match with container tag cookie format
 *
 */

const handleClickId = function(clickId) {
  if (clickId) {
    let clickData = getClickData(),
      today = getToday(),
      s = clickData,
      isClickIdPresent = false
    for (let n = 0; n < s.length; n++)
      if (clickId === s[n].id) {
        isClickIdPresent = true
        break
      }
    if (!isClickIdPresent) {
      clickData.push({
        id: clickId,
        days: today,
        type: 'p',
      })
      saveCookieData(JSON.stringify(clickData))
    }
  }
}

const saveCookieData = stringCookieList => {
  setCookie(clickIdCookieName, stringCookieList, maxCookieAge)
}

const getClickData = () => {
  let t = [],
    e = getCookieData()
  if (e) {
    let a = JSON.parse(e),
      r = getToday()
    a.forEach(function(e) {
      clickWithinLookback(e, r) && t.push(e)
    })
  }
  return t
}

const getCookieData = () => {
  return getCookie(clickIdCookieName)
}

const clickWithinLookback = (t, e) => {
  return Math.abs(e - t.days) <= lookBackPeriodInDays
}

const getToday = () => {
  let t = Date.now()
  return (t = Math.ceil(t / 864e5))
}

/**
 * Returns true if the querystring contains a click ID
 */
const hasClickId = () => {
  return !!extractQueryParamFromPageLocation(clickIdUrlParameter)
}

/**
 * Returns the clickId value
 */
const getClickIdValue = () => {
  return extractQueryParamFromPageLocation(clickIdUrlParameter)
}
