import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import { Radio } from './'

const StyledRadioOption = styled.div`
  display: flex;
  align-items: ${props => (props.alignCenter ? 'center' : null)};
`

const StyledRadio = styled(Radio)`
  margin: 0 12px 0 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 ${desktopVw(12)} 0 0;
    width: ${desktopVw(20)};
    height: ${desktopVw(20)};
  }
`

const Label = styled.label`
  display: flex;
  flex-direction: column;
  font-size: 15px;
  line-height: 1;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(15)};
  }
`

const SubLabel = styled.div`
  color: ${props => props.theme.colors.greyLight};
  font-size: 12px;
  margin-top: 6px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    margin-top: ${desktopVw(6)};
  }
`

export const RadioOption = ({
  label,
  subLabel,
  name,
  id,
  value,
  active,
  onChange,
  onClick,
  disabled,
}) => {
  return (
    <StyledRadioOption alignCenter={!subLabel ? 1 : 0}>
      <StyledRadio
        value={value}
        name={name}
        id={id}
        onChange={onChange}
        checked={value === active}
        onClick={() => onClick && onClick()}
        disabled={disabled}
      />
      <Label htmlFor={id}>
        {label}
        {subLabel && <SubLabel>{subLabel}</SubLabel>}
      </Label>
    </StyledRadioOption>
  )
}
