import React from 'react'
import styled, { css } from 'styled-components'

const plusButtonStyles = css`
  position: absolute;
  background: ${props => props.theme.colors.primary};
`

const StyledPlusButton = styled.div`
  position: relative;
  width: 8px;
  height: 8px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const VerticalBar = styled.div`
  ${plusButtonStyles}
  right: 50%;
  transform: ${props =>
    props.active ? `translateX(50%) scaleY(0)` : `translateX(50%) scaleY(1)`};
  height: 8px;
  width: 2px;
  transition: transform 250ms ease-out;
  transform-origin: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 11px;
  }
`

const HorizontalBar = styled.div`
  ${plusButtonStyles}
  top: 50%;
  height: 2px;
  width: 8px;
  transform: ${props =>
    props.active
      ? `translateY(-50%) scaleX(2.62)`
      : `translateY(-50%) scaleX(1)`};
  transition-delay: 250ms;
  transition: transform 350ms ease-out;
  transform-origin: right;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: 11px;
  }
`

const PlusButton = ({ className, active }) => (
  <StyledPlusButton className={`${className} plus-btn`}>
    <VerticalBar active={active} />
    <HorizontalBar className='plus-btn__horizontal-bar' active={active} />
  </StyledPlusButton>
)

export default PlusButton
