import React from 'react'
import styled, { css } from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../styles/utils'

const sharedFontStyles = css`
  font-family: ${props => props.theme.fonts.heading};
  font-size: 16px;
  color: ${props => props.theme.colors.secondary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-weight: 400;
    font-size: ${desktopVw(11)};
  }
`

const sharedBoxStyles = css`
  background: transparent;
  margin: 0;
  border: 1px solid white;
  -webkit-appearance: none;
  border-radius: 0;
  box-sizing: border-box;
`

const SubscribeFormContainer = styled.div``

const Form = styled.form`
  display: flex;
  margin-top: 18px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(12)};
  }
`

const EmailInput = styled.input`
  ${sharedFontStyles}
  ${sharedBoxStyles}
  width: 100%;
  flex: 1;
  padding: 20px 13px;

  &::placeholder {
    color: white;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(22)} ${desktopVw(5)} ${desktopVw(22)} ${desktopVw(20)};
  }
`

const SubmitBtn = styled.button`
  ${sharedFontStyles}
  ${sharedBoxStyles}

  padding: 20px 13px;
  border-left: none;

  white-space: nowrap;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(22)} ${desktopVw(16)};
  }
`

const SuccessMsg = styled.div`
  margin: 10px 0 0;
  ${sharedFontStyles};
  white-space: pre-wrap;

  p {
    margin-bottom: 0 !important;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(10)} 0 0;
  }
`

const SuccessMsgText = styled(SuccessMsg)`
  font-weight: 400;
`

const ErrorMsg = styled.p`
  ${sharedBoxStyles}
  font-family: ${props => props.theme.fonts.primary};
  font-size: 11px;
  color: ${props => props.theme.colors.red};
  padding: 10px 5px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(11)};
  }
`
const LoadingMsg = styled(ErrorMsg)`
  color: ${props => props.theme.colors.secondary};
`

const SubscribeForm = ({
  className,
  email,
  handleChange,
  onFocus,
  submitText,
  handleSubmit,
  success,
  successMessage, // Json
  errorMsg,
  loadingMsg = null,
  title = 'Add your email to subscribe',
  placeholder = 'Your e-mail',
  type = 'email',
  successMessageText = null,
  onBlur = () => {},
}) => {
  return (
    <SubscribeFormContainer className={className}>
      <Form className='subscribeform__form'>
        <EmailInput
          className='subscribeform__input'
          title={title}
          placeholder={placeholder}
          type={type}
          value={email}
          onChange={e => handleChange(e.target.value)}
          onFocus={() => onFocus()}
          required
          onBlur={onBlur}
        />
        <SubmitBtn
          className='subscribeform__submit'
          type='submit'
          onClick={e => handleSubmit(e)}
        >
          {submitText || 'Join us'}
        </SubmitBtn>
      </Form>
      {success && successMessage && (
        <SuccessMsg className='subscribeform__success-msg'>
          {documentToReactComponents(successMessage)}
        </SuccessMsg>
      )}
      {success && successMessageText && (
        <SuccessMsgText className='subscribeform__success-msg'>
          {successMessageText}
        </SuccessMsgText>
      )}
      {errorMsg && (
        <ErrorMsg className='subscribeform__error-msg' role='alert'>
          {errorMsg}
        </ErrorMsg>
      )}
      {loadingMsg && (
        <LoadingMsg className='subscribeform__loading-msg'>
          {loadingMsg}
        </LoadingMsg>
      )}
    </SubscribeFormContainer>
  )
}

export default SubscribeForm
