import React from 'react'
import styled, { keyframes } from 'styled-components'

import { desktopVw } from '../styles/utils'

const rotate = keyframes`

 0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); }
 `

const LoaderWrapper = styled.div`
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`

const Loader = styled.div`
  height: 24px;
  width: 24px;
  color: ${props => props.theme.colors.greyLighter};
  display: inline-block;
  position: relative;
  border: 3px solid;
  border-radius: 50%;
  border-right-color: ${props => props.theme.colors.greyLight};
  animation: ${rotate} 1s linear infinite;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(24)};
    border-width: ${desktopVw(3)};
    width: ${desktopVw(24)};
  }
`

const SpinningLoader = ({ className }) => {
  return (
    <LoaderWrapper className={className}>
      <Loader />
    </LoaderWrapper>
  )
}

export default SpinningLoader
