import React from 'react'
import styled, { css } from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'

import { toggleMobileMenu, setCartOpen } from '../../../actions'

const styles = css`
  position: absolute;
  height: 2px;
  width: 100%;
  transition: transform 500ms ease;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  transform-origin: left;
  background: ${props =>
    props.active ? props.theme.colors.primary : props.theme.colors.secondary};
  background-color: ${props => props.theme.colors.primary} !important;
`

const TopBar = styled.div`
  ${styles}
  top: 0;
  transform: ${props =>
    props.active ? `translateY(-1px) rotate(45deg)` : `rotate(0)`};
`

const MiddleBar = styled.div`
  ${styles}
  top: 50%;
  transform: translateY(-50%);
  opacity: ${props => (props.active ? '0' : '1')};
  transition: opacity 10ms ease;
`

const BottomBar = styled.div`
  ${styles}
  bottom: 0;
  transform: ${props => (props.active ? `rotate(-45deg)` : ` rotate(0)`)};
`

const StyledHamburger = styled.button`
  width: 21px;
  height: 16px;
  position: relative;
  padding: 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const Hamburger = ({ className }) => {
  const { cartOpen, mobileMenuOpen } = useSelector(state => state.ui)

  const dispatch = useDispatch()

  return (
    <StyledHamburger
      className={className}
      onClick={() => {
        dispatch(toggleMobileMenu())

        if (cartOpen) {
          dispatch(setCartOpen(false))
        }
      }}
      active={mobileMenuOpen}
      aria-label={mobileMenuOpen ? 'close menu' : 'open menu'}
      aria-expanded={mobileMenuOpen ? 'true' : 'false'}
    >
      <TopBar className='hamburger-bar' active={mobileMenuOpen} />
      <MiddleBar className='hamburger-bar' active={mobileMenuOpen} />
      <BottomBar className='hamburger-bar' active={mobileMenuOpen} />
    </StyledHamburger>
  )
}

export default Hamburger
