import { extractProductJsonFromAPIProduct } from '../utils/build'
import {
  decodeIdFromGraphQLResponse,
  getCookie,
  getPathParams,
  getUserBrowser,
  getUserDeviceCategory,
  getUserTrafficSource,
} from '../utils'
import { useDispatch } from 'react-redux'
import { replaceUpsellVariant } from '../actions'
import { captureException } from '../components/Sentry/utils'
import { ANALYTIC_COOKIES } from '../utils/constants'
import experimentManager from '../trackers/ExperimentManager'
import EventType from '../trackers/eventType'
import eventTracker from '../trackers/EventTracker'

const useReplaceUpsells = () => {
  const dispatch = useDispatch()

  const replaceUpsell = async ({ lineItems }) => {
    if (!lineItems) return

    const userId =
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL) ||
      getCookie(ANALYTIC_COOKIES.LB_USER_ID) ||
      ''

    const upsellCart = ((lineItems?.edges ?? lineItems) || []).map(lineItem => {
      const { customAttributes, variant, variantId } =
        lineItem?.node ?? lineItem
      const lineItemCreatedAtObj = (customAttributes || []).find(
        ca => ca.key === 'createdAt',
      )
      return {
        variantId: decodeIdFromGraphQLResponse(variant?.id ?? variantId),
        lineItemCreatedAt: lineItemCreatedAtObj
          ? Number(lineItemCreatedAtObj.value)
          : undefined,
      }
    })

    const requestParams = {
      limit: 1,
      device_category: getUserDeviceCategory(),
      userId: userId.toLowerCase(),
      browser: getUserBrowser(),
      trafficSource: getUserTrafficSource(),
      fetchPersonalized:
        experimentManager.getBucketForUser('personalized_upsell') !== 'control',
    }

    try {
      const url = `${
        process.env.GATSBY_QUINCE_STORE_FRONT_API
      }/get-upsell-variants${getPathParams(
        requestParams,
      )}&cart=${encodeURIComponent(JSON.stringify(upsellCart))}`

      dispatch(replaceUpsellVariant({ loading: true }))

      const upsells = await (
        await fetch(url, {
          method: 'GET',
        })
      ).json()

      if (upsells && upsells.length > 0) {
        let upsellObj = upsells[0]
        upsellObj = {
          ...upsellObj,
          product: extractProductJsonFromAPIProduct(upsellObj.product),
        }
        dispatch(replaceUpsellVariant({ loading: false, data: upsellObj }))
      } else dispatch(replaceUpsellVariant({ loading: false }))

      eventTracker.track(EventType.API_RESPONSE, {
        apiName: 'get-upsell-variants',
        inputRequest: { ...requestParams, cart: upsellCart },
        response: upsells.map(upsell => {
          return {
            color: upsell?.color,
            productId: decodeIdFromGraphQLResponse(
              upsell.product.shopifyProduct.id,
            ),
          }
        }),
      })
    } catch (e) {
      captureException(
        e,
        { cart: upsellCart },
        { type: 'API_FAILURE', API: 'GET_UPSELL_VARIANTS' },
      )
      dispatch(replaceUpsellVariant({ loading: false }))
    }
  }

  return { replaceUpsell }
}

export default useReplaceUpsells
