import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'
import { Radio } from './'

const Fieldset = styled.fieldset`
  border: 0;
  padding: 0;
  margin: 0;
`

const Legend = styled.legend`
  margin: 0 0 20px;
  padding: 0;
  font-size: 16px;
  line-height: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(15)};
    font-size: ${desktopVw(16)};
    line-height: ${desktopVw(20)};
  }
`

const Label = styled.label`
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const Rating = styled.div`
  font-size: 12px;
  text-transform: uppercase;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(5)} 0 0;
    font-size: ${desktopVw(11)};
  }
`

const ValContainer = styled.div`
  display: flex;
  justify-content: space-between;
  margin: 0 0 7px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(10)};
  }
`

const StartVal = styled.span``

const EndVal = styled.span``

const RadioContainer = styled.div`
  display: flex;
  justify-content: space-between;
`

const StyledRadio = styled(Radio)`
  margin: 0 0 8px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(5)};
    min-width: ${desktopVw(20)};
    min-height: ${desktopVw(20)};
  }
`

export const RadioSelectionRating = ({
  label,
  name,
  onChange,
  active,
  range,
  startVal,
  endVal,
  required = true,
}) => {
  return (
    <Fieldset>
      <Legend>{label}</Legend>

      <Rating>
        <ValContainer>
          <StartVal>{startVal}</StartVal>
          <EndVal>{endVal}</EndVal>
        </ValContainer>

        <RadioContainer>
          {[...Array(range)].map((_, i) => (
            <Label key={`${label}${i}`}>
              <StyledRadio
                value={i + 1}
                name={name}
                defaultChecked={i + 1 === parseInt(active)}
                onChange={onChange}
                {...(i === 0 ? { required } : {})}
              />
              {i + 1}
            </Label>
          ))}
        </RadioContainer>
      </Rating>
    </Fieldset>
  )
}
