import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

const CheckMark = styled.div`
  visibility: hidden;
  width: 50%;
  height: 50%;
  background: ${props => props.theme.colors.primary};
  border-radius: 50%;
  align-self: center;
  justify-self: center;
`

const StyledRadio = styled.div`
  position: relative;
  display: flex;
  justify-content: center;
  border: 1px solid;
  overflow: hidden;
  width: 20px;
  height: 20px;
  border-color: ${props => props.theme.colors.greyLight};
  border-radius: 50%;
  flex-shrink: 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(15)};
    height: ${desktopVw(15)};
  }
`

const Input = styled.input`
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;

  &:checked {
    border-color: ${props => props.theme.colors.primary};

    + ${CheckMark} {
      visibility: visible;
    }
  }
`

export const Radio = ({
  className,
  checked,
  id,
  type = 'radio',
  value,
  name,
  required,
  onChange,
  register,
  onClick,
  disabled,
}) => {
  return (
    <StyledRadio className={className}>
      <Input
        value={value}
        name={name}
        id={id}
        checked={checked}
        onChange={e => onChange && onChange(e)}
        type={type}
        ref={
          register &&
          register({
            required: { value: required, message: 'Please choose an option' },
          })
        }
        onClick={() => onClick && onClick()}
        disabled={disabled}
      />
      <CheckMark />
    </StyledRadio>
  )
}
