import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { Auth } from 'aws-amplify'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import { getCookie, validateEmail } from '../utils'

import { desktopVw } from '../styles/utils'

import ModalHeader from './ModalHeader'
import {
  BoxStyledErrorMessage,
  Container,
  Input,
  Password,
} from './FormElements'

import Button from './Button'

import Modal from './Modal'
import { StyledVerificationMessage } from './FormElements/StyledVerificationMessage'
import { captureWarning } from './Sentry/utils'
import { ANALYTIC_COOKIES } from '../utils/constants'

const StyledModal = styled(Modal)`
  overflow-y: scroll;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  padding: 25px 20px 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(53)} ${desktopVw(80)} ${desktopVw(77)};
    height: auto;
    width: ${desktopVw(600)};
    box-sizing: border-box;
  }
`

const StyledButton = styled(Button)`
  display: block;
  margin-top: 20px;
  width: 100%;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-top: ${desktopVw(30)};
  }
`

const Prompt = styled.div`
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(30)} 0 0;
  }
`

const Message = styled.p`
  text-align: center;
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 500;
  margin: 0 auto;
  font-size: 25px;
  line-height: 30px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(30)} auto;
    font-size: ${desktopVw(25)};
    line-height: ${desktopVw(30)};
    max-width: ${desktopVw(380)};
  }
`

const UnVerifiedMessage = styled(Button)`
  font-family: ${props => props.theme.fonts.primary};
  text-align: ${props => (props.alignCenter ? 'center' : null)};
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 11px;
  margin: 10px 0 0;
  margin-bottom: 0 !important;
  border: none;
  padding: 0px;
  transition: none;

  &:hover {
    background: inherit;
    border-color: inherit;
    color: inherit;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(10)} 0 0;
    font-size: ${desktopVw(11)};
  }
`

const PlaceholderText = styled.p`
  color: ${props => props.theme.colors.primary};
  font-size: 16px;
  margin-bottom: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    text-transform: uppercase;
    margin-bottom: ${desktopVw(10)};
  }
`

const ForgotPasswordModal = ({ open, onClose, username }) => {
  const [stage, setStage] = useState(0)
  const [email, setEmail] = useState('')
  const [code, setCode] = useState('')
  const [password, setPassword] = useState('')
  const [unverifiedAccount, setUnverifiedAccount] = useState(false)
  const [verificationEmailSent, setVerificationEmailSent] = useState(false)

  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')

  useEffect(() => {
    if (!username) return
    setEmail(username)
  }, [username])

  const resendVerificationEmail = async email => {
    try {
      await Auth.resendSignUp(email)
      eventTracker.trackClick(EventType.BUTTON_CLICK, {
        trackInternal: true,
        category: 'login',
        action: 'resend_verification_forgot_password',
        context: {
          login_email: email,
          lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
        },
      })
    } catch (err) {
      captureWarning(
        `Could not resend verification email for the user from forgot password flow - ${email}, ${err}`,
      )
    }
    setUnverifiedAccount(false)
    setVerificationEmailSent(true)
    setTimeout(() => setVerificationEmailSent(false), 10000)
  }

  const handleChange = () => {
    setError('')
  }

  const sendVerificationCode = async () => {
    setError('')

    if (!validateEmail(email)) {
      setError('Please enter a valid email address')
      return
    }

    setLoading(true)

    try {
      await Auth.forgotPassword(email)
      setLoading(false)
      setStage(1)
      eventTracker.trackClick(EventType.FORM_SUBMIT, {
        trackInternal: true,
        category: 'login',
        action: 'forgot_password',
        context: {
          login_email: email,
          lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
        },
      })
    } catch (error) {
      setError(error.message)
      if (error.name == 'UserNotFoundException') {
        setError(
          'Account does not exist with this email. Please  <a href="/sign-up" style="text-decoration:underline;font-weight: 500">create one</a>.',
        )
      } else if (error.name == 'InvalidParameterException') {
        // This error comes up when the account exists, but the email is not verified.
        try {
          setUnverifiedAccount(true)
          setError(
            'Looks like your account is not verified. Please check your inbox for a verification link to complete the registration.',
          )
        } catch (err) {
          setError(error.message)
          setLoading(false)
        }
      } else if (error.name == 'InternalErrorException') {
        setError('Something went wrong. Please try re-setting after sometime.')
      }
      setLoading(false)
    }
  }

  const submit = async () => {
    setError('')
    if (!code) {
      setError('Please enter your verification code')

      return
    }

    if (!password) {
      setError('Please enter a new password')

      return
    }

    setLoading(true)

    try {
      await Auth.forgotPasswordSubmit(email, code, password)
      setLoading(false)
      setStage(2)
    } catch (error) {
      setError(error.message)
      setLoading(false)
    }
  }

  return (
    <StyledModal open={open} onClose={onClose} showCloseBtn={stage < 2}>
      {stage < 2 && <ModalHeader title='Reset Password' />}
      {stage === 0 && (
        <>
          {error && (
            <BoxStyledErrorMessage
              dangerouslySetInnerHTML={{ __html: error }}
            />
          )}
          {unverifiedAccount && (
            <StyledVerificationMessage>
              cannot find verification mail?{' '}
              <UnVerifiedMessage
                onClick={async () => await resendVerificationEmail(email)}
                trackParams={{
                  trackInternal: true,
                  category: 'login',
                  action: 'click_reset_password_unverified',
                  context: {
                    login_email: email || '',
                    lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
                  },
                }}
              >
                resend one
              </UnVerifiedMessage>
            </StyledVerificationMessage>
          )}
          {verificationEmailSent && (
            <StyledVerificationMessage>
              Verification E-mail Sent!
            </StyledVerificationMessage>
          )}
          <Input
            type='email'
            placeholder='email'
            defaultValue={username || ''}
            onChange={e => handleChange(setEmail(e.target.value))}
          />
          <StyledButton
            black
            loading={loading}
            onClick={() => sendVerificationCode()}
            trackParams={{
              trackInternal: true,
              category: 'login',
              action: 'click_reset_password',
              context: {
                login_email: email || '',
                lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
              },
            }}
          >
            Send Confirmation
          </StyledButton>
        </>
      )}
      {stage === 1 && (
        <>
          <Container>
            <p>
              Please check your email ({email}) for the confirmation code (valid
              for 1 hour).
            </p>
          </Container>
          {error && (
            <BoxStyledErrorMessage
              dangerouslySetInnerHTML={{ __html: error }}
            />
          )}
          <Container>
            <PlaceholderText>Enter Code</PlaceholderText>
            <Input
              type='tel'
              onChange={e =>
                handleChange(setCode((e.target.value || '').trim()))
              }
            />
          </Container>
          <Container>
            <PlaceholderText>New Password</PlaceholderText>
            <Password
              onChange={e => handleChange(setPassword(e.target.value))}
            />
          </Container>
          <StyledButton
            black
            type='submit'
            loading={loading}
            onClick={() => submit()}
          >
            Confirm
          </StyledButton>
        </>
      )}
      {stage === 2 && (
        <Prompt>
          <Message>Your password has been reset.</Message>
          <StyledButton
            black
            onClick={() => onClose()}
            trackParams={{
              trackInternal: true,
              category: 'login',
              action: 'back_to_login',
              context: {
                login_email: email || '',
                lb_user_id: getCookie(ANALYTIC_COOKIES.LB_USER_ID),
              },
            }}
          >
            Back To Log In
          </StyledButton>
        </Prompt>
      )}
    </StyledModal>
  )
}

export default ForgotPasswordModal
