import React from 'react'
import styled from 'styled-components'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import { desktopVw } from '../styles/utils'

const StyledCard = styled.div`
  box-sizing: border-box;
  padding: 12.44px 20.4px 13.85px;
  color: ${props =>
    props.color ? props.theme.colors.secondary : props.theme.colors.primary};
  background: ${props => props.theme.colors.secondary};
  background-color: ${props =>
    props.bgColor ? props.bgColor : props.theme.colors.secondary};

  @media (min-width: ${props => props.theme.breakpoints.mobile}px) {
    padding: 16.44px 30.4px 12px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(34.3)} ${desktopVw(22.8)} ${desktopVw(16)};
  }
`

const Header = styled.header`
  margin: 0 0 43px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0 0 ${desktopVw(96)};
  }
`

const Title = styled.h2`
  font-size: 19px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(19)};
    line-height: ${desktopVw(20)};
  }
`

const Subtitle = styled.p`
  font-size: 19px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(19)};
  }
`

const TextWrapper = styled.div`
  p {
    margin: 0 0 8px;
    font-size: 12px;
    max-width: 219px;
    text-transform: uppercase;
  }

  @media (min-width: ${props => props.theme.breakpoints.mobile}px) {
    p {
      margin: 0 0 18px;
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    p {
      margin: 0 0 -${desktopVw(8)};
      font-size: ${desktopVw(12)};
      line-height: ${desktopVw(15)};
      max-width: ${desktopVw(156.6)};
    }
  }
`

const Footer = styled.footer`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 3px;
  border-top: 1px solid ${props => props.theme.colors.primary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-top: ${desktopVw(5)};
    border-top: none;
    border-bottom: 1px solid ${props => props.theme.colors.primary};
  }
`

const SmallText = styled.p`
  text-transform: uppercase;
  font-size: 8px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(8)};
    margin: ${desktopVw(3)} 0 0;
  }
`

const Price = styled.p`
  font-size: 25px;
  font-family: ${props => props.theme.fonts.secondary};
  position: relative;

  span {
    position: absolute;
    font-size: 10px;
    font-family: ${props => props.theme.fonts.primary};
    font-weight: normal;
    line-height: 16px;
    top: 0;
    left: -8px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(25)};
    line-height: ${desktopVw(32)};

    span {
      font-size: ${desktopVw(12)};
      top: ${desktopVw(4)};
      left: ${desktopVw(-10)};
    }
  }
`

const Card = ({
  className,
  children,
  title,
  subtitle,
  json,
  backgroundColor,
  smallText,
  price,
}) => {
  return (
    <StyledCard
      className={className}
      bgColor={backgroundColor}
      color={backgroundColor ? 1 : null}
    >
      <Header className='card-header'>
        <Title className='card-title'>{title}</Title>
        {subtitle && <Subtitle className='card-subtitle'>{subtitle}</Subtitle>}
      </Header>
      <TextWrapper className='card-textwrapper'>
        {documentToReactComponents(json)}
      </TextWrapper>
      {smallText && (
        <Footer>
          <SmallText>{smallText}</SmallText>
          <Price>
            <span>$</span>
            {price}
          </Price>
        </Footer>
      )}
      {children}
    </StyledCard>
  )
}

export default Card
