import * as types from '../actions/types'
// TODO: Remove this
const sortBy = type => {
  if (!type) return
  switch (type) {
    case types.FEATURED:
      return products => [...products]
    case types.PRICE_LOW_TO_HIGH:
      return products =>
        [...products].sort(
          (a, b) =>
            parseInt(a.p.shopifyProduct.variants.edges[0].node.price) -
            parseInt(b.p.shopifyProduct.variants.edges[0].node.price),
        )
    case types.PRICE_HIGH_TO_LOW:
      return products =>
        [...products].sort(
          (a, b) =>
            parseInt(b.p.shopifyProduct.variants.edges[0].node.price) -
            parseInt(a.p.shopifyProduct.variants.edges[0].node.price),
        )
    case types.ALPHABETICALLY_A_TO_Z:
      return products =>
        [...products].sort((a, b) => {
          const nameA = a.p.shopifyProduct.title
            ? a.p.shopifyProduct.title.toLowerCase()
            : a.p.title.toLowerCase()
          const nameB = b.p.shopifyProduct.title
            ? b.p.shopifyProduct.title.toLowerCase()
            : b.p.title.toLowerCase()
          if (nameA < nameB) {
            return -1
          }
          if (nameA > nameB) {
            return 1
          }

          return 0
        })

    case types.ALPHABETICALLY_Z_TO_A:
      return products =>
        [...products].sort((a, b) => {
          const nameA = a.p.shopifyProduct.title
            ? a.p.shopifyProduct.title.toLowerCase()
            : a.p.title.toLowerCase()
          const nameB = b.p.shopifyProduct.title
            ? b.p.shopifyProduct.title.toLowerCase()
            : b.p.title.toLowerCase()
          if (nameA < nameB) {
            return 1
          }
          if (nameA > nameB) {
            return -1
          }

          return 0
        })
    case types.OLDEST_TO_NEWEST:
      return products =>
        [...products].sort(
          (a, b) => new Date(a.p.createdAt) - new Date(b.p.createdAt),
        )
    case types.NEWEST_TO_OLDEST:
      return products =>
        [...products].sort(
          (a, b) => new Date(b.p.createdAt) - new Date(a.p.createdAt),
        )
    // https://stackoverflow.com/questions/29829205/sort-an-array-so-that-null-values-always-come-last
    case types.BEST_SELLER:
      return products =>
        [...products].sort((a, b) => {
          const aHasTag = a.p.tags && a.p.tags.includes(types.BEST_SELLER)
          const bHasTag = b.p.tags && b.p.tags.includes(types.BEST_SELLER)

          if (aHasTag === bHasTag) return 0

          if (aHasTag) return -1
          if (bHasTag) return 1

          return 0
        })

    default:
      throw new Error(`Unknown sort param: ${type}.`)
  }
}

const initialState = {
  sortKey: types.FEATURED,
  // sortBy: sortBy(types.FEATURED),
}

const sort = (state = initialState, { type, payload }) => {
  switch (type) {
    case types.SET_SORT_BY:
      return {
        sortKey: payload,
        sortBy: sortBy(payload),
      }
    case types.CLEAR_SORT_BY:
      return { sortKey: '', sortBy: null }
    default:
      return state
  }
}

export default sort
