import EventType from '../eventType'

export default class TaboolaPixel {
  [EventType.PAGE_VIEW] = properties => {
    if (window._tfa) {
      window._tfa.push({ notify: 'event', name: 'page_view', id: 1399245 })
    }
  };
  [EventType.ADD_TO_CART] = properties => {
    if (window._tfa)
      window._tfa.push({ notify: 'event', name: 'add_to_cart', id: 1399245 })
  };
  [EventType.PRODUCT_VIEW] = properties => {
    if (window._tfa)
      window._tfa.push({ notify: 'event', name: 'product_view', id: 1399245 })
  }
}
