import React from 'react'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const TrackableButton = React.forwardRef((props, ref) => {
  const clonedProps = { ...props }
  delete clonedProps.trackParams
  return (
    <button {...clonedProps} ref={ref} onClick={handleOnClick(props)}>
      {props.children}
    </button>
  )
})

const handleOnClick = props => e => {
  if (props.onClick) {
    props.onClick(e)
  }
  if (props.trackParams) {
    eventTracker.trackClick(EventType.BUTTON_CLICK, props.trackParams)
  }
}

export default TrackableButton
