import { useState, useContext, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { StoreContext } from '../contexts'
import queryVariantBySelectedOptions from '../shopify/queryVariantBySelectedOptions'
import { captureFatalException } from '../components/Sentry/utils'

/**
 *
 * @param {string} handle
 * @param {string} [store]
 *
 * @return {{product: Object, loading: boolean, error: Error}}
 */
const useSelectedOptions = (
  handle,
  startingSize = '',
  startingColor = '',
  storeSubdomain,
) => {
  // eslint-disable react-hooks/exhaustive-deps
  const [hasGroupVariant, setHasGroupVariant] = useState(false)

  const [selectedSize, setSelectedSize] = useState(startingSize || '')
  const [selectedColor, setSelectedColor] = useState(startingColor || '')
  const [selectedOptions, setSelectedOptions] = useState([])

  useEffect(() => {
    setHasGroupVariant(false)
  }, [handle])

  useEffect(() => {
    if (startingSize) {
      setSelectedSize(startingSize)
    }
    if (startingColor) {
      setSelectedColor(startingColor)
    }
  }, [startingSize, startingColor])

  const handleChange = e => {
    const { name } = e.target
    const { value } = e.target

    if (name === 'Size') {
      setSelectedSize(value)
    }

    if (name === 'Color') {
      setSelectedColor(value)
    }
  }

  useEffect(() => {
    if (hasGroupVariant) {
      setSelectedOptions([
        { name: 'Size', value: selectedSize },
        { name: 'Color', value: selectedColor },
        { name: 'GB', value: '-' },
      ])

      return
    }

    setSelectedOptions([
      { name: 'Size', value: selectedSize },
      { name: 'Color', value: selectedColor },
    ])
  }, [selectedColor, selectedSize, hasGroupVariant])

  const [state, setState] = useState({
    productVariant: null,
    loading: false,
    error: handle ? null : new Error('"handle" is missing.'),
  })

  useEffect(() => {
    const { productVariant } = state

    if (!productVariant) return

    if (
      !productVariant.variantBySelectedOptions &&
      productVariant.options.length > 2
    ) {
      setHasGroupVariant(true)
    }
  }, [state])

  const stores = useContext(StoreContext)
  const activeStoreSubdomain = useSelector(
    ({ storeSubdomain }) => storeSubdomain,
  )
  const { client } = stores[storeSubdomain || activeStoreSubdomain]

  useEffect(() => {
    if (selectedOptions && selectedOptions.length > 0 && handle) {
      const queryShopifyProduct = async () => {
        setState({ productVariant: null, loading: true, error: null })
        try {
          const {
            data: { product },
          } = await queryVariantBySelectedOptions(client, {
            handle,
            selectedOptions,
          })
          setState({ productVariant: product, loading: false, error: null })
        } catch (error) {
          if ((error || '').toLowerCase() !== 'cancelled') {
            captureFatalException(
              `query ShopifyVariant for ${handle} failed with the following error: ${error}`,
              { handle, selectedOptions },
            )
          }
          setState({ productVariant: null, loading: false, error })
        }
      }

      queryShopifyProduct()
    }
  }, [handle, selectedOptions])

  return {
    handleChange,
    selectedSize,
    selectedColor,
    productVariant: state.productVariant,
  }
}

export default useSelectedOptions
