import EventType from '../eventType'

export default class GoogleAdsTag {
  [EventType.PAGE_VIEW] = properties => {
    window.gtag('event', 'conversion', {
      send_to: 'AW-773421270/4AphCOWJ7JQBENbx5fAC',
      value: 0.0,
    })
  };

  [EventType.PRODUCT_VIEW](properties) {
    window.gtag('event', 'conversion', {
      send_to: 'AW-773421270/yJGDCOuJ7JQBENbx5fAC',
      value: formatRevenue(properties.price),
    })
  }

  [EventType.ADD_TO_CART] = properties => {
    window.gtag('event', 'conversion', {
      send_to: 'AW-773421270/cdIiCO6J7JQBENbx5fAC',
      value: formatRevenue(properties.price),
    })
  };

  [EventType.SEARCH] = properties => {
    window.gtag('event', 'conversion', {
      send_to: 'AW-773421270/6lZuCPSJ7JQBENbx5fAC',
      value: 0.0,
    })
  }
}

const formatRevenue = price => {
  if (price != null) {
    return price
  }
  return 0
}
