import React, { useEffect, useRef } from 'react'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'

const ScrollTracker = ({ slug }) => {
  const scrollTwentyFive = useRef(false)
  const scrollFifty = useRef(false)
  const scrollSeventyFive = useRef(false)
  const scrollHundred = useRef(false)

  const TWENTY_FIVE = 25
  const FIFTY = 50
  const SEVENTY_FIVE = 75
  const HUNDRED = 100

  const getProperties = value => {
    return {
      scrollPercentage: value,
    }
  }

  useEffect(() => {
    scrollTwentyFive.current = false
    scrollFifty.current = false
    scrollSeventyFive.current = false
    scrollHundred.current = false
  }, [slug])

  useEffect(() => {
    window.addEventListener('scroll', handleScroll)
  }, [])

  const handleScroll = ev => {
    const currentScrollPercentage = computeScrollPercentage()
    if (currentScrollPercentage >= TWENTY_FIVE && !scrollTwentyFive.current) {
      scrollTwentyFive.current = true
      eventTracker.track(EventType.PAGE_SCROLL, getProperties(TWENTY_FIVE))
    } else if (currentScrollPercentage >= FIFTY && !scrollFifty.current) {
      scrollFifty.current = true
      eventTracker.track(EventType.PAGE_SCROLL, getProperties(FIFTY))
    } else if (
      currentScrollPercentage >= SEVENTY_FIVE &&
      !scrollSeventyFive.current
    ) {
      scrollSeventyFive.current = true
      eventTracker.track(EventType.PAGE_SCROLL, getProperties(SEVENTY_FIVE))
    } else if (currentScrollPercentage == HUNDRED && !scrollHundred.current) {
      scrollHundred.current = true
      eventTracker.track(EventType.PAGE_SCROLL, getProperties(HUNDRED))
    }
  }

  const computeScrollPercentage = () => {
    const scrollTop = window.scrollY
    const docHeight = document.body.scrollHeight
    const scrollPercent = Math.round(
      (scrollTop / (docHeight - window.innerHeight)) * 100,
    )
    return scrollPercent
  }

  return <></>
}

export default ScrollTracker
