import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'

import Chevron from '../../assets/dropdown.svg'

const SelectWrapper = styled.div`
  position: relative;
  box-sizing: border-box;
  cursor: default;
`

const StyledSelect = styled.div`
  position: relative;
  display: flex;
  align-items: center;
  height: 38px;
  font-size: 12px;
  font-family: ${props => props.theme.fonts.primary};
  border: 1px solid ${props => props.theme.colors.greyLight};
  border-bottom: ${props => (props.open ? '1px solid transparent' : null)};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    height: ${desktopVw(38)};
    overflow: hidden;
  }
`

const DropdownPanel = styled.div`
  display: ${props => (props.open ? 'block' : 'none')};
  position: absolute;
  top: 100%;
  z-index: 1000;
  overflow-y: scroll;
  box-sizing: border-box;
  padding: 0 0 16px;
  max-height: 270px;
  width: 100%;
  border: 1px solid ${props => props.theme.colors.greyLight};
  border-top: none;
  background: ${props => props.theme.colors.secondary};
  -webkit-overflow-scrolling: touch;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 0 ${desktopVw(16)};
    max-height: ${desktopVw(270)};
  }
`

const DefaultOption = styled.div`
  color: ${props => props.theme.colors.greyLight};
  font-size: 12px;
  padding: 6px 16px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(6)} ${desktopVw(16)};
  }
`

const SelectedOption = styled.div`
  font-size: 12px;
  padding: 6px 16px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(6)} ${desktopVw(16)};
  }
`

const Option = styled.div`
  font-size: 12px;
  padding: 6px 16px;

  &:hover {
    background: ${props => props.theme.colors.greyLighter};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
    padding: ${desktopVw(6)} ${desktopVw(16)};
  }
`

const DropdownIcon = styled(Chevron)`
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 16px;
  pointer-events: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    right: ${desktopVw(16)};
  }
`

export const CustomSelect = ({
  className,
  name,
  label,
  options,
  onChange,
  defaultOption,
}) => {
  const [selectedOption, setSelectedOption] = useState(defaultOption || '')
  const [open, setOpen] = useState(false)

  useEffect(() => {
    setSelectedOption(defaultOption)
  }, [defaultOption])

  const handleClick = e => {
    setSelectedOption(e.target.dataset.displayname)

    const evt = {
      target: {
        name: e.target.dataset.name,
        value: e.target.dataset.value,
      },
    }

    onChange(evt)
    setOpen(false)
  }

  return (
    <SelectWrapper className={className}>
      <StyledSelect onClick={() => setOpen(!open)} open={open}>
        {!selectedOption && label && (
          <DefaultOption value={label} disabled>
            {label}
          </DefaultOption>
        )}
        {selectedOption && <SelectedOption>{selectedOption}</SelectedOption>}
        <DropdownIcon />
      </StyledSelect>
      <DropdownPanel open={open}>
        {selectedOption && label && (
          <DefaultOption value={label} disabled>
            {label}
          </DefaultOption>
        )}
        {options &&
          options.map(option => (
            <Option
              key={option.value}
              onClick={e => handleClick(e)}
              data-name={name}
              data-displayname={option.name}
              data-value={option.value}
            >
              {option.name}
            </Option>
          ))}
      </DropdownPanel>
    </SelectWrapper>
  )
}
