import React, { useState, useEffect } from 'react'
import styled, { css } from 'styled-components'
import { Link } from 'gatsby'
import { useDispatch, useSelector } from 'react-redux'
import { setMobileMenuOpen } from '../../actions'

import { desktopVw } from '../../styles/utils'

import { getCurrentUser, getPathName, isLoggedIn } from '../../utils'

import WishlistEmpty from '../../assets/wishlist-empty.svg'
import WishlistFull from '../../assets/wishlist-full.svg'
import useWishlist from '../../hooks/useWishlist'

const iconStyle = css`
  width: 19px;
  fill: ${props => props.theme.colors.primary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(19)};
  }
`

const StyledLink = styled(Link)`
  display: inline-flex;
  align-items: flex-start;
`

const InactiveWishlist = styled(WishlistEmpty)`
  ${iconStyle}
  fill: ${props => props.theme.colors.primary} !important;
`

const ActiveWishlist = styled(WishlistFull)`
  ${iconStyle}
`

const Count = styled.span`
  font-size: 11px;
  margin-left: 3px;
  color: ${props => props.theme.colors.primary};

  @media (max-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    top: 2px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(14)};
    margin-left: ${desktopVw(3)};
  }
`

const WishlistLink = () => {
  const [active, setActive] = useState(false)
  const { data: wishlist } = useSelector(state => state.wishlist)
  const [wishlistCount, setWishlistCount] = useState(0)

  const dispatch = useDispatch()

  const { fetchUserWishlist } = useWishlist()

  useEffect(() => {
    if (isLoggedIn()) {
      fetchUserWishlist(getCurrentUser().email)
    }
  }, [])

  useEffect(() => {
    // Some of the wishlist products can be outdated. When fetching from API,
    // they don’t have contentful product. In those cases, wishlistId becomes empty string ““.
    // So, wishlist count in header can this way be upto date with products shown in the wishlist page.
    const count = wishlist.filter(
      wishlistProduct => !!wishlistProduct.wishlistId,
    ).length

    setActive(count > 0)
    setWishlistCount(count)
  }, [wishlist])

  return (
    <StyledLink
      to={getPathName('wishlist')}
      aria-label='Wishlist'
      onClick={() => dispatch(setMobileMenuOpen(false))}
    >
      {active ? <ActiveWishlist /> : <InactiveWishlist />}
      {wishlistCount >= 1 && <Count>{wishlistCount}</Count>}
    </StyledLink>
  )
}

export default WishlistLink
