import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'
import { emailPattern } from '../../utils'

import inputStyle from './inputStyle'
import { ErrorMessage } from './ErrorMessage'

const StyledInput = styled.input`
  ${inputStyle}

  &::placeholder {
    font-size: ${({ largeText }) => (largeText ? '13px' : null)};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    &::placeholder {
      font-size: ${desktopVw(12)};
    }
  }
`

export const Input = ({
  className,
  placeholder,
  type,
  name,
  errors,
  defaultValue,
  onChange,
  register,
  required,
  readOnly,
  value,
  errorClassName,
}) => {
  return (
    <>
      <StyledInput
        className={className}
        placeholder={placeholder}
        name={name}
        onChange={e => onChange && onChange(e)}
        type={type}
        defaultValue={defaultValue}
        readOnly={readOnly}
        largeText={placeholder && placeholder.length > 30 ? 1 : 0}
        ref={
          register &&
          register({
            required: {
              value: required,
              message: `Please fill in ${placeholder}`,
            },
            pattern: {
              value: type === 'email' && emailPattern,
              message: `Please enter a valid email address`,
            },
          })
        }
        value={value}
      />
      {errors && errors[name] && (
        <ErrorMessage className={errorClassName}>
          {errors[name].message}
        </ErrorMessage>
      )}
    </>
  )
}
