import React from 'react'
import styled from 'styled-components'
import { desktopVw } from '../../styles/utils'

import useCheckoutLineItem from '../../hooks/useCheckoutLineItem'
import TextButton from '../TextButton'
import eventTracker from '../../trackers/EventTracker'
import EventType from '../../trackers/eventType'
import DiscountTag from '../../assets/new_discount_tag.svg'
import {
  decodeIdFromGraphQLResponse,
  extractLineItemFromGraphEdge,
} from '../../utils'
import { withErrorBoundary } from '../ErrorBoundary/ErrorBoundary'

const StyledLineItem = styled.li`
  display: flex;
  margin-bottom: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(15)};
  }
`

const Figure = styled.figure`
  margin-right: 12px;
  width: 84px;
  min-height: 108px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(12)};
    width: ${desktopVw(80)};
    min-height: ${desktopVw(104)};
  }
`

const Body = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex: 1;
`

const Img = styled.img`
  object-fit: cover;
  width: 100%;
  height: 100%;
`

const TitleWrapper = styled.div`
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 7px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(5)};
  }
`

const Title = styled.h2`
  font-family: ${props => props.theme.fonts.primary};
  font-weight: 400;
  font-size: 12px;
  max-width: 80%;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-weight: 500;
    font-size: ${desktopVw(12)};
  }
`

const PriceContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin-top: 4px;
`

const Price = styled.span`
  margin-top: 2px;
  font-size: 12px;
  text-decoration: ${props =>
    props.isDiscountApplicable === true && 'line-through'};
  color: ${props => props.isDiscountApplicable === true && '#6c6c6c'};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(12)};
  }
`

const DiscountContainer = styled.div`
  display: flex;
  margin-bottom: 10px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: ${desktopVw(10)};
  }
`

const DiscountCode = styled.div`
  color: ${props => props.theme.colors.lightGreen};
  text-transform: uppercase;
  font-size: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const DiscountAmount = styled.div`
  color: ${props => props.theme.colors.lightGreen};
  text-transform: uppercase;
  font-size: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const DiscountImage = styled(DiscountTag)`
  margin-right: 5px;
  width: 13px;
  height: 13px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(5)};
    width: ${desktopVw(13)};
    height: ${desktopVw(13)};
  }
`

const Options = styled.div`
  margin-bottom: 10px;
  font-size: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-bottom: ${props =>
      props.isDiscountApplicable ? desktopVw(10) : desktopVw(17)};
  }
`

const Option = styled.span`
  text-transform: uppercase;
  font-size: 10px;
  margin-right: 9px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-right: ${desktopVw(9)};
  }
`

const QuantityWrapper = styled.div`
  display: flex;
  align-items: center;
`

const QuantitySelector = styled.div`
  margin-right: 25px;
  display: flex;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(10)};
  }
`

const QuantityInput = styled.input`
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 -1px;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid #785a28;
  width: 37px;
  height: 30px;
  font-size: 10px;
  font-family: ${props => props.theme.fonts.primary};
  text-align: center;
  background: transparent;
  border-radius: 0;
  -webkit-appearance: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(37)};
    height: ${desktopVw(25)};
    font-size: ${desktopVw(10)};
  }
`

const QuantityButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  width: 33px;
  height: 30px;
  border: 1px solid #785a28;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${desktopVw(33)};
    height: ${desktopVw(25)};
    font-size: ${desktopVw(10)};
  }
`

const Delete = styled(TextButton)`
  text-transform: uppercase;
  font-family: ${props => props.theme.fonts.primary};
  font-size: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
  }
`

const CustomAttributeWrapper = styled.div`
  display: flex;
`

const CustomAttribute = styled.div`
  font-size: 10px;
  text-transform: uppercase;
  margin-top: 10px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(10)};
    margin-top: ${desktopVw(10)};
  }
`

const LineItemImage = ({ title, variant }) => (
  <Img
    src={variant?.image?.transformedSrc}
    alt={`${title} - ${variant?.title}`}
  />
)

const LineItemImageWithErrorBoundary = withErrorBoundary(LineItemImage, {
  fallbackRender: () => <div />,
})

const LineItem = React.forwardRef(
  (
    {
      lineItem: {
        title,
        quantity,
        variant,
        customAttributes,
        discountAllocations,
        id: lineItemId,
      },
    },
    ref,
  ) => {
    const { updateLineItemQuantity, removeLineItem } = useCheckoutLineItem()

    const add = () => {
      updateLineItemQuantity({
        lineItemId,
        variantId: variant.id,
        quantity: quantity + 1,
        customAttributes,
      }).then(updatedLineItems => {
        if (updatedLineItems) {
          eventTracker.track(EventType.UPDATE_ITEM_QUANTITY, {
            variantId: decodeIdFromGraphQLResponse(variant.id),
            productId: decodeIdFromGraphQLResponse(variant.product.id),
            lineItems: updatedLineItems.edges.map(edge =>
              extractLineItemFromGraphEdge(edge),
            ),
          })
        }
      })
    }

    const subtract = () => {
      updateLineItemQuantity({
        lineItemId,
        variantId: variant.id,
        quantity: quantity > 0 ? quantity - 1 : 0,
        customAttributes,
      }).then(updatedLineItems => {
        if (updatedLineItems) {
          eventTracker.track(EventType.UPDATE_ITEM_QUANTITY, {
            variantId: decodeIdFromGraphQLResponse(variant.id),
            productId: decodeIdFromGraphQLResponse(variant.product.id),
            lineItems: updatedLineItems.edges.map(edge =>
              extractLineItemFromGraphEdge(edge),
            ),
          })
        }
      })
    }

    const removeFromCart = () => {
      removeLineItem({
        lineItemId,
      }).then(updatedLineItems => {
        if (updatedLineItems) {
          eventTracker.track(EventType.REMOVE_FROM_CART, {
            variantId: decodeIdFromGraphQLResponse(variant.id),
            productId: decodeIdFromGraphQLResponse(variant.product.id),
            lineItems: updatedLineItems.edges.map(edge =>
              extractLineItemFromGraphEdge(edge),
            ),
          })
        }
      })
    }

    const showCustomAttributes =
      customAttributes.filter(({ key }) => key === 'Pre order').length > 0

    let discountAmount =
      discountAllocations.length > 0 &&
      discountAllocations
        .map(discountAllocation => discountAllocation.allocatedAmount.amount)
        .reduce((acc, val) => acc + parseFloat(val), 0)
        .toFixed(2)
        .replace(/\.00$/, '')
    if (discountAmount) discountAmount = parseFloat(discountAmount)

    const discountCode =
      discountAllocations.length > 0 &&
      discountAllocations[0].discountApplication.title

    return (
      <StyledLineItem ref={ref}>
        <Figure>
          <LineItemImageWithErrorBoundary variant={variant} title={title} />
        </Figure>
        <Body>
          <TitleWrapper>
            <Title>{title}</Title>
          </TitleWrapper>
          <Options isDiscountApplicable={discountAmount && discountCode}>
            {variant.selectedOptions.map(({ name, value }, i) =>
              name !== 'GB' && name !== 'Title' && title !== 'Gift Card' ? (
                <Option key={`${name}${i}`}>{`${name} ${value}`}</Option>
              ) : null,
            )}
            {title === 'Gift Card' /** Special handling for gift card */ &&
              variant.selectedOptions.map(({ name, value }, i) =>
                name !== 'GB' && name !== 'Title' && name !== 'Size' ? (
                  <Option key={`Value${i}`}>{`Value ${value}`}</Option>
                ) : null,
              )}
          </Options>
          {discountAmount && discountCode && discountCode !== 'pd_exp' && (
            <DiscountContainer>
              <DiscountImage />
              <DiscountCode>{discountCode}</DiscountCode>
              <DiscountAmount>
                &nbsp;(-${parseFloat(discountAmount).toFixed(2)})
              </DiscountAmount>
            </DiscountContainer>
          )}
          <QuantityWrapper>
            <QuantitySelector>
              <QuantityButton type='button' onClick={() => subtract()}>
                -
              </QuantityButton>
              <QuantityInput
                type='text'
                readOnly
                value={quantity < 10 ? `0${quantity}` : quantity}
                aria-label='quantity'
              />
              <QuantityButton
                aria-label={`add ${title} - ${variant.title}`}
                type='button'
                onClick={() => add()}
              >
                +
              </QuantityButton>
            </QuantitySelector>
            <Delete
              aria-label={`remove ${title} - ${variant.title}`}
              type='button'
              onClick={() => removeFromCart()}
            >
              Delete
            </Delete>
          </QuantityWrapper>
          {showCustomAttributes && (
            <CustomAttributeWrapper>
              {customAttributes.map(({ key, value }, i) => (
                <CustomAttribute key={`${key}${value}${i}`}>
                  {key} : {value}
                </CustomAttribute>
              ))}
            </CustomAttributeWrapper>
          )}
        </Body>
        <PriceContainer>
          {discountAmount && (
            <Price>
              {' '}
              $
              {parseFloat(
                parseFloat(
                  quantity *
                    variant.presentmentPrices.edges[0].node.price.amount,
                ) - parseFloat(discountAmount),
              ).toFixed(2)}{' '}
            </Price>
          )}
          {discountCode !== 'pd_exp' && (
            <Price isDiscountApplicable={discountAmount && discountAmount > 0}>
              {`$${parseFloat(
                quantity * variant.presentmentPrices.edges[0].node.price.amount,
              ).toFixed(2)}`}
            </Price>
          )}
        </PriceContainer>
      </StyledLineItem>
    )
  },
)

export default LineItem
