import { getCurrentUser } from '../utils'
import useKlaviyoIdentify from '../hooks/useKlaviyoIdentify'

class KlaviyoInitializer {
  initialize() {
    let user = getCurrentUser()
    if (user && user.email) {
      useKlaviyoIdentify(user.email)
    }
  }
}

const klaviyoInitializer = new KlaviyoInitializer()
Object.freeze(klaviyoInitializer)

export default klaviyoInitializer
