import React, { useEffect, useState } from 'react'
import styled, { ThemeProvider } from 'styled-components'
import { graphql, useStaticQuery } from 'gatsby'
import { Helmet } from 'react-helmet'
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'

import Amplify from 'aws-amplify'
import config from '../aws-exports'

import { desktopVw, mobileVw } from '../styles/utils'
import fallbackTheme from '../styles/theme'

// Hooks
import useIntersect from '../hooks/useIntersect'

// Components
import Header from './Header'
import Footer from './Footer'
import Cart from './Cart'
import { GlobalStyle } from '../shared/css/globalStyles'
import SubscribeForm from './SubscribeForm'
import experimentManager from '../trackers/ExperimentManager'
import {
  extractQueryParamFromPageLocation,
  getCookie,
  isUtmAttentiveText,
  isValidEmail,
  loadState,
  saveState,
} from '../utils'
import useKlaviyoSignup from '../hooks/useKlaviyoSignup'
import useNewsLetterSignUp from '../hooks/useNewsLetterSignUp'
import useCustomerService from '../hooks/useCustomerService'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import ScrollTracker from '../utils/scrollTracker'
import { captureEvent, captureWarning } from './Sentry/utils'
import { ANALYTIC_COOKIES, FEATURE_COOKIES } from '../utils/constants'
import useAttentiveSdk from '../hooks/useAttentiveSdk'
import useInterval from '../hooks/useInterval'
import RightArrow from '../assets/right-arrow.svg'
import { CookiesConsent } from './CookiesConsent'

Amplify.configure(config)

const klaviyoListId = 'NWNxgi'
const subscribing = 'Subscribing...'
const somethingWentWrong = 'Oops something went wrong, please try again later'

const loadFirstPurchaseCouponCode = loadState('firstPurchaseCouponCode')
const saveFirstPurchaseCouponCode = saveState('firstPurchaseCouponCode')

const IntersectionDiv = styled.div`
  position: absolute;
  width: 100%;
  height: ${props => props.theme.announcementBar.mobile.height}px;
  top: 0;
  left: 0;
  visibility: hidden;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${props => desktopVw(props.theme.announcementBar.desktop.height)};
  }
`

const AnnouncementBar = styled.div`
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0 20px;
  height: ${props => props.theme.announcementBar.mobile.height}px;
  font-size: 7px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: ${props => props.theme.colors.secondary};
  background-color: ${props => props.theme.colors.green};

  @media (min-width: ${props => props.theme.breakpoints.mobile}px) {
    font-size: 7px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    z-index: 1100;
    font-size: 0.7vw;
    height: ${props => desktopVw(props.theme.announcementBar.desktop.height)};
  }
`

const AnnouncementBarDesktop = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
  }
`

const AnnouncementBarMobile = styled.div`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
  }
`

const PDPAnnouncementBar = styled(AnnouncementBar)`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: flex;
  }
`

const TextField = styled.p`
  margin-right: ${desktopVw(16)};
`

// only for desktop
const StyledSubscribeForm = styled(SubscribeForm)`
  font-size: 0.7vw;
  font-weight: 400;
  font-family: ${props => props.theme.fonts.primary};
  .subscribeform {
    &__form {
      margin-top: 0;
      height: 100%;
      width: ${mobileVw(292)};
    }
    &__input,
    &__submit {
      padding: 0;
      border-color: ${props => props.theme.colors.secondary};
      color: ${props => props.theme.colors.secondary};
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &__input {
      padding: 6px 16px 6px 16px;
      font-size: 7px;
      width: ${mobileVw(272)};
      font-weight: 400;
      &::placeholder {
        color: ${props => props.theme.colors.secondary};
      }
      &:focus {
        &::placeholder {
          color: transparent;
        }
      }
    }
    &__submit {
      width: ${desktopVw(132)};
      background: ${props => props.theme.colors.secondary};
      color: ${props => props.theme.colors.primary};
    }
    &__success-msg {
      color: ${props => props.theme.colors.secondary};
    }
    &__loading-msg {
      color: ${props => props.theme.colors.secondary};
    }
    @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
      &__form {
        width: ${desktopVw(340)};
      }
      &__input {
        font-size: ${desktopVw(10)};
        padding: ${desktopVw(6)} ${desktopVw(16)};
      }
      &__submit {
        font-size: ${desktopVw(10)};
        font-weight: 400;
      }
    }
  }
`

const EmailWrapper = styled.span`
  display: flex;
  align-items: center;
`

const EmailText = styled.span`
  margin-right: ${desktopVw(15)};
`

const SuccessMsg = styled.div``

let successMessage

const Layout = ({
  children,
  theme,
  slug,
  subscribeBannerCardColor,
  data,
  disableSEOHeaderNode,
  showBgColor,
}) => {
  const { contentfulSite } = useStaticQuery(graphql`
    query LayoutQuery {
      contentfulSite {
        announcementBarRich {
          json
        }
        announcementBarRichMobile {
          json
        }
        announcementBannerMobile {
          childContentfulTextSectionTextRichTextNode {
            json
          }
          sectionType
        }
        announcementBannerDesktop {
          childContentfulTextSectionTextRichTextNode {
            json
          }
          sectionType
        }
        cartBackgroundColor {
          name
          hexcode
        }
      }
    }
  `)

  const [bannerIndex, setBannerIndex] = useState(0)
  const [mobileAnnouncements, setMobileAnnouncements] = useState(
    contentfulSite?.announcementBannerMobile,
  )
  const [desktopAnnouncements, setDesktopAnnouncements] = useState(
    contentfulSite?.announcementBannerDesktop,
  )
  const [delay, setDelay] = useState(10000)

  useInterval(() => {
    const email =
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
      getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL) ||
      ''
    if (email) {
      setMobileAnnouncements(announcements =>
        announcements.filter(item => item.sectionType !== 'email'),
      )
      setDesktopAnnouncements(announcements =>
        announcements.filter(item => item.sectionType !== 'email'),
      )
    }
    if (errorMsg) {
      setErrorMsg(null)
    }
    setBannerIndex(
      bannerIndex => (bannerIndex + 1) % mobileAnnouncements.length,
    )
  }, delay)

  const { checkOldUser } = useCustomerService()

  const [email, setEmail] = useState('')
  const [loadingMsg, setLoadingMsg] = useState(null)
  const [errorMsg, setErrorMsg] = useState(null)
  const [showSuccessMsg, setShowSuccessMsg] = useState(false)
  const [showSubscription, setShowSubscription] = useState(true)
  const { signup } = useKlaviyoSignup()
  const { newsLetterSignUp } = useNewsLetterSignUp()
  const { initializeAttentive } = useAttentiveSdk()

  const [ref, isIntersecting] = useIntersect(null, '0px', 0, true)
  const isProductPage = !data || data.contentfulProduct

  useEffect(() => {
    // disable attentive discount bc of first purchase discount
    // if (
    //   getCookie(FEATURE_COOKIES.IS_WELCOME_POPUP_PREVIOUSLY_OPENED) ||
    //   isUtmAttentiveText()
    // )
    //   initializeAttentive()
    const discountCode = extractQueryParamFromPageLocation('discount_code')
    if (discountCode) saveFirstPurchaseCouponCode({ code: discountCode })
  }, [])

  const onFocus = () => {
    setDelay(null)
    setErrorMsg('')
  }

  const onBlur = () => {
    setDelay(10000)
  }

  const captureEmailError = email => {
    captureWarning(
      `Something went wrong in subscribing user via E-mail - Product Page. Email : ${email}`,
    )
  }

  /* Klaviyo API for E-mails */
  const subscribeEmail = async () => {
    setShowSuccessMsg(false)
    setLoadingMsg(subscribing)

    try {
      const queryStringParameters = {
        klaviyoListId,
        email,
      }

      const { response, error } = await signup(queryStringParameters)
      await signupOneQuince()

      if (response.status === 400) {
        setErrorMsg(somethingWentWrong)
        setLoadingMsg(null)
        captureEmailError(email)
        setShowSubscription(false)
      }

      if (response.status === 200) {
        setShowSuccessMsg(true)
        successMessage = 'Keep An Eye Out'
        setLoadingMsg(null)
        setTimeout(() => {
          setShowSubscription(false)
        }, 5000)
      } else if (error) {
        setErrorMsg(somethingWentWrong)
        setLoadingMsg(null)
        captureEmailError(email)
        setShowSubscription(false)
      }
    } catch (err) {
      setErrorMsg(somethingWentWrong)
      setLoadingMsg(null)
      captureEmailError(email)
      setShowSubscription(false)
    }
  }

  /* Storefront API for news letter sign up */
  const signupOneQuince = async () => {
    const bucket = experimentManager.getBucketForUser(
      'first_purchase_email_discount',
    )
    try {
      const isOldUser = await checkOldUser(email)
      if (bucket === 'five-percent') {
        if (!loadFirstPurchaseCouponCode && isOldUser.response === 'false') {
          successMessage = `Your discount code is sent via email!`
        } else successMessage = `You are already subscribed.`
      } else {
        successMessage = `You are already registered.`
      }

      newsLetterSignUp(email, bucket, isOldUser.response).then(
        ({ response, error, loading }) => {
          if (
            response &&
            bucket === 'five-percent' &&
            isOldUser.response === 'false'
          ) {
            saveFirstPurchaseCouponCode({ code: response.code })
          } else if (error) {
            captureWarning(
              `Discount code generated in first purchase could not be applied : ${response.code} - ${email} and bucket: ${bucket}`,
            )
          }
        },
      )
    } catch (err) {
      captureEvent(
        `Something went wrong in applying first purchase discount : ${email} and bucket: ${bucket}`,
      )
      setErrorMsg(somethingWentWrong)
      setLoadingMsg(null)
      setShowSubscription(false)
    }
  }

  const handleEmailSubmit = e => {
    e.preventDefault()

    if (!isValidEmail(email)) {
      setShowSuccessMsg(false)
      setErrorMsg(`${email} is an invalid e-mail address`)
      return
    }

    subscribeEmail()
    eventTracker.track(EventType.EMAIL_NEWSLETTER_SUBSCRIBE, {
      email,
    })
  }

  const AnnouncementBarRich = (
    <>
      <AnnouncementBarDesktop>
        {desktopAnnouncements[bannerIndex]?.sectionType === 'email' ? (
          <>
            {successMessage || errorMsg ? (
              <SuccessMsg>{successMessage || errorMsg}</SuccessMsg>
            ) : (
              <EmailWrapper>
                <EmailText>
                  {documentToReactComponents(
                    desktopAnnouncements[bannerIndex]
                      ?.childContentfulTextSectionTextRichTextNode?.json,
                  )}
                </EmailText>
                <StyledSubscribeForm
                  email={email}
                  handleChange={setEmail}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  handleSubmit={handleEmailSubmit}
                  placeholder='YOUR EMAIL'
                  submitText='GET UPDATES'
                />
              </EmailWrapper>
            )}
          </>
        ) : (
          documentToReactComponents(
            desktopAnnouncements[bannerIndex]
              ?.childContentfulTextSectionTextRichTextNode?.json,
          )
        )}
      </AnnouncementBarDesktop>
      <AnnouncementBarMobile>
        {mobileAnnouncements[bannerIndex]?.sectionType === 'email' ? (
          <>
            {showSuccessMsg || errorMsg ? (
              <SuccessMsg>{successMessage || errorMsg}</SuccessMsg>
            ) : (
              <StyledSubscribeForm
                email={email}
                handleChange={setEmail}
                onFocus={onFocus}
                onBlur={onBlur}
                handleSubmit={handleEmailSubmit}
                placeholder={
                  mobileAnnouncements[bannerIndex]
                    ?.childContentfulTextSectionTextRichTextNode?.json
                    ?.content[0]?.content[0]?.value
                }
                submitText={<RightArrow />}
              />
            )}
          </>
        ) : (
          documentToReactComponents(
            mobileAnnouncements[bannerIndex]
              ?.childContentfulTextSectionTextRichTextNode?.json,
          )
        )}
      </AnnouncementBarMobile>
    </>
  )

  // TODO: integrate this one email from cookie is detected
  // if (isProductPage) {
  //   if (loadingMsg) AnnouncementBarRich = <p>{loadingMsg}</p>
  //   else if (showSuccessMsg) {
  //     if (showSubscription) AnnouncementBarRich = <p>{successMessage}</p>
  //   } else
  //     AnnouncementBarRich = (
  //       <StyledSubscribeForm
  //         email={email}
  //         handleChange={setEmail}
  //         onFocus={onFocus}
  //         handleSubmit={handleEmailSubmit}
  //         success={showSuccessMsg}
  //         successMessage={successMessage}
  //         errorMsg={errorMsg}
  //         loadingMsg={loadingMsg}
  //         placeholder='Your Email'
  //         submitText='Get Notified'
  //       />
  //     )
  // }

  const getAnnouncementBar = () =>
    isProductPage ? (
      <PDPAnnouncementBar>{AnnouncementBarRich}</PDPAnnouncementBar>
    ) : (
      <AnnouncementBar>{AnnouncementBarRich}</AnnouncementBar>
    )

  return (
    <ThemeProvider theme={theme || fallbackTheme}>
      <>
        <CookiesConsent />
        <Helmet>
          <html lang='en' />
          <meta name='viewport' content='width=device-width, initial-scale=1' />
          {/*  giordan google site verification */}
          <meta
            name='google-site-verification'
            content='SB-d1B8-Mbyw5Au_KLzXGOofOFlahghYMmDjVMCLvNE'
          />
        </Helmet>
        <GlobalStyle showSwellPopUp={slug === '/account/my-rewards'} />
        <IntersectionDiv ref={ref} />
        {slug !== '/404' && getAnnouncementBar()}
        <Header
          scrolledDown={!isIntersecting}
          disableSEOHeaderNode={disableSEOHeaderNode}
          showBgColor={showBgColor}
        />
        <main>{children}</main>
        {!['/404', '/haveitall'].includes(slug) && (
          <Footer subscribeBannerCardColor={subscribeBannerCardColor} />
        )}
        <Cart
          backgroundColor={
            contentfulSite.cartBackgroundColor &&
            contentfulSite.cartBackgroundColor.hexcode
          }
        />
        <ScrollTracker slug={slug} />
      </>
    </ThemeProvider>
  )
}

export default Layout
