import EventType from '../eventType'
import { getCookie } from '../../utils'
import { ANALYTIC_COOKIES } from '../../utils/constants'

export default class RockerboxPixel {
  [EventType.PAGE_VIEW] = properties => {
    // they want to trigger identify event on every page view
    if (window.RB) window.RB.track('identify', getParameters())
    if (window.RB) window.RB.track('view')
  };

  [EventType.ADD_TO_CART] = properties => {
    if (window.RB)
      window.RB.track('conv.add_to_cart', getParameters(properties))
  };

  [EventType.EMAIL_NEWSLETTER_SUBSCRIBE] = properties => {
    if (window.RB)
      window.RB.track('conv.email_signup', getParameters(properties))
  };

  [EventType.PRODUCT_VIEW] = properties => {
    if (window.RB)
      window.RB.track('conv.view_product', getParameters(properties))
  }
}

function getParameters(properties) {
  let email =
    getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL) ||
    getCookie(ANALYTIC_COOKIES.LB_CUSTOMER_TENTATIVE_EMAIL)
  if (email) {
    email = email.toLowerCase()
  }
  return { email }
}
