import {
  checkForWordInURL,
  eraseCookie,
  extractDomainFromURL,
  extractQueryParamFromPageLocation,
  getCookie,
  getCurrentUser,
  setCookie,
} from '../utils'
import eventTracker from './EventTracker'
import EventType from './eventType'
import { ANALYTIC_COOKIES } from '../utils/constants'

export default function initialize() {
  // Capturing user information in cookie.
  setCustomerDetailsInCookie()

  // Persisting Session information in cookies.
  createLastBrandUserId()
  captureCampaignParams()
  createNewSessionIfNeeded()

  // Extending session every 80 minutes
  setInterval(function() {
    createNewSessionIfNeeded()
  }, 80 * 60 * 1000)
}

function createLastBrandUserId() {
  if (!getCookie(ANALYTIC_COOKIES.LB_USER_ID)) {
    setCookie(ANALYTIC_COOKIES.LB_USER_ID, uniqueIdGenerator())
    setCookie('_lb_user_first_access', true, 1)
  }
}

// TODO: Change to a better algorithm
const uniqueIdGenerator = () => {
  // Math.random should be unique because of its seeding algorithm.
  // Convert it to base 36 (numbers + letters), and grab the first 9 characters
  // after the decimal.
  return `_${Math.random()
    .toString(36)
    .substr(2, 9)}`
}

function captureCampaignParams() {
  let isUTMParamsPresent = false
  if (extractQueryParamFromPageLocation('utm_source')) {
    setCookie(
      ANALYTIC_COOKIES.UTM_SOURCE,
      extractQueryParamFromPageLocation('utm_source'),
      120,
    )
    eraseCookie(ANALYTIC_COOKIES.UTM_MEDIUM)
    eraseCookie(ANALYTIC_COOKIES.UTM_CONTENT)
    eraseCookie(ANALYTIC_COOKIES.UTM_TERM)
    eraseCookie(ANALYTIC_COOKIES.QCLID)
    isUTMParamsPresent = true
  }
  if (extractQueryParamFromPageLocation('utm_content')) {
    setCookie(
      ANALYTIC_COOKIES.UTM_CONTENT,
      extractQueryParamFromPageLocation('utm_content'),
      120,
    )
  }
  if (extractQueryParamFromPageLocation('utm_term')) {
    setCookie(
      ANALYTIC_COOKIES.UTM_TERM,
      extractQueryParamFromPageLocation('utm_term'),
      120,
    )
  }
  if (extractQueryParamFromPageLocation('utm_medium')) {
    setCookie(
      ANALYTIC_COOKIES.UTM_MEDIUM,
      extractQueryParamFromPageLocation('utm_medium'),
      120,
    )
  }
  if (extractQueryParamFromPageLocation('utm_campaign')) {
    setCookie(
      ANALYTIC_COOKIES.UTM_CAMPAIGN,
      extractQueryParamFromPageLocation('utm_campaign'),
      120,
    )
  }
  if (extractQueryParamFromPageLocation('qclid')) {
    setCookie(
      ANALYTIC_COOKIES.QCLID,
      extractQueryParamFromPageLocation('qclid'),
      120,
    )
  }

  // Setting SEO referred in Campaign
  if (!isUTMParamsPresent) {
    captureSEOReferrerAsCampaign()
  }
}

function captureSEOReferrerAsCampaign() {
  const referrer = document.referrer ? document.referrer : ''
  const domain = extractDomainFromURL(referrer)
  if (
    !domain ||
    checkForWordInURL(referrer, 'apay-us.amazon') ||
    [
      'lastbrand',
      'onequince',
      'localhost',
      'myshopify',
      'shopify',
      'paypal',
      'klarnapayments',
    ].includes(domain)
  ) {
    return
  }
  setCookie(ANALYTIC_COOKIES.UTM_SOURCE, domain, 120)
  setCookie(ANALYTIC_COOKIES.UTM_MEDIUM, 'organic', 120)
  eraseCookie(ANALYTIC_COOKIES.UTM_CAMPAIGN)
  eraseCookie(ANALYTIC_COOKIES.UTM_CONTENT)
  eraseCookie(ANALYTIC_COOKIES.UTM_TERM)
  eraseCookie(ANALYTIC_COOKIES.QCLID)
}

function setCustomerDetailsInCookie() {
  const user = getCurrentUser()
  if (user && user.email) {
    setCookie(ANALYTIC_COOKIES.LB_CUSTOMER_EMAIL, user.email)
  }
}

function createNewSessionIfNeeded() {
  if (!getCookie(ANALYTIC_COOKIES.SESSION_ID)) {
    const sessionId = uniqueIdGenerator()
    setCookie(ANALYTIC_COOKIES.SESSION_ID, sessionId, 120)
    eventTracker.trackInternal(EventType.SESSION_CREATE, {
      sessionId,
    })
  } else {
    // If session is already present then extending session further by approx 2 hour
    setCookie(
      ANALYTIC_COOKIES.SESSION_ID,
      getCookie(ANALYTIC_COOKIES.SESSION_ID),
      120,
    )
  }
}
