import EventType from '../eventType'
import { getCurrentUser } from '../../utils'

export default class CriteoAnalytics {
  [EventType.HOME_PAGE_VIEW] = properties => {
    sendCriteoEvent({ event: 'viewHome' })
  };

  [EventType.COLLECTION_VIEW] = properties => {
    let productIds = [...new Set(properties.variantList.map(x => x.product_id))]
    sendCriteoEvent({ event: 'viewList', item: productIds })
  };

  [EventType.PRODUCT_VIEW](properties) {
    sendCriteoEvent({ event: 'viewItem', item: properties.productId })
  }

  [EventType.ADD_TO_CART] = properties => {
    let items = properties.lineItems.map(x => {
      return {
        id: x['productId'],
        price: x['price'],
        quantity: x['quantity'],
      }
    })
    sendCriteoEvent({ event: 'viewBasket', item: items })
  }
}

const sendCriteoEvent = event => {
  window.criteo_q = window.criteo_q || []
  let deviceType = /iPad/.test(navigator.userAgent)
    ? 't'
    : /Mobile|iP(hone|od)|Android|BlackBerry|IEMobile|Silk/.test(
        navigator.userAgent,
      )
    ? 'm'
    : 'd'
  let user = getCurrentUser()
  let userEmail = user && user.email ? user.email : ''
  window.criteo_q.push(
    { event: 'setAccount', account: process.env.GATSBY_CRITEO_ACCOUNT_ID },
    { event: 'setEmail', email: userEmail },
    { event: 'setSiteType', type: deviceType },
    event,
  )
}
