import FbPixel from './integrations/FbPixel'
import GoogleAnalytics from './integrations/GoogleAnalytics'
import InternalAnalytics from './integrations/InternalAnalytics'
import CriteoAnalytics from './integrations/CriteoAnalytics'
import GoogleAdsTag from './integrations/GoogleAdsTag'
import UetAnalytics from './integrations/UetAnalytics'
import SnapchatPixel from './integrations/SnapchatPixel'
import PinterestTag from './integrations/PinterestTag'
import { captureException } from '../components/Sentry/utils'
import { getCookie } from '../utils'
import { ANALYTIC_COOKIES } from '../utils/constants'
import PodsightsPixel from './integrations/PodsightsPixel'
import AcuityAdsPixel from './integrations/AcuityAdsPixel'
import RockerboxPixel from './integrations/RockerboxPixel'
import TaboolaPixel from './integrations/TaboolaPixel'

function isUtmSourceTest() {
  return getCookie(ANALYTIC_COOKIES.UTM_SOURCE) === 'quince_test'
}

class EventTracker {
  constructor() {
    this.internalAnalytics = new InternalAnalytics()
    this.gaAnalytics = new GoogleAnalytics()
    this.fbPixel = new FbPixel()
    this.criteoAnalytics = new CriteoAnalytics()
    this.googleAdsTag = new GoogleAdsTag()
    this.uetAnalytics = new UetAnalytics()
    this.podsightsPixel = new PodsightsPixel()
    this.snapchatPixel = new SnapchatPixel()
    this.pinterestTag = new PinterestTag()
    this.acuityAdsPixel = new AcuityAdsPixel()
    this.rockerboxPixel = new RockerboxPixel()
    this.taboolaPixel = new TaboolaPixel()
    this.integrations = [
      this.gaAnalytics,
      this.internalAnalytics,
      this.fbPixel,
      this.criteoAnalytics,
      this.googleAdsTag,
      this.uetAnalytics,
      this.podsightsPixel,
      this.snapchatPixel,
      this.pinterestTag,
      this.acuityAdsPixel,
      this.rockerboxPixel,
      this.taboolaPixel,
    ]
  }

  // Generic track to be used for standard events tracking, that requires event push to all the integrations.
  track = (eventType, properties) => {
    if (isUtmSourceTest()) return
    try {
      this.integrations.forEach(integration => {
        if (integration[eventType]) {
          setTimeout(() => integration[eventType](properties), 0)
        }
      })
    } catch (e) {
      captureException(e)
    }
  }

  // This is to track events in Google Analytics only
  trackGA = (eventType, properties) => {
    if (isUtmSourceTest()) return
    setTimeout(() => this.gaAnalytics[eventType](properties), 0)
  }

  // This is to track only in internal event tracking system.
  trackInternal = (eventType, properties) => {
    if (isUtmSourceTest()) return
    setTimeout(() => this.internalAnalytics[eventType](properties), 0)
  }

  // This is to track click events in ga or internal analytics based on input parameters.
  trackClick = (eventType, trackParams) => {
    if (trackParams.trackGA) {
      this.trackGA(eventType, {
        action: trackParams.action,
        category: trackParams.category,
        label: trackParams.label,
        value: trackParams.value,
        context: trackParams.context,
      })
    }
    if (trackParams.trackInternal) {
      this.trackInternal(eventType, {
        action: trackParams.action,
        category: trackParams.category,
        label: trackParams.label,
        value: trackParams.value,
        context: trackParams.context,
      })
    }
  }
}

const eventTracker = new EventTracker()
Object.freeze(eventTracker)

export default eventTracker
