import React from 'react'
import styled, { css } from 'styled-components'
import { useDispatch } from 'react-redux'
import { setActiveMegaMenuId } from '../actions'
import TrackableButton from './TrackableButton'
import TrackableLink from './TrackableLink'

const textButtonStyle = css`
  font-family: ${props => props.theme.fonts.primary};
  position: relative;
  text-decoration: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 0 100%;
  background-size: 0 1px;
  transition: background-size 350ms cubic-bezier(0.645, 0.045, 0.355, 1);
  background-image: ${props =>
    `linear-gradient(${props.theme.colors.primary}, ${props.theme.colors.primary});`};

  background-size: ${props => (props.active ? '100% 1px' : null)};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    &:hover {
      background-size: 100% 1px;
      transition: background 0.75s cubic-bezier(0.77, 0, 0.175, 1);
    }

    &:disabled {
      &:hover {
        background-image: none;
      }
    }
  }
`

const Button = styled(TrackableButton)`
  ${textButtonStyle}
`

const StyledLink = styled(TrackableLink)`
  ${textButtonStyle}
`

const Anchor = styled.a`
  ${textButtonStyle}
`

const TextButton = React.forwardRef(
  (
    {
      className,
      children,
      active,
      to,
      asAnchor,
      disabled,
      onClick,
      href,
      trackParams,
      style,
      onMouseEnter,
      onMouseLeave,
    },
    ref,
  ) => {
    const dispatch = useDispatch()
    if (to) {
      return (
        <StyledLink
          to={to}
          className={className}
          active={active ? 1 : null}
          disabled={disabled}
          onClick={() => {
            dispatch(setActiveMegaMenuId(''))

            if (onClick) onClick()
          }}
          trackParams={trackParams}
          style={style || {}}
        >
          {children}
        </StyledLink>
      )
    }

    if (asAnchor) {
      return (
        <Anchor
          className={className}
          active={active ? 1 : null}
          disabled={disabled}
          onClick={() => {
            onClick()
          }}
          trackParams={trackParams}
        >
          {children}
        </Anchor>
      )
    }

    if (onClick || onMouseEnter || onMouseLeave) {
      return (
        <Button
          type='button'
          className={className}
          active={active ? 1 : null}
          disabled={disabled}
          onClick={() => {
            if (onClick) onClick()
          }}
          trackParams={trackParams}
          style={style || {}}
          onMouseEnter={() => {
            if (onMouseEnter) onMouseEnter()
          }}
          onMouseLeave={() => {
            if (onMouseLeave) onMouseLeave()
          }}
          ref={ref || null}
        >
          {children}
        </Button>
      )
    }

    if (href) {
      return (
        <Anchor href={href} className={className}>
          {children}
        </Anchor>
      )
    }

    return <Button className={className}>{children}</Button>
  },
)

export default TextButton
