export function getLineItemPropertiesFromProductMetaTags(
  metaTags,
  shippingInformation,
) {
  if (!metaTags && !shippingInformation) return []
  if (
    !(metaTags || []).includes(MADE_TO_ORDER_CONTENTFUL_TAG) &&
    !shippingInformation
  )
    return []
  // shopify doesn't accept boolean field for meta tags
  return getMetaTagMap(shippingInformation)
}

