export const scrollToSection = (
  section,
  collectionListRef,
  offset = 0,
  headerHeight = 0,
  behavior = 'smooth',
) => {
  const collectionListRefElement = collectionListRef[section]
  if (!collectionListRefElement) return
  const top =
    collectionListRefElement.current.getBoundingClientRect().top -
    offset +
    window.scrollY -
    headerHeight
  window.scrollTo({
    top:
      collectionListRefElement.current.getBoundingClientRect().top -
      offset +
      window.scrollY -
      headerHeight,
    behavior,
  })
}
