import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../../styles/utils'
import { Radio, ErrorMessage } from './'

const Fieldset = styled.fieldset`
  border: 0;
  padding: 0;
  margin: 0;
  border-bottom: ${props =>
    props.hasDisplayValues
      ? `solid 1px ${props.theme.colors.silver}`
      : `solid 0px`};
  padding-bottom: ${props => (props.hasDisplayValues ? '20px' : '0px')};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding-bottom: ${props =>
      props.hasDisplayValues ? desktopVw(20) : desktopVw(0)};
  }
`

const Legend = styled.legend`
  padding: 0;
  margin: 0;
  font-size: 16px;
  line-height: 20px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    font-size: ${desktopVw(16)};
    line-height: ${desktopVw(20)};
  }

  @media (max-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-bottom: 10px;
  }
`

const Label = styled.label`
  padding: 0 13.5px;

  &:first-child {
    padding-left: 0;
  }

  &:last-child {
    padding-right: 13px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0 ${desktopVw(32.5)};

    &:first-child {
      padding-left: 0;
      padding-right: ${desktopVw(25.5)};
    }
    &:last-child {
      padding-right: ${desktopVw(33)};
    }

    padding: ${props => (props.hasDisplayValues ? '0px !important' : null)};
  }
`

const Rating = styled.div`
  display: flex;
  align-items: center;
  font-size: 11px;
  text-transform: uppercase;
  margin: 5px 0 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: ${desktopVw(5)} 0 0;
    font-size: ${desktopVw(11)};
  }
`

const RadioContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: ${props => (props.hasDisplayValues ? '100%' : 'auto')};
`

const DisplayValue = styled.div`
  margin-top: 15px;
  text-transform: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-transform: uppercase;
    margin-top: ${desktopVw(15)};
  }
`

const LabelContainer = styled.div`
  display: flex;
  flex-direction: column;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(33)};
    margin-top: ${desktopVw(15)};
    align-items: ${props =>
      props.isLast === true
        ? 'flex-end'
        : props.index !== 0
        ? 'center'
        : 'start'};
  }
`

export const RadioSelection = ({
  label,
  name,
  register,
  errors,
  range,
  required = true,
  displayValues = [],
}) => {
  return (
    <>
      <Fieldset hasDisplayValues={displayValues.length > 0 ? true : false}>
        <Legend>{label}</Legend>
        <Rating>
          <RadioContainer
            hasDisplayValues={displayValues.length > 0 ? true : false}
          >
            {[...Array(range)].map((_, i) => (
              <LabelContainer index={i} isLast={displayValues.length === i + 1}>
                <Label
                  key={`${label}${i}`}
                  hasDisplayValues={displayValues.length > 0 ? true : false}
                >
                  <Radio
                    value={i + 1}
                    name={name}
                    register={register}
                    error={errors[name]}
                    required={required}
                  />
                </Label>
                {displayValues.length > 0 && (
                  <DisplayValue>{displayValues[i]}</DisplayValue>
                )}
              </LabelContainer>
            ))}
          </RadioContainer>
        </Rating>
      </Fieldset>
      {errors && errors[name] && (
        <ErrorMessage>{errors[name].message}</ErrorMessage>
      )}
    </>
  )
}
