import React from 'react'
import styled from 'styled-components'
import { useSelector, useDispatch } from 'react-redux'
import {
  setMobileMenuOpen,
  setCartOpen,
  setActiveMegaMenuId,
} from '../../actions'

// Styles
import { desktopVw } from '../../styles/utils'

import Logo from '../../assets/logo.svg'
import BackArrow from '../../assets/back-arrow.svg'
import Hamburger from './AsideMenu/Hamburger'
import CartButton from './CartButton'
import SearchLink from './SearchLink'
import WishlistLink from './WishlistLink'
import TrackableLink from '../TrackableLink'

const StyledMenuBar = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: ${props => props.theme.header.mobile.height}px;
  box-sizing: border-box;
  padding: 0 10px;
  background-color: ${props => {
    // eslint-disable-next-line immutable/no-let
    let bgColor = null

    if (props.bgColorMegaMenu) {
      bgColor = props.backgroundColor
        ? props.backgroundColor
        : props.theme.colors.secondary
    } else if (props.isMenuBarBgWhite) {
      bgColor = '#ffffff'
    } else {
      bgColor = props.bgColor ? props.theme.header.scrollBackgroundColor : null
    }

    return bgColor
  }};

  svg {
    fill: ${props =>
      props.color
        ? props.theme.colors.primary
        : props.theme.header.mobile.color};
  }

  a,
  button {
    color: ${props =>
      props.color
        ? props.theme.colors.primary
        : props.theme.header.mobile.color};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: relative;
    z-index: 1;
    justify-content: space-between;
    align-items: center;
    height: ${props => desktopVw(props.theme.header.desktop.height)};
    padding: 0 ${desktopVw(42)} 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      left: ${desktopVw(42)};
      right: ${desktopVw(42)};
      bottom: 0;
      height: 1px;
      background: ${props => props.theme.colors.primary};
      opacity: ${props => (props.border ? '1' : '0')};
    }

    svg {
      fill: ${props =>
        props.color
          ? props.theme.colors.primary
          : props.theme.header.desktop.color};
    }

    a,
    button {
      color: ${props =>
        props.color
          ? props.theme.colors.primary
          : props.theme.header.desktop.color};
    }
  }
`

const StyledHamburger = styled(Hamburger)`
  div {
    background-color: ${props =>
      props.bgColor
        ? props.theme.colors.primary
        : props.theme.header.mobile.color};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    div {
      background-color: ${props =>
        props.bgColor
          ? props.theme.colors.primary
          : props.theme.header.desktop.color};
    }
  }
`

const LogoLink = styled(TrackableLink)`
  position: absolute;
  left: 0;
  right: 0;
  width: 88px;
  margin: 0 auto;
  svg {
    fill: ${props => props.theme.colors.primary} !important;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    position: static;
    margin: 0;
    width: ${desktopVw(109)};
    z-index: 1000;
  }
`

const MenuButtons = styled.ul`
  display: flex;
  align-items: center;
  margin-top: 6px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-left: ${desktopVw(11)};
    margin-top: ${desktopVw(6)};
  }
`

const MenuButton = styled.li`
  margin-right: 9px;

  &:last-child {
    margin-right: 0;
    margin-top: -9px;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(11)};

    &:last-child {
      margin-top: -${desktopVw(9)};
    }
  }
`

const StyledBackArrow = styled(BackArrow)`
  width: 21px;
`

const MenuBar = ({
  className,
  children,
  scrolledDown,
  onClose,
  backgroundColor,
  showBgColor,
  isMenuBarBgWhite,
}) => {
  const dispatch = useDispatch()
  const { mobileMenuOpen, megaMenuId } = useSelector(state => state.ui)

  return (
    <StyledMenuBar
      className={className}
      color={mobileMenuOpen || megaMenuId || scrolledDown ? 1 : null}
      bgColorMegaMenu={megaMenuId}
      bgColor={scrolledDown || showBgColor}
      border={megaMenuId}
      backgroundColor={backgroundColor}
      isMenuBarBgWhite={isMenuBarBgWhite}
    >
      {onClose ? (
        <StyledBackArrow onClick={onClose} />
      ) : (
        <StyledHamburger bgColor={scrolledDown ? 1 : null} />
      )}
      <LogoLink
        to='/'
        onClick={() => {
          dispatch(setActiveMegaMenuId(''))
          dispatch(setMobileMenuOpen(false))
          dispatch(setCartOpen(false))
        }}
        aria-label='logo'
        trackParams={{
          trackGA: true,
          trackInternal: true,
          category: 'navigation',
          action: 'click_header_logo',
        }}
      >
        <Logo />
      </LogoLink>
      {children}
      <MenuButtons>
        <MenuButton>
          <SearchLink />
        </MenuButton>
        <MenuButton>
          <WishlistLink />
        </MenuButton>
        <MenuButton>
          <CartButton />
        </MenuButton>
      </MenuButtons>
    </StyledMenuBar>
  )
}

export default MenuBar
