import React from 'react'
import ReactDOM from 'react-dom'

import styled, { keyframes } from 'styled-components'
import { desktopVw } from '../styles/utils'

// Hooks
import useBodyScrollLock from '../hooks/useBodyScrollLock'
import CloseIcon from '../assets/close.svg'

const fadeIn = keyframes`
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
`

const StyledModal = styled.section`
  position: relative;
  z-index: 2000;
  background: ${props => props.theme.colors.secondary};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    overflow: hidden;
    opacity: 0;
    animation: ${props => (props.startAnim ? fadeIn : null)} 200ms ease-out
      forwards;
    animation-delay: 100ms;

    &::-webkit-scrollbar {
      display: none;
    }
  }
`

const ModalContainer = styled.div`
  z-index: 1900;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
`

const Overlay = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  animation: ${props => (props.startAnim ? fadeIn : null)} 200ms ease-out
    forwards;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
  }
`

const Close = styled.button`
  position: absolute;
  z-index: 1;
  top: 38px;
  right: 20px;
  width: 11px;
  cursor: pointer;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: ${props => (props.hide ? 'none' : 'block')};
    top: ${desktopVw(65)};
    right: ${desktopVw(80)};
    width: ${desktopVw(11)};
  }
`

const StyledCloseIcon = styled(CloseIcon)`
  path {
    fill: ${props => props.theme.colors.primary};
  }
`

const Modal = ({ className, children, onClose, open, showCloseBtn = true }) => {
  let portalRoot

  if (typeof document !== 'undefined') {
    portalRoot = document.getElementById('portal')
  }

  useBodyScrollLock(open)

  return open && portalRoot
    ? ReactDOM.createPortal(
        <ModalContainer>
          <Overlay onClick={onClose} startAnim={open} />
          <StyledModal className={className} startAnim={open}>
            {showCloseBtn && (
              <Close
                className='modal__close_button'
                type='button'
                aria-label='close'
                onClick={onClose}
              >
                <StyledCloseIcon />
              </Close>
            )}

            {children}
          </StyledModal>
        </ModalContainer>,
        portalRoot,
      )
    : null
}

export default Modal
