import * as types from '../actions/types'
import { loadState, saveState } from '../utils/localStorage'

const initialState = loadState('waitlist')
const saveWaitlist = saveState('waitlist')

const waitlist = (state = initialState || [], { type, payload }) => {
  switch (type) {
    case types.ADD_TO_WAITLIST: {
      if (state.includes(payload)) return

      const newState = [...state, payload]

      saveWaitlist(newState)
      return newState
    }
    case types.REMOVE_FROM_WAITLIST: {
      const newState = state.filter(handle => handle !== payload)

      saveWaitlist(newState)
      return newState
    }

    default:
      return state
  }
}

export default waitlist
