import React from 'react'
import { captureFatalException } from '../Sentry/utils'

const initialState = { error: null, info: null }

class ErrorBoundary extends React.Component {
  state = initialState

  resetErrorBoundary = (...args) => {
    if (this.props.onReset) this.props.onReset(...args)
    this.setState(initialState)
  }

  componentDidCatch(error, info) {
    if (this.props.onError)
      this.props.onError(error, info && info.componentStack)
    captureFatalException(error, info)
    this.setState({ error, info })
  }

  render() {
    const { error, info } = this.state
    const { fallbackRender, FallbackComponent, fallback } = this.props

    if (error !== null) {
      const props = {
        componentStack: info && info.componentStack,
        error,
        resetErrorBoundary: this.resetErrorBoundary,
      }
      if (React.isValidElement(fallback)) {
        return fallback
      } else if (typeof fallbackRender === 'function') {
        return fallbackRender(props)
      } else if (FallbackComponent) {
        return <FallbackComponent {...props} />
      } else {
        throw new Error(
          'react-error-boundary requires either a fallback, fallbackRender, or FallbackComponent prop',
        )
      }
    }

    return this.props.children
  }
}

function withErrorBoundary(Component, errorBoundaryProps) {
  function Wrapped(props) {
    return (
      <ErrorBoundary {...errorBoundaryProps}>
        <Component {...props} />
      </ErrorBoundary>
    )
  }

  // Format for display in DevTools
  const name = Component.displayName || Component.name || 'Unknown'
  Wrapped.displayName = `withErrorBoundary(${name})`

  return Wrapped
}

function useErrorHandler(givenError) {
  const [error, setError] = React.useState(null)
  if (givenError) throw givenError
  if (error) throw error
  return setError
}

export { ErrorBoundary, withErrorBoundary, useErrorHandler }
