import { getCookie, isUtmAttentiveText, setCookie } from '../utils'
import {
  ATTENTIVE_POP_UP_EXPIRY_MINUTES,
  FEATURE_COOKIES,
} from '../utils/constants'
import eventTracker from '../trackers/EventTracker'
import EventType from '../trackers/eventType'
import theme from '../styles/theme'

function useAttentiveSdk() {
  const initializeAttentive = () => {
    const mq = window.matchMedia(
      `(max-width: ${theme.breakpoints.desktopSmall}px)`,
    )
    if (
      ((getCookie(FEATURE_COOKIES.IS_ATTENTIVE_POPUP_PREVIOUSLY_OPENED) ||
        !mq.matches) &&
        !isUtmAttentiveText()) ||
      process.env.GATSBY_BUILD_ENVIRONMENT !== 'production'
    )
      return
    const script = document.createElement('script')
    script.src = 'https://cdn.attn.tv/quince/dtag.js'
    script.id = 'attentive'
    document.head.appendChild(script)
    eventTracker.track(EventType.FORM_OPEN, {
      action: 'attentive_popup_open',
    })
    setCookie(
      FEATURE_COOKIES.IS_ATTENTIVE_POPUP_PREVIOUSLY_OPENED,
      true,
      ATTENTIVE_POP_UP_EXPIRY_MINUTES,
    )
  }

  return { initializeAttentive }
}

export default useAttentiveSdk
