import { useContext, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { replaceCheckout } from '../actions'
import { StoreContext } from '../contexts'
import {
  getLocalStorageCheckoutKey,
  getNewLineItemsWithTransformedSrc,
} from '../utils'
import queryCheckout from '../shopify/queryCheckout'
import createCheckout from '../shopify/createCheckout'

const hydrateCheckout = async (client, storeSubdomain, dispatch) => {
  const checkoutId = localStorage.getItem(
    getLocalStorageCheckoutKey(storeSubdomain),
  )

  let checkout

  const data = await queryCheckout(client, { id: checkoutId })

  if (data && data.node) {
    if (data.node.orderStatusUrl) {
      const {
        data: {
          checkoutCreate: { checkout: shopifyCheckout },
        },
      } = await createCheckout(client, { input: {} })
      checkout = shopifyCheckout
    } else if (data.node.webUrl.indexOf('lastbrand.myshopify.com') !== -1) {
      const { lineItems } = data.node
      let input = {}
      if (lineItems && lineItems.edges && lineItems.edges.length > 0) {
        const extractedLineItems = lineItems.edges.map(edge => {
          const { variant } = edge.node
          return {
            variantId: variant.id,
            quantity: edge.node.quantity,
            customAttributes:
              edge.node.customAttributes &&
              edge.node.customAttributes.length > 0
                ? edge.node.customAttributes.map(({ key, value }) => {
                    return {
                      key,
                      value,
                    }
                  })
                : undefined,
          }
        })
        input = { lineItems: extractedLineItems }
      }
      const {
        data: {
          checkoutCreate: { checkout: shopifyCheckout },
        },
      } = await createCheckout(client, { input })
      checkout = shopifyCheckout
    } else {
      checkout = data.node
    }
  } else {
    const {
      data: {
        checkoutCreate: { checkout: shopifyCheckout },
      },
    } = await createCheckout(client, { input: {} })
    checkout = shopifyCheckout
  }

  // const customerAccessToken = await loadCustomerAccessToken(client)
  // if (customerAccessToken != null)
  //   checkoutCustomerAssociate(client, {
  //     checkoutId: checkout.id,
  //     customerAccessToken: customerAccessToken.accessToken,
  //   })

  dispatch(
    replaceCheckout({
      ...checkout,
      lineItems: {
        edges: getNewLineItemsWithTransformedSrc(checkout?.lineItems),
      },
      storeSubdomain,
    }),
  )
}

const useCheckoutHydration = () => {
  const dispatch = useDispatch()
  const checkouts = useSelector(({ checkouts }) => checkouts)
  const stores = useContext(StoreContext)

  /* eslint react-hooks/exhaustive-deps: off */
  useEffect(() => {
    Object.values(checkouts).forEach(({ id, storeSubdomain }) => {
      if (!id) {
        hydrateCheckout(stores[storeSubdomain].client, storeSubdomain, dispatch)
      }
    })
  }, [])
}

export default useCheckoutHydration
