import React, { useEffect, useRef, useState } from 'react'
import styled from 'styled-components'
import { graphql, useStaticQuery } from 'gatsby'
import { useDispatch, useSelector } from 'react-redux'
import { setActiveMegaMenuId } from '../../actions'
import { isLoggedIn } from '../../utils/auth'

// Styles
import { desktopVw } from '../../styles/utils'

import { debounce, getLinkPath, getOverrideMenu } from '../../utils'

// Hooks
import useOnClickOutSide from '../../hooks/useOnClickOutside'

// Components
import TextButton from '../TextButton'
import MenuBar from './MenuBar'
import AsideMenu from './AsideMenu'
import MegaMenu from './MegaMenu'
import SpacerImg from '../SpacerImg'

import MyAccount from '../../assets/my_account.svg'

const MEGA_MENU_TRANSITION_DELAY_IN_MS = 300

const StyledHeader = styled.header`
  z-index: 1000;
  position: ${props => (props.position ? 'fixed' : 'sticky')};
  top: 0;
  left: 0;
  right: 0;
  border-bottom: ${props => props.theme.header.mobile.borderBottom};

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    border-bottom: ${props => props.theme.header.desktop.borderBottom};
  }
`

const DesktopNav = styled.nav`
  display: none;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    flex: 1;
    display: flex;
    justify-content: center;
    height: ${props => desktopVw(props.theme.header.desktop.height)};
    padding-right: ${isLoggedIn() ? 0 : desktopVw(23)};
  }
`

const MenuSecondary = styled.div`
  display: none;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: block;
    height: ${props => desktopVw(props.theme.header.desktop.height)};
    display: flex;
    align-items: center;
  }
`

const StyledTextButton = styled(TextButton)`
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 2px;

  background-size: ${props => (props.active ? `100% 1px;` : `0% 1px;`)};

  &:last-child {
    margin-right: 0;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    text-transform: uppercase;
    font-size: ${desktopVw(11)};
    color: ${props => props.theme.colors.primary} !important;

    background-image: ${props =>
      props.borderColor
        ? `linear-gradient(${props.theme.colors.primary}, ${props.theme.colors.primary});`
        : `linear-gradient(${props.theme.colors.primary}, ${props.theme.colors.primary});`};
  }
`

const MenuButton = styled(StyledTextButton)`
  background-position: ${desktopVw(23)}
    ${props => (props.isLoggedInUserMenu ? '60%' : '58%')};
  padding-left: ${desktopVw(23)};
  position: static;
  height: 100%;
  display: ${props => (props.isTextLink ? 'flex' : 'block')};
  align-items: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .menu-label {
      height: ${props =>
        props.isTextLink
          ? 'auto'
          : desktopVw(props.theme.header.desktop.height)};
    }

    &.mega-menu-label {
      .mega-menu {
        transition-delay: ${MEGA_MENU_TRANSITION_DELAY_IN_MS}ms;
        transition-property: visibility;

        visibility: hidden;
        display: flex !important;
        position: absolute;
        left: 0;
        right: 0;
        top: calc(
          ${props => desktopVw(props.theme.header.desktop.height)} - 4px
        );
        box-shadow: none;
        cursor: default;
      }

      .mega-menu::before {
        content: '';
        display: block;
        position: absolute;
        left: ${desktopVw(42)};
        right: ${desktopVw(42)};
        top: 0px;
        height: 1px;
        background: ${props => props.theme.colors.primary};
        opacity: 1;
        margin-top: 3px;
      }

      .sub-menu-item {
        color: black !important;

        a {
          color: black !important;
        }
      }
    }

    &.mega-menu-label:hover {
      .mega-menu {
        transition-delay: ${MEGA_MENU_TRANSITION_DELAY_IN_MS}ms;
        visibility: visible;
      }
    }
  }
`

const StyledMyAccountSvg = styled(MyAccount)`
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin-right: ${desktopVw(10)};
    path {
      fill: currentColor !important;
    }
    width: ${desktopVw(16)};
    height: ${desktopVw(16)};
  }
`

const LoggedInLinkText = styled.span`
  position: relative;
  bottom: 3px;
`

const MenuButtonWithMegaMenu = props => {
  const {
    scrolledDown,
    megaMenuId,
    isActiveMegaMenu,
    id,
    setIsMenuBarBgWhite,
    setHoveredMenuEl,
    menu,
    menuBackgroundColor,
    hoveredMenuEl,
    isButton,
    trackParams,
    menuLabelContent,
    isLoggedInUserMenu,
  } = props

  const menuRef = useRef()

  return (
    <MenuButton
      key={id}
      isButton={isButton}
      borderColor={scrolledDown || megaMenuId}
      active={isActiveMegaMenu}
      trackParams={trackParams}
      menuId={id}
      className={`mega-menu-label-${id} mega-menu-label`}
      onMouseEnter={() => {
        setIsMenuBarBgWhite(true)
        setHoveredMenuEl(menuRef)
      }}
      onMouseLeave={() => {
        setIsMenuBarBgWhite(false)
        setHoveredMenuEl(null)
      }}
      ref={menuRef}
      isLoggedInUserMenu={isLoggedInUserMenu}
    >
      <span className='menu-label'>{menuLabelContent()}</span>

      <MegaMenu
        key={id}
        menu={menu}
        active={megaMenuId}
        backgroundColor={menuBackgroundColor && menuBackgroundColor.hexcode}
        setIsMenuBarBgWhite={setIsMenuBarBgWhite}
        hoveredMenuEl={hoveredMenuEl}
      />
    </MenuButton>
  )
}

const Header = ({
  scrolledDown,
  disableSEOHeaderNode,
  showBgColor,
  className,
}) => {
  const {
    contentfulSite: { menuBackgroundColor, menuPrimary, menuSecondary },
  } = useStaticQuery(graphql`
    query MenuQuery {
      contentfulSite {
        menuPrimary {
          id
          link {
            ...Link
          }
          submenuColumnOne {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnTwo {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnThree {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnFour {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnFive {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnSix {
            ... on ContentfulCard {
              ...Card
            }
          }
          spotlight {
            id
            images {
              title
              description
              fluid(maxWidth: 768) {
                ...GatsbyContentfulFluid_withWebp
              }
            }
            link {
              ...Link
            }
          }
        }
        menuSecondary {
          id
          link {
            ...Link
          }
          submenuColumnOne {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnTwo {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnThree {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnFour {
            ... on ContentfulCard {
              ...Card
            }
          }
          submenuColumnFive {
            ... on ContentfulCard {
              ...Card
            }
          }
          spotlight {
            id
            images {
              title
              description
              fluid(maxWidth: 768) {
                ...GatsbyContentfulFluid_withWebp
              }
            }
            link {
              ...Link
            }
          }
        }
        menuBackgroundColor {
          name
          hexcode
        }
      }
    }
  `)

  const dispatch = useDispatch()
  const { megaMenuId } = useSelector(state => state.ui)

  const [ref] = useOnClickOutSide(
    () => megaMenuId && dispatch(setActiveMegaMenuId('')),
  )

  const { mobileMenuOpen } = useSelector(state => state.ui)

  const { userInfo } = useSelector(state => state.login)

  const menuPrimaryOverride = getOverrideMenu(menuPrimary, userInfo)
  const menuSecondaryOverride = getOverrideMenu(menuSecondary, userInfo)

  const [isMenuBarBgWhite, setIsMenuBarBgWhite] = useState(false)
  const [hoveredMenuEl, setHoveredMenuEl] = useState(null)

  const debouncedSetIsMenuBarBgWhite = debounce(
    setIsMenuBarBgWhite,
    MEGA_MENU_TRANSITION_DELAY_IN_MS,
  )

  const debouncedSetHoveredMenuEl = debounce(
    setHoveredMenuEl,
    MEGA_MENU_TRANSITION_DELAY_IN_MS,
  )

  // On initial page load, event listeners won't be yet available
  // But hover for mega menu is still functional. Background
  // and hovered element are thus set manually here.
  useEffect(() => {
    // eslint-disable-next-line no-undef
    const hoveredEl = document.querySelectorAll('.mega-menu-label:hover')
    if (hoveredEl.length > 0) {
      debouncedSetIsMenuBarBgWhite(true)
      debouncedSetHoveredMenuEl({ current: hoveredEl[0] })
    }
  }, [])

  return (
    <StyledHeader ref={ref} className={className}>
      <MenuBar
        scrolledDown={scrolledDown}
        megaMenuActive={!!megaMenuId}
        backgroundColor={menuBackgroundColor && menuBackgroundColor.hexcode}
        showBgColor={showBgColor}
        isMenuBarBgWhite={isMenuBarBgWhite}
      >
        {!disableSEOHeaderNode && (
          <h1>
            <SpacerImg altText='Quince' />
          </h1>
        )}
        <DesktopNav>
          {menuPrimaryOverride.map(menu => {
            const { id, link, submenuColumnOne, submenuColumnTwo } = menu
            const isMegaMenu = submenuColumnOne || submenuColumnTwo
            const isActiveMegaMenu = id === megaMenuId
            return isMegaMenu ? (
              <MenuButtonWithMegaMenu
                isButton={isMegaMenu}
                scrolledDown={scrolledDown}
                megaMenuId={megaMenuId}
                isActiveMegaMenu={isActiveMegaMenu}
                id={id}
                setIsMenuBarBgWhite={debouncedSetIsMenuBarBgWhite}
                hoveredMenuEl={hoveredMenuEl}
                setHoveredMenuEl={debouncedSetHoveredMenuEl}
                link={link}
                menu={menu}
                menuBackgroundColor={menuBackgroundColor}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'navigation',
                  action: 'click_header_link',
                  label: link.text,
                }}
                menuLabelContent={() => <>{link.text}</>}
              />
            ) : (
              <MenuButton
                key={id}
                borderColor={scrolledDown || megaMenuId}
                active={id === megaMenuId}
                to={getLinkPath(link)}
                trackParams={{
                  trackGA: true,
                  trackInternal: true,
                  category: 'navigation',
                  action: 'click_header_link',
                  label: link.text,
                  value: getLinkPath(link),
                  context: { overrideText: link.overrideText },
                }}
                isTextLink
              >
                <span className='menu-label'>
                  {link.overrideText || link.text // Override text is only in case of Picked for You collection.
                  // Because we still want to log label as 'Picked for You', hence adding overrider text on UI.
                  }
                </span>
              </MenuButton>
            )
          })}
        </DesktopNav>
        <MenuSecondary>
          {menuSecondaryOverride.map(menu => {
            const { id, link } = menu
            switch (link.entry.slug) {
              case 'log-in':
                return (
                  <>
                    {isLoggedIn() ? (
                      <MenuButtonWithMegaMenu
                        isButton
                        scrolledDown={scrolledDown}
                        megaMenuId={megaMenuId}
                        isActiveMegaMenu={id === megaMenuId}
                        id={id}
                        setIsMenuBarBgWhite={debouncedSetIsMenuBarBgWhite}
                        hoveredMenuEl={hoveredMenuEl}
                        setHoveredMenuEl={debouncedSetHoveredMenuEl}
                        link={link}
                        menu={menu}
                        menuBackgroundColor={menuBackgroundColor}
                        trackParams={{
                          trackGA: true,
                          trackInternal: true,
                          category: 'navigation',
                          action: 'click_header_link',
                          label: link.text,
                          context: { overrideText: link.overrideText },
                        }}
                        menuLabelContent={() => (
                          <>
                            <StyledMyAccountSvg />
                            <LoggedInLinkText>
                              {link.overrideText || link.text}
                            </LoggedInLinkText>
                          </>
                        )}
                        isLoggedInUserMenu
                      />
                    ) : (
                      <StyledTextButton
                        key={link.id}
                        to={getLinkPath(link)}
                        borderColor={scrolledDown || megaMenuId}
                        active={id === megaMenuId}
                        trackParams={{
                          trackGA: true,
                          trackInternal: true,
                          category: 'navigation',
                          action: 'click_header_link',
                          label: link.text,
                          value: getLinkPath(link),
                        }}
                      >
                        {link.text}
                      </StyledTextButton>
                    )}
                  </>
                )

              case 'search':
                return null
              default:
                return (
                  <StyledTextButton
                    key={link.id}
                    to={getLinkPath(link)}
                    borderColor={scrolledDown || megaMenuId}
                    active={id === megaMenuId}
                    trackParams={{
                      trackGA: true,
                      trackInternal: true,
                      category: 'navigation',
                      action: 'click_header_link',
                      label: link.text,
                      value: getLinkPath(link),
                      context: { overrideText: link.overrideText },
                    }}
                  >
                    {link.overrideText || link.text // Override text is only in case of Picked for You collection.
                    // Because we still want to log label as 'Picked for You', hence adding overrider text on UI.
                    }
                  </StyledTextButton>
                )
            }
          })}
        </MenuSecondary>
      </MenuBar>

      {mobileMenuOpen && (
        <AsideMenu
          menuPrimary={menuPrimaryOverride}
          menuSecondary={menuSecondaryOverride}
          backgroundColor={menuBackgroundColor && menuBackgroundColor.hexcode}
        />
      )}
    </StyledHeader>
  )
}

export default Header
