import EventType from '../eventType'

export default class GoogleAnalytics {
  constructor() {
    this.enhancedEcommerceLoaded = false
  }

  loadEnhancedEcommerce = () => {
    if (!this.enhancedEcommerceLoaded) {
      //To load the enhanced e-commerce plugin,
      window.ga('require', 'ec')
      this.enhancedEcommerceLoaded = true
    }
    //To specify the currency of the transaction
    window.ga('set', '&cu', 'USD')
  };

  // Note: Not handling Page view event here, as the google analytics gatsby plugin takes care of sending ga page view
  // event. https://github.com/gatsbyjs/gatsby/blob/master/packages/gatsby-plugin-google-analytics/src/gatsby-browser.js

  [EventType.PRODUCT_VIEW] = properties => {
    this.loadEnhancedEcommerce()
    logEnhancedEcommerceProductAction(properties, 'detail')
    logEnhancedEcommerceEvent('Viewed Product')
  };

  [EventType.ADD_TO_CART] = properties => {
    this.loadEnhancedEcommerce()
    logEnhancedEcommerceProductAction(properties, 'add')
    logEnhancedEcommerceEvent('Added Product')
  };

  [EventType.ZOOM_PRODUCT_IMAGE] = properties => {
    logGAEvent('view_product', 'zoom_product_photo')
  };

  [EventType.REMOVE_FROM_CART] = properties => {
    logGAEvent('checkout', 'remove_from_cart')
  };

  [EventType.ACCORDION_TOGGLE] = properties => {
    logGAEvent(
      properties.category,
      properties.action,
      properties.label,
      properties.value,
    )
  };

  [EventType.BUTTON_CLICK] = properties => {
    logGAEvent(
      properties.category,
      properties.action,
      properties.label,
      properties.value,
    )
  };

  [EventType.LINK_CLICK] = properties => {
    logGAEvent(
      properties.category,
      properties.action,
      properties.label,
      properties.value,
    )
  };

  [EventType.FORM_SUBMIT] = properties => {
    logGAEvent(
      properties.category,
      properties.action,
      properties.label,
      properties.value,
    )
  };

  [EventType.PAGE_SCROLL] = properties => {
    logGAEvent(
      'Scroll',
      undefined,
      undefined,
      properties.scrollPercentage,
      true,
    )
  };

  [EventType.SEARCH] = properties => {
    // send a virtual pageview to Google Analytics
    window.ga(
      'send',
      'pageview',
      `/?q=${
        properties?.searchQuery
          ? properties?.searchQuery.toLowerCase()
          : properties?.searchQuery
      }`,
    )
  }
}

// Pushes normal event to GA with category as enhanced ecommerce.
const logEnhancedEcommerceEvent = eventAction => {
  logGAEvent('EnhancedEcommerce', eventAction, undefined, undefined, true)
}

const logGAEvent = (category, action, label, value, nonInteraction) => {
  window.ga('send', 'event', {
    eventCategory: category,
    eventAction: action,
    eventLabel: label,
    eventValue: value,
    nonInteraction: nonInteraction,
  })
}

const logEnhancedEcommerceProductAction = (properties, action) => {
  let event = {
    id: properties.sku,
    name: properties.productTitle + ' - ' + properties.variantTitle,
    category: properties.productType,
    quantity: properties.quantity ? properties.quantity : 1,
    price: properties.price,
    brand: properties.vendor,
    variant: properties.variantTitle,
    currency: 'USD',
  }
  // This will not be true for now
  if (properties.coupon) {
    event.coupon = properties.coupon
  }
  window.ga('ec:addProduct', event)
  window.ga('ec:setAction', action)
}
